export default {
	/**
	 * @function standardizedOnBeforeRequest
	 * @description creates a copy of a web request 'details' object from either
	 * Chrome or Firefox API that standardizes mismatched attribute names to provide
	 * simple access within the extension
	 * https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/webRequest/onBeforeRequest#details
	 * https://developer.chrome.com/extensions/webRequest#event-onBeforeRequest
	 * @param {Object} details of webrequest object as defined in docs in description
	 * @return {Object} details of webrequest with consistent attribute keys
	 */
	standardizedOnBeforeRequest(details) {
		return this._standardizedInitiator(details);
	},

	/**
	 * @function standardizedOnCompleted
	 * @description creates a copy of a web request 'details' object from either
	 * Chrome or Firefox API that standardizes mismatched attribute names to provide
	 * simple access within the extension
	 * https://developer.mozilla.org/en-US/Add-ons/WebExtensions/API/webRequest/onCompleted#details
	 * https://developer.chrome.com/extensions/webRequest#event-onCompleted
	 * @param {Object} details of webrequest object as defined in docs in description
	 * @return {Object} details of webrequest with consistent attribute keys
	 */
	standardizedOnCompleted(details) {
		return this._standardizedInitiator(details);
	},
	_standardizedInitiator(details) {
		const standardizedDetails = { ...details };
		// the below reconciles Chrome and Firefox
		if (details.originUrl) {
			standardizedDetails.initiator = details.originUrl;
		}
		return standardizedDetails;
	},
};
