	.wrapper-content {
		position: relative;
		padding: 0.47em 0.8em;

		.icon,
		.bbb-close-button {
			position: absolute;
			margin: auto;
			top: 0;
			bottom: 0;
		}

		.icon {
			left: 1.3em;
		}

		.warning-message-text {
			padding: 0 2em 0 3em;
		}

		.bbb-close-button {
			transition: stroke $sidecar-reveal-duration $sidecar-reveal-duration;
			stroke: $bbb-orange-dark;
			cursor: pointer;
			right: 1.3em;
		}
	}
	@media screen and (max-width: $medium-screen-width) {
		&.forSideCard {
			width: $container-width * 0.9;
		}
		&.indent-from-bottom {
			top: 18em;
		}
		@media screen and (max-width: $small-screen-width) {
			&.forSideCard {
				width: $container-width * 0.85;
			}
			&.indent-from-bottom {
				top: 17em;
			}
