<template>
	<div class="warning-message-container" :class="warningClasses">
		<div class="wrapper-content">
			<ExclamationPointIcon class="icon" />
			<p class="warning-message-text">
				<slot />
			</p>
			<CrossIcon @click.native="close" />
		</div>
	</div>
</template>

<script>
import ExclamationPointIcon from './exclamation-point-icon';
import CrossIcon from './cross-icon';

export default {
	components: {
		CrossIcon,
		ExclamationPointIcon,
	},
	props: {
		isRounded: {
			type: Boolean,
			default: false,
		},
		isIndentFromBottom: {
			type: Boolean,
			default: false,
		},
		ifTriggerSidecar: {
			type: Boolean,
			default: false,
		},
		close: {
			type: Function,
			default: null,
		},
	},
	computed: {
		warningClasses() {
			return {
				rounded: this.isRounded,
				'indent-from-bottom': this.isIndentFromBottom,
				forSideCard: this.ifTriggerSidecar,
			};
		},
	},
};
</script>
