// Logic of this component based on vue-text-dot package: https://www.npmjs.com/package/vue-text-dot. We don't use
exactly that package because it has extra markup which we don't want to have and also we made few changes in ellipsed()
method.

<template>
	<div>
		<slot></slot>
	</div>
</template>

<script>
export default {
	props: {
		lines: { type: Number, default: 3 },
	},
	mounted() {
		this.ellipsed();
	},
	methods: {
		ellipsed() {
			const el = this.$el;
			let text = el.textContent;
			if (!text) {
				return;
			}
			let height = parseInt(window.getComputedStyle(el).height, 10);
			let lineHeight;
			if (window.getComputedStyle(el)['line-height'] === 'normal') {
				lineHeight = parseInt(window.getComputedStyle(el)['font-size'], 10) * 1.5;
			} else {
				lineHeight = parseInt(window.getComputedStyle(el)['line-height'], 10);
			}
			while (height > lineHeight * this.lines * 3) {
				text = text.substring(0, text.length / 2);
				el.textContent = text;
				height = parseInt(window.getComputedStyle(el).height, 10);
			}
			while (height > lineHeight * this.lines) {
				text = text.replace(/(\s)*([a-zA-Z0-9]+|\W)(\.\.\.)?$/, '...');
				el.textContent = text;
				height = parseInt(window.getComputedStyle(el).height, 10);
			}
		},
	},
};
</script>
