const formatters = {
	/**
	 * @function moneyFormat
	 * @param {Number} number a number to any precision of decimal places
	 * @returns {String} a USD currency-formatted String of the provided `number`
	 * 					 rounded to two decimal places.
	 */
	moneyFormat(number) {
		if (Number.isNaN(Number(number))) {
			// TODO: set up error/warn publishing
			return '$--';
		}
		const localeFormatting = {
			style: 'currency',
			currency: 'USD',
			minimumFractionDigits: 2,
		};
		return number.toLocaleString('en', localeFormatting);
	},
	/**
	 * @function toFloat
	 * @param {String} moneyString string representation of money
	 * @return {Number}
	 */
	toFloat(moneyString) {
		if (typeof moneyString === 'number') {
			return moneyString;
		}
		// comma replacement is necessary to not drop things post-comma
		// negation and dollar sign replacement get it to numeric
		const floatCleanString = parseFloat(moneyString.trim().replace(/[-$,:;'"=\sA-z]|[#&]\d*/g, ''));
		// TODO: improve this method https://jira.rmn.com/browse/EXT-6334
		// if (Number.isNaN(floatCleanString)) {
		// 	throw new Error('toFloat parameter can`t be converted to number!');
		// }
		return floatCleanString;
	},
	/**
	 * @function elemToFloat
	 * @param {Element} domNode The element you want to extract the floating-point number from
	 * @return {Number}
	 */
	elemToFloat(domNode) {
		return formatters.toFloat(domNode.textContent);
	},
	/**
	 * @function toInt
	 * @param {String} numberString The string representation of money
	 * @return {Number}
	 */
	toInt(numberString) {
		const cleanString = numberString.trim().replace(/[-$,]/g, '');
		// We always want base-10 integers in the extension
		return parseInt(cleanString, 10);
	},
	/**
	 * @function pluralize
	 * @param {Number} quantity The quantity of the given noun
	 * @param {String} noun The noun which should be formatted according to quantity
	 * @return {String}
	 */
	pluralize(quantity, noun) {
		if (quantity === 1) {
			return noun;
		}
		// check for noun ending with 'z', 'x', 's', 'sh', or 'ch'
		const suffix = /([zxs]|ch|sh)$/.test(noun) ? 'es' : 's';
		return noun + suffix;
	},
	/**
	 * @function getRandomInt
	 * @param {Number} min The min integer number
	 * @param {Number} max The max integer number
	 * @return {Number} a random integer number in [min, max] range
	 */
	getRandomInt(min, max) {
		return Math.floor(Math.random() * (max - min)) + min;
	},
	/**
	 * @function roundToDecimal
	 * @param {Number} number a number to any precision of decimal places
	 * @param {Number} digits a count of the places after comma, default: 2
	 * @return {Number} a rounded number
	 */
	roundToDecimal(number, digits = 2) {
		if (typeof number !== 'number') {
			// In case not a number was passed, nothing to round
			return number;
		}
		return parseFloat(number.toFixed(digits));
	},
};

export default formatters;
export const { moneyFormat, toFloat, elemToFloat, toInt, pluralize, getRandomInt, roundToDecimal } = formatters;
