<template>
	<div class="fixed-top-right">
		<CloseIcon :prompt-name="promptName" :on-dark-bg="true" />
		<div class="bbb-container-light bbb-color-blob">
			<span class="primary-text">{{ codes.length }} {{ codes.length | pluralize('Code') }} Found</span>
			<p class="secondary-text">Genie will test and apply {{ codes.length > 1 ? 'them' : 'it' }} for you.</p>
			<button :class="QA_CLASSES.applySavingsBtn" class="button-primary" @click="checkCodes">
				Apply All Savings
			</button>
		</div>
		<WarningMessage
			v-if="showFreeGiftsWarningMessage"
			:is-rounded="true"
			:is-indent-from-bottom="true"
			:close="closeWarningMessage"
		>
			Free gifts may be removed when codes are applied
		</WarningMessage>
	</div>
</template>
<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import { WarningMessage, CloseIcon } from './partials';
import { pluralize } from '../../utils/format';
import { COUPON_ACTIONS, DISPLAY_ACTIONS } from '../store/types';
import { backgroundMessaging } from '../lib';

export default {
	components: {
		CloseIcon,
		WarningMessage,
	},
	filters: {
		pluralize,
	},
	data() {
		return {
			promptName: 'test codes',
			QA_CLASSES: Object.freeze({
				applySavingsBtn: 'qa-apply-savings-button',
			}),
		};
	},
	computed: {
		...mapState(['merchant']),
		...mapState('display', ['showFreeGiftsWarningMessage']),
		...mapGetters('coupon', ['codes']),
	},
	mounted() {
		this.$fireViewPrompt(this.promptName);
		// used to instruct the extension to mute itself on next pageview
		backgroundMessaging.markCartPromptDisplayed(this.merchant.domain, Date.now());
		// since we are moving to only prompting here, we want to prevent this display
		// TODO: if this succeeds and is not rolled back, consolidate prompt display calls
		backgroundMessaging.markCboPromptDisplayed(this.merchant.domain, Date.now());
	},
	methods: {
		...mapActions('coupon', { startChecking: COUPON_ACTIONS.START_CHECKING }),
		...mapActions('display', {
			closeWarningMessage: DISPLAY_ACTIONS.CLOSE_FREE_GIFTS_WARNING,
		}),
		checkCodes() {
			this.$fireClickPrompt(this.promptName, 'test codes');
			this.startChecking(this.promptName);
		},
	},
};
</script>
