<template>
	<div class="fixed-top-right">
		<CloseIcon :prompt-name="promptName" :on-dark-bg="true" />
		<div class="bbb-container-light bbb-color-blob">
			<span class="primary-text">Save on Prescriptions</span>
			<p class="secondary-text">
				Up to 80% off your Rx.
				<a class="see-details" @click="seeDetails">See details</a>
			</p>
			<span class="button-primary" @click="comparePrices">Compare Rx Prices</span>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import { rxSaver } from '../consts/links';
import { CloseIcon } from './partials';
import { backgroundMessaging } from '../lib';

export default {
	components: {
		CloseIcon,
	},
	data() {
		return {
			promptName: 'rx saver prompt',
		};
	},
	computed: {
		...mapState(['merchant']),
	},
	mounted() {
		this.$fireViewPrompt(this.promptName);
		backgroundMessaging.markRxSaverPromptDisplayed(this.merchant.domain, Date.now());
	},
	methods: {
		comparePrices() {
			this.$fireClickPrompt(this.promptName, 'compare prices');
			window.open(rxSaver, '_blank');
		},
		seeDetails() {
			this.$fireClickPrompt(this.promptName, 'see details');
			window.open(rxSaver, '_blank');
		},
	},
};
</script>

<style lang="scss" scoped>
.button-primary {
	text-align: center;
}
</style>
