import Promise from 'bluebird';
import cloneDeep from 'lodash.clonedeep';
import isEqual from 'lodash.isequal';
import { backgroundMessaging, handleDriverError, owenAnalytics } from '../../lib';
import { completionActions, log } from '../../../utils';
import { ACTIONS, MUTATIONS } from '../types/coupon';
import { DISPLAY_MUTATIONS, VALIDATION_ACTIONS } from '../types';
import { COUPON_CHECKING_STATES } from '../../consts';

class CouponLookupTable extends Map {
	get(key) {
		if (!this.has(key)) {
			return { uuid: 'unavailable', ranking: 'unavailable' };
		}
		return super.get(key);
	}

	set(key, value) {
		const uuid = value.uuid || 'unavailable';
		const ranking = value.ranking ? value.ranking.toLowerCase() : 'unavailable';
		super.set(key, { uuid, ranking });
	}
}
const couponLookupTable = new CouponLookupTable();

export const defaultState = {
	coupons: [],
	originalCodes: [],
	parsedMerchantCodes: [],
	codesForValidation: [],
	workingCodes: [],
	startPrice: Number.MAX_SAFE_INTEGER,
	bestResult: {
		codes: [],
		price: Number.MAX_SAFE_INTEGER,
		discount: 0,
		freeShipping: false,
		freeGifts: 0,
	},
	preTaxShippingPrice: null,
	driver: null,
	isStackable: false,
	isOneCodeStacking: false,
	enforceOriginalCaseCodes: false,
	enforceMaxCoupons: Number.MAX_SAFE_INTEGER,
	allowNoSavingsCode: false,
	cartContinueUrl: null,
	completionAction: completionActions.default,
	codesRecentlyChecked: false,
	checkingStatus: COUPON_CHECKING_STATES.NOT_STARTED,
	checkingIndex: -1,
	startedCheckingAt: null,
	isAllCodesGathered: false,
	additionalCallout: {
		message: '',
		showAlways: false,
		showWhenCodeWasFound: false,
		isChanged: false,
	},
};

export const getters = {
	codes: state =>
		[
			...new Set(
				[
					...state.originalCodes,
					...state.coupons.filter(coupon => coupon.ranking === 'Top Rated').map(coupon => coupon.code),
					...state.parsedMerchantCodes,
					...state.coupons.filter(coupon => coupon.ranking !== 'Top Rated').map(coupon => coupon.code),
					...state.codesForValidation.map(coupon => coupon.code),
				].map(code => (state.enforceOriginalCaseCodes ? code : code.toUpperCase())),
			),
		].slice(0, state.enforceMaxCoupons),
	isNotStarted: state => state.checkingStatus === COUPON_CHECKING_STATES.NOT_STARTED,
	isChecking: state => state.checkingStatus === COUPON_CHECKING_STATES.CHECKING,
	isStacking: state => state.checkingStatus === COUPON_CHECKING_STATES.STACKING,
	isFinalizing: state => state.checkingStatus === COUPON_CHECKING_STATES.FINALIZING,
	totalToCheck: (state, getters) => {
		switch (state.checkingStatus) {
			case COUPON_CHECKING_STATES.CHECKING:
				return getters.codes.length;
			case COUPON_CHECKING_STATES.STACKING:
				return state.workingCodes.length;
			default:
				return 0;
		}
	},
	currentCode: (state, getters) => {
		switch (state.checkingStatus) {
			case COUPON_CHECKING_STATES.CHECKING:
				return getters.codes[state.checkingIndex];
			case COUPON_CHECKING_STATES.STACKING:
				if (getters.workingCodesFound) {
					return state.workingCodes[state.checkingIndex];
				}
				// Return last checked code in case working codes were not found
				return getters.codes[state.checkingIndex];
			default:
				return null;
		}
	},
	workingCodesFound: state => state.workingCodes.length > 0,
	noSavingsCodeFound: state =>
		state.bestResult.codes.length > 0 && state.bestResult.codes[0] !== null && state.allowNoSavingsCode,
	validCodeFound: (state, getters) =>
		state.bestResult.discount > 0 ||
		state.bestResult.freeGifts > 0 ||
		state.bestResult.freeShipping ||
		getters.noSavingsCodeFound,
};

export const mutations = {
	[MUTATIONS.UPDATE_BEST_RESULT](state, { codes, price, discount, freeShipping, freeGifts }) {
		state.bestResult = { codes, price, discount, freeShipping, freeGifts };
	},
	[MUTATIONS.UPDATE_PRE_TAX_SHIPPING_PRICE](state, preTaxShippingPrice) {
		state.preTaxShippingPrice = preTaxShippingPrice;
	},
	[MUTATIONS.UPDATE_ORIGINAL_CODES](state, originalCodes) {
		state.originalCodes = originalCodes;
	},
	[MUTATIONS.UPDATE_START_PRICE](state, startPrice) {
		state.startPrice = startPrice;
	},
	[MUTATIONS.UPDATE_PARSED_MERCHANT_CODES](state, parsedMerchantCodes) {
		state.parsedMerchantCodes = parsedMerchantCodes;
	},
	[MUTATIONS.UPDATE_WORKING_CODES](state, workingCodes) {
		state.workingCodes = workingCodes;
	},
	[MUTATIONS.UPDATE_GATHERED_CODES_STATUS](state) {
		state.isAllCodesGathered = true;
	},
	[MUTATIONS.SET_CHECKING_STATUS](state, checkingStatus) {
		state.checkingStatus = checkingStatus;
	},
	[MUTATIONS.SET_CHECKING_INDEX](state, checkingIndex) {
		state.checkingIndex = checkingIndex;
	},
	[MUTATIONS.SET_COMPLETION_ACTION](state, completionAction) {
		state.completionAction = completionAction;
	},
	[MUTATIONS.SET_ONE_CODE_STACKING](state) {
		state.isOneCodeStacking = true;
	},
	[MUTATIONS.SET_ADDITIONAL_CALLOUT](state, additionalCallout) {
		state.additionalCallout = { ...additionalCallout, isChanged: true };
	},
	[MUTATIONS.SET_STARTED_CHECKING_AT](state, timestamp) {
		state.startedCheckingAt = timestamp;
	},
	/** @param {boolean} hasRecentlyChecked */
	[MUTATIONS.SET_RECENTLY_CHECKED](state, hasRecentlyChecked) {
		state.codesRecentlyChecked = hasRecentlyChecked;
	},
};

export const actions = {
	async [ACTIONS.ADVANCE_TO_CHECKOUT]({ state }) {
		if (state.cartContinueUrl) {
			window.history.replaceState(window.history.state, window.document.title, state.cartContinueUrl);
		}

		switch (state.completionAction) {
			case completionActions.reload:
				window.location.reload();
				break;
			case completionActions.revisit:
				// We sometimes have to do this instead of `window.location.reload()`
				// to avoid picking up history from driver fetching
				// and posting to the page again
				window.location = window.location.href;
				break;
			case completionActions.custom:
				// Call custom driver complete action
				try {
					await state.driver.completeExperiencePromisified(state.bestResult.codes);
				} catch (e) {
					handleDriverError(e, {
						source: 'completeExperiencePromisified',
						bestCodes: state.bestResult.codes,
						bestDiscount: state.bestResult.discount,
					});
				}
				break;
			case completionActions.none:
			default:
				break;
		}
	},
	async [ACTIONS.RESTORE_USER_DATA]({ state, rootState }) {
		try {
			await state.driver.restoreUserData(rootState.merchant.domain);
		} catch (e) {
			handleDriverError(e, { source: 'restoreUserData' });
		}
	},
	async [ACTIONS.START_CHECKING]({ state, getters, commit, dispatch, rootState, rootGetters }, promptName) {
		// early exit if process already started
		if (!getters.isNotStarted) {
			return;
		}
		commit(MUTATIONS.SET_STARTED_CHECKING_AT, Date.now());
		commit(MUTATIONS.UPDATE_BEST_RESULT, cloneDeep(defaultState.bestResult));
		commit(MUTATIONS.UPDATE_WORKING_CODES, defaultState.workingCodes.slice());
		commit(MUTATIONS.SET_CHECKING_STATUS, COUPON_CHECKING_STATES.CHECKING);
		commit(MUTATIONS.SET_CHECKING_INDEX, 0);
		// have the extension open an outclick tab in the background
		backgroundMessaging.fireOutclick({
			uuid: rootState.merchant.uuid,
			pageName: window.location.hostname + window.location.pathname,
			pageType: rootGetters['display/pageType'],
			viewInstanceUuid: rootState.display.viewInstanceUuid,
			testVariations: rootState.testVariations,
		});
		backgroundMessaging.markCartPromptDisplayed(rootState.merchant.domain, Date.now());
		commit(`display/${DISPLAY_MUTATIONS.SET_OPEN_VIEW}`, 'checking', { root: true });
		commit(`display/${DISPLAY_MUTATIONS.SET_CURRENT_VIEW}`, 'checking', { root: true });

		// Repopulate the Lookup Table every time
		couponLookupTable.clear();
		state.coupons.forEach(coupon => {
			couponLookupTable.set(coupon.code, coupon);
		});

		// add support for validation codes
		const { validationSupport } = rootState.validation;
		log('Validation support: %j', validationSupport);
		if (validationSupport) {
			await dispatch(`validation/${VALIDATION_ACTIONS.GET_CODES_FOR_VALIDATION}`, state, { root: true });
			state.codesForValidation = rootGetters['validation/codesForValidation'];
			log(
				'Codes for validation: %j',
				state.codesForValidation ? state.codesForValidation.map(coupon => coupon.code) : [],
			);
		}

		// try to take start price from page before any other actions with DOM
		try {
			const startPrice = await state.driver.getStartPricePromisified();
			// commit changes if startPrice differ from default value
			if (startPrice !== Number.MAX_SAFE_INTEGER) {
				if (startPrice && typeof startPrice === 'number') {
					log('Start price: %j', startPrice);
					commit(MUTATIONS.UPDATE_START_PRICE, startPrice);
				} else {
					handleDriverError(new Error("Can't parse start price with provided total configuration"));
				}
			}
		} catch (e) {
			handleDriverError(e, { source: 'parsingStartPrice' });
		}

		try {
			await state.driver.storeUserData(rootState.merchant.domain);
		} catch (e) {
			handleDriverError(e, { source: 'storeUserData' });
		}

		// Setup everything before codes testing
		const filterCoupons = couponsArray =>
			Array.isArray(couponsArray) ? couponsArray.filter(code => Boolean(code) && typeof code === 'string') : [];
		try {
			try {
				await state.driver.beforeTestCodesPromisified();
			} catch (e) {
				handleDriverError(e, { source: 'beforeTestCodesPromisified' });
			}

			try {
				const parsedMerchantCodes = filterCoupons(await state.driver.getMerchantCodesPromisified());
				log('Merchant Codes Info: %j', {
					codes: parsedMerchantCodes,
					count: parsedMerchantCodes.length,
				});
				if (parsedMerchantCodes.length > 0) {
					commit(MUTATIONS.UPDATE_PARSED_MERCHANT_CODES, parsedMerchantCodes);
				}
			} catch (e) {
				handleDriverError(e, { source: 'getMerchantCodesPromisified' });
			}

			let originalPrice;
			let originalItemsCount;
			let originalShipping;
			try {
				const codes = await state.driver
					.getExistingCodesPromisified()
					.then(codeInfo => filterCoupons(codeInfo.codes));
				log('Existing Codes Info: %j', {
					codes,
					count: codes.length,
				});
				if (codes.length > 0) {
					commit(MUTATIONS.UPDATE_ORIGINAL_CODES, codes);
				}
			} catch (e) {
				handleDriverError(e, { source: 'getExistingCodesPromisifed' });
			}

			// add delay because of blicking if gathering of codes is very quickly
			const timeGatheringViewShownMs = 1500;
			const needWait = Date.now() - state.startedCheckingAt < timeGatheringViewShownMs;
			if (needWait) {
				await Promise.delay(state.startedCheckingAt + timeGatheringViewShownMs - Date.now());
			}

			commit(MUTATIONS.UPDATE_GATHERED_CODES_STATUS);

			if (state.originalCodes.length > 0) {
				try {
					const originalData = await state.driver.removeCodesPromisified(state.originalCodes);

					originalPrice = originalData;
					// If object was passed it means `originalItemsCount` is passed as well
					// need to check `originalData` too, because when `originalData` = `null`
					// typeof originalData will return 'object'
					if (originalData && typeof originalData === 'object') {
						({ originalPrice, originalShipping, originalItemsCount } = originalData);
					}
				} catch (e) {
					handleDriverError(e, { source: 'removeCodesPromisified', existingCodes: state.originalCodes });
				}
			}

			try {
				const updatedOriginalData = await state.driver.beforeCheckCodesPromisified(
					originalPrice,
					originalItemsCount,
					originalShipping,
				);
				let updatedOriginalPrice = updatedOriginalData;
				let updatedOriginalItemsCount;
				let updatedOriginalShipping;
				if (typeof updatedOriginalData === 'object') {
					({
						originalPrice: updatedOriginalPrice,
						originalShipping: updatedOriginalShipping,
						originalItemsCount: updatedOriginalItemsCount,
					} = updatedOriginalData);
				}

				// need to check if `updatedOriginalPrice >= 0`, because when `updatedOriginalPrice = 0`
				// `if` will `return false`, and `originalPrice` will not set
				if (updatedOriginalPrice >= 0) {
					originalPrice = updatedOriginalPrice;
				}
				if (updatedOriginalShipping) {
					originalShipping = updatedOriginalShipping;
				}
				if (updatedOriginalItemsCount) {
					originalItemsCount = updatedOriginalItemsCount;
				}

				// Log the 'start' event
				owenAnalytics.fireTestCodes(this, promptName, 'start', {
					codes: getters.codes.map(code => couponLookupTable.get(code)),
				});
				try {
					await state.driver.checkCodesPromisified(
						getters.codes,
						originalPrice || undefined,
						originalItemsCount || undefined,
						originalShipping || undefined,
					);
				} catch (e) {
					handleDriverError(e, {
						source: 'checkCodesPromisified',
						existingCodes: state.originalCodes,
						totalCodes: getters.codes.length,
						checkedCodes: state.checkingIndex + 1,
					});
					await dispatch(ACTIONS.APPLY_CODES);
				}
			} catch (e) {
				handleDriverError(e, { source: 'beforeCheckCodesPromisified', existingCodes: state.originalCodes });
				await dispatch(ACTIONS.APPLY_CODES);
			}
		} catch (e) {
			handleDriverError(e, { source: 'unrecognized' });
			await dispatch(ACTIONS.APPLY_CODES);
		}
	},
	async [ACTIONS.RECORD_CHECKING_RESULT](
		{ state, getters, commit, dispatch },
		{ code, finalPrice, finalDiscount, freeShipping = false, freeGifts = 0, isValid = true },
	) {
		// early exit if extension is not checking
		if (!getters.isChecking) {
			return;
		}
		log('code-checked: %j', arguments[1]); // eslint-disable-line prefer-rest-params

		// Log the 'check' event
		owenAnalytics.fireTestCodes(this, 'checking', 'check', {
			totalSavings: finalDiscount,
			codes: [couponLookupTable.get(code)],
		});

		const result = { ...state.bestResult };
		if (finalDiscount >= 0 && isValid) {
			/* Need to set `bestResult.codes` in case driver cannot
			remove code and cannot get existing codes to not replace best code new one */
			if (finalPrice < result.price || (finalPrice === result.price && result.codes.length === 0)) {
				log('currently the best code is: "%s"', code);
				result.price = finalPrice;
				result.codes = [code];
				result.discount = finalDiscount;
				// Force free shipping to be `false` in case discount is > 0
				// because free shipping should be displayed only in case there is no discount
				result.freeShipping = finalDiscount === 0 && freeShipping;
				// Code may offer discount and free gift at once
				result.freeGifts = freeGifts > 0 ? freeGifts : 0;
			} else if (freeShipping && !result.discount) {
				// Make free shipping coupon the best code in case
				// there was no coupon with discount
				log('currently the best code is with free shipping: "%s"', code);
				result.freeShipping = freeShipping;
				result.codes = [code];
			} else if (freeGifts > 0 && result.freeGifts <= freeGifts && !result.discount && !result.freeShipping) {
				// Make free gift coupon the best code only in case
				// there was no coupon with discount
				if (result.freeGifts < freeGifts) {
					log('currently the best code is only with free gifts: "%s"', code);
					result.freeGifts = freeGifts;
					result.codes = [code];
				} else if (
					state.originalCodes
						.map(unformattedCode => unformattedCode.toUpperCase())
						.includes(code.toUpperCase())
				) {
					// Make free gift coupon the best code in case it was applied by user
					// and gives the maximum possible amount of gifts
					log('currently the best code is existing code with free gifts: "%s"', code);
					result.codes = [code];
				}
			}
		}

		// In case driver will emit each code with false `isValid` flag
		// CBO offer will be displayed incorrectly because
		// min purchase will be always satisfied as the best price will be `MAX_SAFE_INTEGER`
		if (state.bestResult.price === Number.MAX_SAFE_INTEGER) {
			result.price = finalPrice;
		}

		if (!isEqual(result, state.bestResult)) {
			commit(MUTATIONS.UPDATE_BEST_RESULT, result);
		}

		// Apply codes if the merchant isn't stackable and all codes are tested
		if (state.checkingIndex < getters.codes.length - 1) {
			commit(MUTATIONS.SET_CHECKING_INDEX, state.checkingIndex + 1);
		} else if (!state.isStackable) {
			await dispatch(ACTIONS.APPLY_CODES);
		}
	},
	async [ACTIONS.START_STACKING]({ state, getters, commit, dispatch }, { codes, ...additionalInformation }) {
		// early exit if extension is not checking
		if (!getters.isChecking) {
			return;
		}

		log('stacking codes: %O', { codes, ...additionalInformation });

		if (codes.length < 2) {
			commit(MUTATIONS.SET_ONE_CODE_STACKING);
		}

		commit(MUTATIONS.SET_CHECKING_STATUS, COUPON_CHECKING_STATES.STACKING);

		if (codes.length > 0) {
			commit(MUTATIONS.UPDATE_WORKING_CODES, codes);
			commit(MUTATIONS.SET_CHECKING_INDEX, 0);
		}

		try {
			await state.driver.stackCodesPromisified({ codes, ...additionalInformation });
		} catch (e) {
			handleDriverError(e, {
				source: 'stackCodesPromisified',
				existingCodes: state.originalCodes,
				totalCodes: getters.codes.length,
				codesStacked: state.checkingIndex + 1,
				stackingCodesCount: codes.length,
			});
			await dispatch(ACTIONS.APPLY_CODES);
		}
	},
	[ACTIONS.RECORD_STACKING_RESULT](
		{ state, getters, commit },
		{ code, finalPrice, finalDiscount, freeShipping = false, freeGifts = 0 },
	) {
		// early exit if extension is not stacking
		if (!getters.isStacking) {
			return;
		}

		log('code stacked: %j', arguments[1]); // eslint-disable-line prefer-rest-params

		const { bestResult } = state;
		// Force free shipping to be `false` in case discount is > 0
		// because free shipping should be displayed only in case there is no discount
		const isFreeShipping = finalPrice === bestResult.price && freeShipping;
		const isFreeGift = finalPrice === bestResult.price && freeGifts > bestResult.freeGifts;
		if (finalPrice < bestResult.price || isFreeGift || isFreeShipping) {
			commit(MUTATIONS.UPDATE_BEST_RESULT, {
				codes: [...new Set([...bestResult.codes, code])], // append codes which were stacked
				price: finalPrice,
				discount: finalDiscount,
				freeShipping: isFreeShipping,
				freeGifts,
			});
		}

		commit(MUTATIONS.SET_CHECKING_INDEX, state.checkingIndex + 1);
	},
	async [ACTIONS.FINISH_STACKING](
		{ state, getters, commit, dispatch },
		{ codes, finalPrice, finalDiscount, freeShipping = false, freeGifts = 0 },
	) {
		// early exit if extension is not stacking
		if (!getters.isStacking) {
			return;
		}
		log('stacking complete');

		const newResult = { ...state.bestResult };

		if (Array.isArray(codes)) {
			newResult.codes = [...codes];
		}

		if (typeof finalPrice === 'number') {
			newResult.price = finalPrice;
		}
		// rewrite finalDiscount if there any
		if (typeof finalDiscount === 'number') {
			if (finalDiscount >= 0) {
				newResult.discount = finalDiscount;
			} else {
				handleDriverError(new Error('The negative discount value was received as the best discount.'), {
					source: 'stackable-complete event',
					bestCodes: codes,
					bestDiscount: finalDiscount,
				});
			}
		}
		// rewrite freeShipping if there any
		if (freeShipping) {
			newResult.freeShipping = freeShipping;
		}
		// rewrite freeGifts count if there any
		if (typeof freeGifts === 'number' && freeGifts >= 0) {
			newResult.freeGifts = freeGifts;
		}
		if (!isEqual(newResult, state.bestResult)) {
			commit(MUTATIONS.UPDATE_BEST_RESULT, newResult);
		}
		await dispatch(ACTIONS.APPLY_CODES);
	},
	async [ACTIONS.APPLY_CODES]({ state, commit, dispatch, getters, rootState, rootGetters }) {
		commit(MUTATIONS.SET_CHECKING_STATUS, COUPON_CHECKING_STATES.FINALIZING);

		let codesToApply = state.bestResult.codes;
		// if there were existing codes and we didn't receive discount or free gift, we will apply original codes back
		if (
			!state.bestResult.freeShipping &&
			state.bestResult.freeGifts === 0 &&
			!state.bestResult.discount &&
			state.originalCodes.length > 0
		) {
			codesToApply = state.originalCodes;
		}

		log('Best codes received: %j', codesToApply);
		log('Best Result: %o', state.bestResult);

		try {
			await state.driver.beforeApplyCodesPromisified(codesToApply);
		} catch (e) {
			handleDriverError(e, {
				source: 'beforeApplyCodesPromisified',
				bestCodes: codesToApply,
				bestDiscount: state.bestResult.discount,
			});
		}

		if (codesToApply.length && codesToApply[0] !== null) {
			try {
				const applyStatusData = await state.driver.applyCodesPromisified(codesToApply);
				// check response after applying code
				// throw an error if get status 'false' or endPrice is lawer then bestResult.price
				if (
					typeof applyStatusData === 'object' &&
					((Object.prototype.hasOwnProperty.call(applyStatusData, 'rmnCodeApplied') &&
						!applyStatusData.rmnCodeApplied) ||
						applyStatusData.endPrice > state.bestResult.price)
				) {
					handleDriverError(new Error('Failed to apply the code from the results screen'), {
						bestCodes: codesToApply,
						bestResultPrice: state.bestResult.price,
						statusData: applyStatusData,
					});
				}
			} catch (e) {
				handleDriverError(e, {
					source: 'applyCodesPromisified',
					bestCodes: codesToApply,
					bestDiscount: state.bestResult.discount,
				});
			}
		}

		let preTaxShippingTotal = null;
		try {
			preTaxShippingTotal = await state.driver.getPreTaxShippingTotal();
			if (
				preTaxShippingTotal !== null &&
				(typeof preTaxShippingTotal !== 'number' ||
					Number.isNaN(preTaxShippingTotal) ||
					Object.is(preTaxShippingTotal, -0) ||
					preTaxShippingTotal < 0)
			) {
				throw new Error('Pre tax shipping total is not a number, less than "0" or equal "-0"');
			}
			log('Pre Tax & Shipping Total: %o', preTaxShippingTotal);
		} catch (e) {
			handleDriverError(e, {
				source: 'getPreTaxShippingTotal',
				preTaxShippingTotal,
			});
			preTaxShippingTotal = null;
		}

		if (typeof preTaxShippingTotal === 'number') {
			commit(MUTATIONS.UPDATE_PRE_TAX_SHIPPING_PRICE, preTaxShippingTotal);
		}

		const { startPrice, bestResult } = state;
		// throw an error if startPrice differ from default value MAX_SAFE_INTEGER and is smaller then best result
		if (bestResult.price !== Number.MAX_SAFE_INTEGER && startPrice < bestResult.price) {
			handleDriverError(new Error('Price was increased'), {
				startPrice: state.startPrice,
				finalPrice: state.bestResult.price,
			});
		}

		log('Show final pop up');

		// Log the 'end' event
		const codeData = {
			totalSavings: state.bestResult.discount,
			codes: [],
		};
		if (codeData.totalSavings > 0) {
			codeData.codes = codesToApply.map(code => couponLookupTable.get(code));
		}
		const cboData = {
			uuid: rootState.cbo.uuid,
			discountType: 'dollar',
			discountAmount: 0,
		};
		const totalPriceDefined =
			state.preTaxShippingPrice !== null || state.bestResult.price !== Number.MAX_SAFE_INTEGER;
		const isPercentBased = rootGetters['cbo/isPercentBased'];
		if (totalPriceDefined || isPercentBased) {
			const totalPrice = state.preTaxShippingPrice || state.bestResult.price;
			const { amountOff, percentOff } = rootGetters['cbo/bestDiscountForTotal'](totalPrice);
			if (totalPriceDefined) {
				cboData.discountAmount = isPercentBased ? (totalPrice * percentOff) / 100 : amountOff;
			} else if (isPercentBased) {
				// Fallback in case `totalPrice` is not defined but we have percent based CBO
				cboData.discountType = 'percent';
				cboData.discountAmount = percentOff;
			}
		}
		const promptName =
			getters.validCodeFound || (rootGetters['cbo/isActivated'] && cboData.discountAmount)
				? 'deal success'
				: 'no savings found';
		owenAnalytics.fireTestCodes(this, promptName, 'end', codeData, cboData);

		commit(`display/${DISPLAY_MUTATIONS.SET_CURRENT_VIEW}`, 'results', { root: true });
		commit(`display/${DISPLAY_MUTATIONS.SET_OPEN_VIEW}`, 'offersList', { root: true });

		backgroundMessaging.setLastCodeTestingStatus(getters.validCodeFound);
		backgroundMessaging.markCodeCheckingComplete(rootState.merchant.domain, Date.now());

		log(`Total test time: ${(Date.now() - state.startedCheckingAt) / 1000}s.`);

		let displayedCBO = null;
		if (rootGetters['cbo/isPercentBased']) {
			displayedCBO = `${rootGetters['cbo/bestDiscount'].percentOff}%`;
		} else if (cboData.totalSavings) {
			displayedCBO = `$${cboData.totalSavings}`;
		}
		log('Displayed CBO: %o', displayedCBO);

		dispatch(ACTIONS.FINISH_CHECKING);
		if (state.codesForValidation.length) {
			dispatch(`validation/${VALIDATION_ACTIONS.SEND_RESULTS_TO_THE_VALIDATION}`, null, { root: true });
		}
	},
	[ACTIONS.FINISH_CHECKING]({ commit }) {
		commit(MUTATIONS.SET_RECENTLY_CHECKED, true);
		commit(MUTATIONS.SET_CHECKING_STATUS, defaultState.checkingStatus);
		commit(MUTATIONS.SET_CHECKING_INDEX, defaultState.checkingIndex);
		commit(MUTATIONS.UPDATE_ORIGINAL_CODES, defaultState.originalCodes.slice());
		commit(MUTATIONS.UPDATE_PARSED_MERCHANT_CODES, defaultState.parsedMerchantCodes.slice());
	},
};

export const initializeModule = ({
	coupons = defaultState.coupons.slice(),
	codesForValidation = defaultState.codesForValidation,
	driver = defaultState.driver,
	isStackable = defaultState.isStackable,
	enforceOriginalCaseCodes = defaultState.enforceOriginalCaseCodes,
	enforceMaxCoupons = defaultState.enforceMaxCoupons,
	allowNoSavingsCode = defaultState.allowNoSavingsCode,
	cartContinueUrl = defaultState.cartContinueUrl,
	codesRecentlyChecked = defaultState.codesRecentlyChecked,
	completionAction = defaultState.completionAction,
}) => ({
	namespaced: true,
	state: {
		coupons,
		codesForValidation,
		originalCodes: defaultState.originalCodes.slice(),
		parsedMerchantCodes: defaultState.parsedMerchantCodes.slice(),
		workingCodes: defaultState.workingCodes.slice(),
		startPrice: defaultState.startPrice,
		bestResult: cloneDeep(defaultState.bestResult),
		driver,
		isStackable,
		isOneCodeStacking: defaultState.isOneCodeStacking,
		enforceOriginalCaseCodes,
		enforceMaxCoupons,
		allowNoSavingsCode,
		cartContinueUrl,
		codesRecentlyChecked,
		completionAction,
		preTaxShippingPrice: defaultState.preTaxShippingPrice,
		checkingStatus: defaultState.checkingStatus,
		checkingIndex: defaultState.checkingIndex,
		startedCheckingAt: defaultState.startedCheckingAt,
		isAllCodesGathered: defaultState.isAllCodesGathered,
		additionalCallout: { ...defaultState.additionalCallout },
	},
	getters,
	mutations,
	actions,
});
