export default options => {
	if (!options.method) {
		Object.assign(options, { method: 'GET' });
	}
	let savedResponse = {};
	return fetch(options.url, {
		...options,
		credentials: 'include',
	})
		.then(response => {
			savedResponse = response;
			return response.text();
		})
		.then(res => {
			const responseText = res;
			let body = res;
			try {
				body = JSON.parse(res);
			} catch (e) {
				/* ignore */
			}
			return {
				status: savedResponse.status,
				statusText: savedResponse.statusText,
				headers: savedResponse.headers,
				redirected: savedResponse.redirected,
				body,
				text: () => responseText,
				json: () => JSON.parse(responseText),
			};
		});
};
