<style lang="scss" scoped>
@import '../../styles/vars/colors';

.cbo-action-wrapper {
	align-items: center;
	display: flex;
	flex-flow: column nowrap;

	.activated-text {
		color: $bbb-blue;
		padding-top: 0.5em;
	}
}
</style>

<template>
	<div class="cbo-action-wrapper" :class="QA_CLASSES.cboPrompt">
		<LoadingSpinner v-if="uiStatus === CBO_PROMPT_NAMES.LOADING" />
		<button v-else-if="uiStatus === CBO_PROMPT_NAMES.EXPIRED && isOnListView" class="button-text" @click="hideCbo">
			Dismiss
		</button>
		<template v-else-if="uiStatus === CBO_PROMPT_NAMES.NOT_LOGGED_IN">
			<button
				v-if="currentView === 'results'"
				:class="QA_CLASSES.cboLoginButton + ' button-primary'"
				@click="logIn"
			>
				Log In or Sign Up
			</button>
			<template v-else>
				<button :class="QA_CLASSES.cboLoginButton + ' button-primary'" @click="logIn">Login</button>
				<button class="button-text" @click="signUp">Sign Up — (It's Free)</button>
			</template>
		</template>
		<button v-else-if="uiStatus === CBO_PROMPT_NAMES.NOT_VERIFIED" class="button-primary" @click="verify">
			Try Again
		</button>
		<button
			v-else-if="uiStatus === CBO_PROMPT_NAMES.CAN_ACTIVATE"
			class="button-primary"
			:class="QA_CLASSES.cboActivateButton"
			@click="activate"
		>
			Activate
		</button>
		<span v-else-if="uiStatus === CBO_PROMPT_NAMES.ACTIVATED" class="activated-text primary-text">
			Activated!
		</span>
	</div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex';

import LoadingSpinner from './loading-spinner';
import CBO_PROMPT_NAMES from '../../consts/cbo-prompt-names';
import { CBO_ACTIONS, DISPLAY_MUTATIONS } from '../../store/types';
import { backgroundMessaging } from '../../lib';
import { POP_UP_TYPES } from '../../../consts';

export default {
	components: {
		LoadingSpinner,
	},
	props: {
		hideCbo: {
			type: Function,
			default: null,
		},
	},
	data() {
		return {
			CBO_PROMPT_NAMES,
			QA_CLASSES: Object.freeze({
				cboPrompt: 'qa-cbo-prompt',
				cboActivateButton: 'qa-cbo-activate-button',
				cboLoginButton: 'qa-cbo-login-button',
			}),
		};
	},
	computed: {
		...mapState(['merchant', 'testVariations']),
		...mapState('cbo', { cboUuid: 'uuid' }),
		...mapGetters('cbo', ['uiStatus']),
		...mapState('display', ['viewInstanceUuid', 'currentView']),
		...mapGetters('display', ['pageType']),
		isOnListView() {
			return this.currentView === 'offersList';
		},
	},
	methods: {
		...mapActions('cbo', { attemptToActivate: CBO_ACTIONS.ATTEMPT_TO_ACTIVATE }),
		...mapMutations('display', {
			setCurrentView: DISPLAY_MUTATIONS.SET_CURRENT_VIEW,
			setOpenView: DISPLAY_MUTATIONS.SET_OPEN_VIEW,
		}),
		_fireAnalytics(eventTarget) {
			this.$fireClickPrompt(this.uiStatus, eventTarget, [
				{
					inventoryUuid: this.cboUuid,
					inventoryType: 'rebate',
					siteUuid: this.merchant.uuid || this.merchant.domain,
				},
			]);
		},
		logIn() {
			this._fireAnalytics('login');
			this.openPopUp(POP_UP_TYPES.LOGIN);
		},
		signUp() {
			this._fireAnalytics('login');
			this.openPopUp(POP_UP_TYPES.SIGN_UP);
		},
		activate() {
			if (this.uiStatus === CBO_PROMPT_NAMES.CAN_ACTIVATE) {
				this._fireAnalytics('activate');
				this.attemptToActivate();
			}
		},
		verify() {
			this.openPopUp(POP_UP_TYPES.VERIFY);
		},
		openPopUp(popUpType) {
			backgroundMessaging.displayPopUp(popUpType);
			this.setOpenView(this.currentView);
			this.setCurrentView('backgroundRelaunch');
		},
	},
};
</script>
