import { backgroundMessaging, handleError } from '../../lib';
import { MUTATIONS, ACTIONS } from '../types/search';
import { DISPLAY_MUTATIONS } from '../types';

export const defaultState = {
	searchQuery: '',
	merchantList: [],
	topMerchantsList: [],
	additionalMerchantsList: [],
	isLoading: false,
	aditionalLoadIteration: 0,
};

export const mutations = {
	[MUTATIONS.SET_SEARCH_QUERY](state, query = '') {
		state.searchQuery = query;
	},
	[MUTATIONS.UPDATE_MERCHANT_LIST](state, merchantList = []) {
		state.merchantList = merchantList;
	},
	[MUTATIONS.UPDATE_TOP_MERCHANTS_LIST](state, topMerchantsList = []) {
		state.topMerchantsList = topMerchantsList;
	},
	[MUTATIONS.UPDATE_ADDITIONAL_MERCHANTS_LIST](state, additionalMerchantsList = []) {
		state.additionalMerchantsList = additionalMerchantsList;
	},
	[MUTATIONS.UPDATE_ADITIONAL_LOAD_ITERATION](state, iteration = 0) {
		state.aditionalLoadIteration = iteration;
	},
	[MUTATIONS.START_LOADING](state) {
		state.isLoading = true;
	},
	[MUTATIONS.END_LOADING](state) {
		state.isLoading = false;
	},
};

const generateOutclickLinks = (list, pageType, viewInstanceUuid, testVariations) => {
	return Promise.all(
		list.map(async merchant => {
			// Without uuid outclick URL will be invalid
			if (merchant.uuid) {
				// Try to use outclick URL to reach merchant page
				merchant.outclickURL = await backgroundMessaging.getOutclickURL({
					uuid: merchant.uuid,
					pageName: window.location.hostname + window.location.pathname,
					pageType,
					viewInstanceUuid,
					testVariations,
				});
			} else {
				// Shouldn't happen but left it to be notified
				handleError(new Error(`Merchant uuid is missing for ${merchant.domain}`));
			}

			if (!merchant.outclickURL) {
				// Fallback without using outclick URL
				merchant.outclickURL = `http://${merchant.domain}`;
			}
			return merchant;
		}),
	);
};

export const actions = {
	async [ACTIONS.SEARCH_FOR_MERCHANTS]({ commit, rootState, rootGetters }, query) {
		try {
			const merchantList = Object.values(await backgroundMessaging.searchForMerchants(query));
			const pageType = rootGetters['display/pageType'];
			const { viewInstanceUuid } = rootState.display;
			const { testVariations } = rootState;
			const merchantListWithOutclicks = await generateOutclickLinks(
				merchantList,
				pageType,
				viewInstanceUuid,
				testVariations,
			);
			commit(MUTATIONS.UPDATE_MERCHANT_LIST, merchantListWithOutclicks);
		} catch (e) {
			handleError(e);
		}
	},
	async [ACTIONS.SET_TOP_MERCHANTS]({ commit, rootState, rootGetters }, limit) {
		try {
			const topMerchantsList = Object.values(await backgroundMessaging.getTopMerchants(limit));

			const pageType = rootGetters['display/pageType'];
			const { viewInstanceUuid } = rootState.display;
			const { testVariations } = rootState;

			const topMerchantsListWithOutclicks = await generateOutclickLinks(
				topMerchantsList,
				pageType,
				viewInstanceUuid,
				testVariations,
			);

			commit(MUTATIONS.UPDATE_TOP_MERCHANTS_LIST, topMerchantsListWithOutclicks);
		} catch (e) {
			handleError(e);
		}
	},
	async [ACTIONS.SET_ADDITIONAL_MERCHANTS]({ state, commit, rootState, rootGetters }) {
		try {
			const currentIteration = state.aditionalLoadIteration;
			const additionalMerchantsList = Object.values(
				await backgroundMessaging.getAdditionalSearchMerchants(currentIteration),
			);
			commit(MUTATIONS.UPDATE_ADITIONAL_LOAD_ITERATION, currentIteration + 1);

			const pageType = rootGetters['display/pageType'];
			const { viewInstanceUuid } = rootState.display;
			const { testVariations } = rootState;

			const additionalMerchantsListWithOutclicks = await generateOutclickLinks(
				additionalMerchantsList,
				pageType,
				viewInstanceUuid,
				testVariations,
			);

			commit(MUTATIONS.UPDATE_ADDITIONAL_MERCHANTS_LIST, [
				...state.additionalMerchantsList,
				...additionalMerchantsListWithOutclicks,
			]);
		} catch (e) {
			handleError(e);
		}
	},
	[ACTIONS.NAVIGATE_TO_SEARCH]({ commit }) {
		commit(`display/${DISPLAY_MUTATIONS.SET_CURRENT_VIEW}`, 'merchantSearch', { root: true });
	},
	[ACTIONS.RETURN_STATE_TO_DEFAULT]({ commit }) {
		commit(MUTATIONS.SET_SEARCH_QUERY);
		commit(MUTATIONS.UPDATE_MERCHANT_LIST);
		commit(MUTATIONS.UPDATE_TOP_MERCHANTS_LIST);
		commit(MUTATIONS.UPDATE_ADDITIONAL_MERCHANTS_LIST);
		commit(MUTATIONS.UPDATE_ADITIONAL_LOAD_ITERATION);
		commit(MUTATIONS.END_LOADING);
	},
};

export const initializeModule = ({
	searchQuery = defaultState.searchQuery,
	merchantList = defaultState.merchantList.slice(),
	topMerchantsList = defaultState.topMerchantsList.slice(),
	additionalMerchantsList = defaultState.additionalMerchantsList.slice(),
	isLoading = defaultState.isLoading,
	aditionalLoadIteration = defaultState.aditionalLoadIteration,
} = {}) => ({
	namespaced: true,
	state: {
		searchQuery,
		merchantList,
		topMerchantsList,
		additionalMerchantsList,
		isLoading,
		aditionalLoadIteration,
	},
	mutations,
	actions,
});
