// TODO - would be nice to refactor to a modules setup
import createLogger from 'vuex/dist/logger';
import {
	initializeCboModule,
	initializeCouponModule,
	initializeDisplayModule,
	initializeSearchModule,
	initializeValidationModule,
} from './modules';
import { ACTIONS, MUTATIONS } from './types/root';
import { backgroundMessaging } from '../lib';

export const defaultState = {
	extensionVersion: null,
	installationUuid: null,
	codesForValidation: [],
	userStatus: {
		isLoggedIn: false,
		isVerified: false,
	},
	permissions: {
		all: true,
		rmn: true,
	},
	merchant: {
		uuid: null,
		name: null,
		domain: null,
	},
	testVariations: [],
};

export const mutations = {
	[MUTATIONS.UPDATE_MERCHANT](state, { domain, name, uuid }) {
		state.merchant = { domain, name, uuid };
	},
	[MUTATIONS.UPDATE_USER_STATUS](state, { isLoggedIn, isVerified }) {
		state.userStatus = { isLoggedIn, isVerified };
	},
	[MUTATIONS.UPDATE_PERMISSIONS](state, { all, rmn }) {
		state.permissions = { all, rmn };
	},
};

export const actions = {
	async [ACTIONS.REQUEST_USER_STATUS](context) {
		const userStatus = await backgroundMessaging.requestUserStatus();
		context.commit(MUTATIONS.UPDATE_USER_STATUS, userStatus);
		return userStatus;
	},
};
export const initializeStore = ({
	cashbackOffer,
	coupons,
	driver,
	isStackable,
	enforceOriginalCaseCodes,
	enforceMaxCoupons,
	allowNoSavingsCode,
	cartContinueUrl,
	validationSupport,
	completionAction,
	codesRecentlyChecked,
	currentView,
	openView,
	viewInstanceUuid,
	shouldStandDown,
	shouldShowCboInfoPrompt,
	isOnCartUrl,
	isSinglePageApp,
	showFreeGiftsWarningMessage,
	enableFreeGiftsWarning,
	enableDifferentDiscountMessage,
	shouldShowReviewUs,
	showCboLimitationWarning,
	restoreUserData,
	isCartEmpty,
	watchForCartUrlFn,
	watchForRestoreUserDataFn,
	isCartEmptyFn,
	merchantList,
	showValidCoupons,
	codesForValidation = defaultState.codesForValidation,
	extensionVersion = defaultState.extensionVersion,
	installationUuid = defaultState.installationUuid,
	userStatus = { ...defaultState.userStatus },
	permissions = { ...defaultState.permissions },
	merchant = { ...defaultState.merchant },
	testVariations = defaultState.testVariations.slice(),
} = {}) => ({
	modules: {
		cbo: initializeCboModule(cashbackOffer),
		coupon: initializeCouponModule({
			coupons,
			driver,
			isStackable,
			enforceOriginalCaseCodes,
			enforceMaxCoupons,
			allowNoSavingsCode,
			cartContinueUrl,
			completionAction,
			codesRecentlyChecked,
			codesForValidation,
		}),
		display: initializeDisplayModule({
			currentView,
			openView,
			viewInstanceUuid,
			shouldStandDown,
			shouldShowCboInfoPrompt,
			isOnCartUrl,
			isSinglePageApp,
			showFreeGiftsWarningMessage,
			enableFreeGiftsWarning,
			showCboLimitationWarning,
			restoreUserData,
			isCartEmpty,
			watchForCartUrlFn,
			watchForRestoreUserDataFn,
			isCartEmptyFn,
			enableDifferentDiscountMessage,
			shouldShowReviewUs,
			showValidCoupons,
		}),
		search: initializeSearchModule({ merchantList }),
		validation: initializeValidationModule({ merchant, installationUuid, codesForValidation, validationSupport }),
	},
	state: {
		extensionVersion,
		installationUuid,
		userStatus: {
			isLoggedIn: userStatus.isLoggedIn,
			isVerified: userStatus.isVerified,
		},
		permissions: {
			all: permissions.all,
			rmn: permissions.rmn,
		},
		merchant: {
			uuid: merchant.uuid,
			name: merchant.name,
			domain: merchant.domain,
		},
		testVariations,
	},
	mutations,
	actions,
	plugins: BUILD_ENVIRONMENT !== 'production' ? [createLogger()] : [],
});
