<style lang="scss">
@import '../styles/vars/colors';
@import '../styles/vars/sizes';

.list-view-container {
	background: $bbb-white;
	width: 470px;

	.list-view-body {
		max-height: 550px;
		overflow-x: hidden;
		overflow-y: auto;
		position: relative;
	}

	.cta-wrapper {
		border-bottom: solid 1px $bbb-gray-light;
		overflow: hidden;
	}

	.bbb-container-gray,
	.bbb-container-light,
	.bbb-container-dark {
		height: $bbb-container-height;
	}

	.codes-wrapper {
		padding: 1em 1.75em;
		.card {
			line-height: 25px !important; // to fix the hover effect on copy code button EXT-6287
		}
	}

	.code-section-header {
		text-align: center;
		font-size: 17px;
		margin: 0 0 1em;
	}

	@media screen and (max-height: 700px) {
		.list-view-body {
			max-height: 440px;
		}
	}
}
@media screen and (max-width: $medium-screen-width) {
	.list-view-container {
		width: $container-width * 0.9;
		.bbb-container-gray,
		.bbb-container-light,
		.bbb-container-dark {
			height: $bbb-container-height * 0.9;
		}
		.code-section-header {
			font-size: 16px;
		}
		.list-view-body {
			max-height: 490px;
		}
		.codes-wrapper {
			padding: 1em 1.55em;
		}
	}
	@media screen and (max-width: $small-screen-width) {
		.list-view-container {
			width: $container-width * 0.85;
			.bbb-container-gray,
			.bbb-container-light,
			.bbb-container-dark {
				height: $bbb-container-height * 0.85;
			}
			.code-section-header {
				font-size: 14px;
			}
			.list-view-body {
				max-height: 440px;
			}
			.codes-wrapper {
				padding: 1em 1.45em;
			}
		}
	}
}
</style>

<template>
	<div class="list-view-container">
		<Header :merchant-name="merchant.name" :merchant-domain="merchant.domain" :prompt-name="promptName" />
		<div class="list-view-body">
			<div v-if="shouldShowCboCta || showOffersListCta" class="cta-wrapper">
				<CboUi v-if="!showOffersListCta" :prompt-prefix="'list'" />
				<div v-else class="bbb-container-light bbb-color-blob bbb-no-logo codes-cta">
					<div>
						<h1 class="primary-text">
							<span :class="QA_CLASSES.codesCount">{{ coupons.length }}</span>
							{{ coupons.length | pluralize('Code') }} Found
						</h1>
						<h2 class="secondary-text">Genie will test &amp; apply them for you.</h2>
					</div>
					<button class="button-primary" :class="QA_CLASSES.applySavingsBtn" @click="checkCodes">
						Apply All Savings
					</button>
				</div>
			</div>
			<WarningMessage v-if="showFreeGiftsWarningMessage" :close="closeWarningMessage"
				>Free gifts may be removed when codes are applied</WarningMessage
			>
			<div v-if="coupons.length" class="codes-wrapper">
				<div v-if="shouldShowCboCta && !showOffersListCta" class="primary-text code-section-header">
					Try combining cash back offers with promo codes for maximum savings!
				</div>
				<ItemCard
					v-for="(coupon, index) in coupons"
					:key="index"
					:title="coupon.title || coupon.description"
					:info="`code: ${coupon.code}`"
				>
					<button
						slot="primaryCta"
						class="button-primary"
						:class="[{ copied: index === copiedIndex }, QA_CLASSES.codeButton]"
						@click="copy(coupon.code, index)"
					>
						{{ index === copiedIndex ? '✓ Copied' : 'Copy Code' }}
					</button>
				</ItemCard>
			</div>
			<PrivacyPolicy />
		</div>
		<Footer />
	</div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import copyText from 'clipboard-copy';
import { CboUi, ItemCard, Header, Footer, WarningMessage, PrivacyPolicy } from './partials';
import { backgroundMessaging } from '../lib';
import { pluralize } from '../../utils/format';
import { CBO_PROMPT_NAMES } from '../consts';
import { SEARCH_ACTIONS, COUPON_ACTIONS, DISPLAY_ACTIONS } from '../store/types';
import config from '../../config';

export default {
	components: {
		ItemCard,
		CboUi,
		Header,
		Footer,
		WarningMessage,
		PrivacyPolicy,
	},
	filters: {
		pluralize,
	},
	data() {
		return {
			copiedIndex: -1,
			CBO_PROMPT_NAMES,
			QA_CLASSES: Object.freeze({
				codesCount: 'qa-codes-count',
				applySavingsBtn: 'qa-apply-savings-button',
				codeButton: 'qa-code-button',
			}),
			// does not exist for lower environments
			helpUrl: 'https://help.retailmenot.com',
			profileUrl: `https://secure.${config.site.baseDomain}/my-rewards`,
		};
	},
	computed: {
		...mapState(['merchant', 'testVariations']),
		...mapState('cbo', { cboUuid: 'uuid' }),
		...mapState('coupon', ['coupons']),
		...mapState('display', ['viewInstanceUuid', 'showFreeGiftsWarningMessage']),
		...mapGetters('cbo', {
			cboUiStatus: 'uiStatus',
			isCboActivated: 'isActivated',
		}),
		...mapGetters('display', ['showOffersListCta', 'pageType']),
		shouldShowCboCta() {
			return this.cboUuid && this.cboUiStatus !== CBO_PROMPT_NAMES.DEACTIVATED;
		},
		promptName() {
			return `/list/${this.merchant.domain}`;
		},
	},
	mounted() {
		this.$fireViewPrompt(this.promptName);
		this.$fireCodeListImpressions(this.merchant.domain, this.coupons, {
			uuid: this.cboUuid,
			activated: this.isCboActivated,
		});
	},
	methods: {
		...mapActions('coupon', { startChecking: COUPON_ACTIONS.START_CHECKING }),
		...mapActions('search', { navigateToSearch: SEARCH_ACTIONS.NAVIGATE_TO_SEARCH }),
		...mapActions('display', {
			closeWarningMessage: DISPLAY_ACTIONS.CLOSE_FREE_GIFTS_WARNING,
		}),
		copy(textToCopy, index) {
			this.clipboardCopy(textToCopy, index);
			this.$fireClickPrompt(this.promptName, 'copy code');
			backgroundMessaging.fireOutclick({
				uuid: this.merchant.uuid,
				pageName: window.location.hostname + window.location.pathname,
				pageType: this.pageType,
				viewInstanceUuid: this.viewInstanceUuid,
				testVariations: this.testVariations,
			});
		},
		clipboardCopy(textToCopy, index) {
			return copyText(textToCopy).then(() => {
				this.copiedIndex = index;
			});
		},
		checkCodes() {
			this.$fireClickPrompt(this.promptName, 'test codes');
			this.startChecking(this.promptName);
		},
	},
};
</script>
