	display: flex;
	align-items: center;
	width: 100%;
	margin-top: 0;
	padding: 22px 16px;
	border-radius: 4px;
	box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.12);
	background-color: #ffffff;
	transition: box-shadow 0.25s ease;
	cursor: pointer;
	* {
		cursor: pointer;
	}
	&:hover {
		box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.18);
		.merchant-block-btn .button-primary:before {
			transform: scale(1.02);
		}
	}

	& + .merchant-block {
		margin-top: 16px;
	}
}
.merchant-block-logo {
	width: 55px;
	height: 55px;
	background-color: #fff;
	border-radius: 50%;
	border: 1px solid #f6f3ff;
	overflow: hidden;
	img {
		width: 100%;
		height: 100%;
		object-fit: scale-down;
	}
}

.merchant-block-content {
	flex: 1;
	padding: 0 16px;

	.primary-text {
		color: #222220;
		font-size: 14px;
		line-height: normal;
		letter-spacing: normal;
		display: block;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
	}

	.secondary-text {
		font-size: 14px;
		font-weight: 600;
		font-style: normal;
		line-height: normal;
		letter-spacing: normal;
		color: #555555;
		margin-top: 4px;
		margin-bottom: 0;
		display: block;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
	}
}

.merchant-block-btn {
	.button-primary {
		width: auto;
		margin-top: 0;
		margin-left: 0;
		padding: 15px 16px;
		&:before {
			box-shadow: none !important;
		}
	}
}
