</style>

<template>
	<div class="merchant-block" @click="openMerchantTab">
		<div class="merchant-block-logo">
			<img ref="logoImageRef" :src="merchantLogoUrl" alt="" @error="imageLoadErrorHandler" />
		</div>
		<div class="merchant-block-content">
			<span class="primary-text">{{ title }}</span>
			<p class="secondary-text">{{ merchantDescription }}</p>
		</div>
		<div class="merchant-block-btn">
			<button class="button-primary">
				Shop Store
			</button>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import driversInfo from '../../drivers/drivers-information.json';
import { DRIVER_DEFAULTS } from '../../consts';

const { DEFAULT_MAX_CODES_TO_CHECK } = DRIVER_DEFAULTS;
const driversInfoMap = new Map(driversInfo.map(driver => [driver.domain, driver.maxCoupons]));

export default {
	props: {
		promptName: {
			type: String,
			required: true,
		},
		domain: {
			type: String,
			required: true,
		},
		title: {
			type: String,
			required: true,
		},
		rebatesCount: {
			type: Number,
			required: true,
		},
		codesCount: {
			type: Number,
			required: true,
		},
		outclickUrl: {
			type: String,
			required: true,
		},
	},
	data() {
		return {
			defaultLogoUrl: 'https://www.retailmenot.com/thumbs/logos/default_store_logo.png',
		};
	},
	computed: {
		...mapState({ currentMerchant: 'merchant' }),
		merchantLogoUrl() {
			return `https://o.rmncdn.com/thumbs/logos/l/${this.domain}-coupons.jpg`;
		},
		codesCountToShow() {
			const codesCountFromDriver = driversInfoMap.has(this.domain)
				? driversInfoMap.get(this.domain)
				: DEFAULT_MAX_CODES_TO_CHECK;
			return Math.min(codesCountFromDriver, this.codesCount);
		},
		merchantDescription() {
			let cashbackString = '';
			let codesString = '';

			if (this.rebatesCount) {
				cashbackString = this.codesCountToShow ? 'Cash Back + ' : 'Cash Back';
			}
			if (this.codesCountToShow) {
				codesString = `${this.codesCountToShow} ${this.codesCountToShow === 1 ? 'Code' : 'Codes'}`;
			}
			return cashbackString + codesString;
		},
	},
	methods: {
		imageLoadErrorHandler() {
			const { logoImageRef } = this.$refs;
			// check to avoid error when refresh merchants list too often
			if (logoImageRef) {
				// replace logo with default image if logo can't be loaded
				logoImageRef.setAttribute('src', this.defaultLogoUrl);
			}
		},
		openMerchantTab() {
			window.open(this.outclickUrl);
			this.$fireClickPrompt(this.promptName, 'merchant cell', [
				{
					inventoryUuid: 'unavailable',
					inventoryType: 'store',
					siteUuid: this.currentMerchant.uuid || this.currentMerchant.domain,
				},
			]);
		},
	},
};
</script>
