<style lang="scss">
@import '../../styles/vars/colors';
@import '../../styles/vars/durations';
@import '../../styles/vars/z-index';

.bbb-close-button {
	cursor: pointer;
	stroke: $bbb-text-light;
	position: absolute;
	right: 20px;
	top: 21px;
	transition: stroke $sidecar-reveal-duration $sidecar-reveal-duration;
	z-index: z(close-icon);

	&.on-dark-bg {
		stroke: $bbb-white;
	}
}
</style>

<template>
	<CrossIcon :bbb-close-button="true" :on-dark-bg="onDarkBg" @click.native.stop.once="close" />
</template>

<script>
import { mapActions } from 'vuex';
import { DISPLAY_ACTIONS } from '../../store/types';
import CrossIcon from './cross-icon';

export default {
	components: {
		CrossIcon,
	},
	props: {
		promptName: {
			type: String,
			required: true,
		},
		onDarkBg: Boolean,
	},
	methods: {
		...mapActions('display', { closeExtension: DISPLAY_ACTIONS.CLOSE_EXTENSION }),
		close() {
			this.$fireClickPrompt(this.promptName, 'x');
			this.closeExtension();
		},
	},
};
</script>
