import handleError from './handle-error';
import { domainFromHostname } from '../../utils/browser';

export default function handleDriverError(e, extraInfo = {}) {
	let { message } = e;
	const matchingTokenError = message && message.match(/Unexpected token (.) in JSON at position (\d+)/i);
	// matchingTokenError[1] !== '<' If symbol equal to '<' that's mean that we have error with parsing HTML
	if (matchingTokenError && matchingTokenError[1] !== '<') {
		message = 'Unexpected token in JSON';
		[, extraInfo.errorTokenSymbol, extraInfo.errorTokenPosition] = matchingTokenError;
	}
	const bucketedMerchantError = new Error(`${window.location.hostname} - Error: ${message}`);
	bucketedMerchantError.stack = e.stack;
	handleError(bucketedMerchantError, { ...extraInfo, domain: domainFromHostname(window.location.hostname) });
}
