export default options => {
	const { url, headers = [], method = 'GET', caller = 'XMLHttpRequest', withCredentials = true } = options;
	let body = null;

	if (typeof options.body === 'string') {
		({ body } = options);
	} else if (options.body instanceof FormData) {
		body = new URLSearchParams(options.body).toString();
	} else if (options.body instanceof URLSearchParams) {
		body = options.body.toString();
	} else if (options.body instanceof Array || typeof options.body === 'object') {
		body = JSON.stringify(options.body);
	} else {
		({ body } = options);
	}

	return new Promise((resolve, reject) => {
		delete sessionStorage.rmnTemp;
		const script = document.createElement('script');

		const headersArr = Object.entries(headers).map(
			([headerName, headerValue]) => `request.setRequestHeader("${headerName}", "${headerValue}");`,
		);
		script.innerHTML = `
	var request = new ${caller}();
		request.withCredentials = ${withCredentials};
		request.open('${method}', '${url}', true);

		// Listen for ready state
		request.onreadystatechange = () => {
			if (!request || request.readyState !== 4) {
				return;
			}

			// Prepare the response
			const responseHeaders = reformatHeaders(request.getAllResponseHeaders());
			let responseData = request.responseText;

			sessionStorage.rmnTemp = JSON.stringify({
				body: responseData,
				status: request.status,
				statusText: request.statusText,
				headers: responseHeaders
			});
		};

		// Handle browser request cancellation (as opposed to a manual cancellation)
		request.onabort = () => {
			sessionStorage.rmnTemp = JSON.stringify({
				status: 600,
				statusText: 'ABORTED'
			});
		};
		// Handle low level network errors
		request.onerror = () => {
			sessionStorage.rmnTemp = JSON.stringify({
				status: 600,
				statusText: 'Network error'
			});
		};
		// Handle timeout
		request.ontimeout = () => {
			sessionStorage.rmnTemp = JSON.stringify({
				status: 600,
				statusText: 'timeout of ' + config.timeout + 'ms exceeded'
			});
		};


		// Add headers to the request
		${headersArr.join(' ')}

		// Send the request
		// when we have enclosed objects or arrays inside request json-body we need to
		// pass it like an object. Because such json will be incorrectly transformed into string.

		request.send('${body}');

		function reformatHeaders(headersString) {
	const ignoreDuplicateOf = [
		'age', 'authorization', 'content-length', 'content-type', 'etag',
		'expires', 'from', 'host', 'if-modified-since', 'if-unmodified-since',
		'last-modified', 'location', 'max-forwards', 'proxy-authorization',
		'referer', 'retry-after', 'user-agent'
	];
	const emptyHeadersObject = {};
	try {
		const headersArray = headersString.split('\\n');
		return headersArray.reduce((headersObject, current) => {
			const arr = current.split(':');
			const key = arr[0].trim();
			const value = arr[1].trim();
			if (ignoreDuplicateOf.indexOf(key) === -1) {
				headersObject[key] = value;
			}

			return headersObject;
		}, emptyHeadersObject);
	} catch (e) {
		return emptyHeadersObject;
	}
}
	`;

		document.body.appendChild(script);
		const interval = setInterval(() => {
			if (sessionStorage.rmnTemp) {
				const res = JSON.parse(sessionStorage.rmnTemp);
				delete sessionStorage.rmnTemp;
				script.remove();
				const responseText = res.body;
				try {
					res.body = JSON.parse(res.body);
				} catch (e) {
					/* ignore */
				}

				clearInterval(interval);
				// Non-existent status used to handle `onabort`, `onerror`, `ontimeout` request events
				if (res.status >= 600) {
					reject(new Error(res.statusText));
				}
				resolve({
					...res,
					text: () => responseText,
					json: () => JSON.parse(responseText),
				});
			}
		}, 300);
	});
};
