import { genieReporter } from './monitoring';
import { STORAGE_KEYS, MILLISECONDS } from '../consts';

/**
 * We make heavy use of computed property names in this file
 * This is supported in Chrome 47+, FF 34+
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Object_initializer
 */

export const getKeyFromStorage = async key => {
	try {
		const results = await browser.storage.local.get(key);
		return Array.isArray(key) ? results : results[key];
	} catch (e) {
		const err = e instanceof Error ? e : new Error(e);
		genieReporter.captureException(err, {
			extra: { source: { filePath: 'bg/storage-access.js', method: 'getKeyFromStorage', key } },
		});
		return null;
	}
};
export const setStorageValue = async dictionary => {
	try {
		await browser.storage.local.set(dictionary);
	} catch (e) {
		const err = e instanceof Error ? e : new Error(e);
		genieReporter.captureException(err, {
			extra: {
				source: { filePath: 'bg/storage-access.js', method: 'storage-access.setStorageValue' },
				dictionary,
			},
		});
	}
};

export const getRebateActivationEndpointOverride = () =>
	getKeyFromStorage(STORAGE_KEYS.OVERRIDES.REBATE_ACTIVATION_ENDPOINT);
export const getDataStoreIndexOverride = () => getKeyFromStorage(STORAGE_KEYS.OVERRIDES.DATA_STORE_INDEX);
export const getDataStoreApiKeyOverride = () => getKeyFromStorage(STORAGE_KEYS.OVERRIDES.DATA_STORE_API_KEY);

export const shouldDisplaySurvey = async () => {
	const testCodesCount = (await getKeyFromStorage(STORAGE_KEYS.NUMBER_OF_TIMES_CHECKED_CODES)) || 0;
	const lastCodeTestingStatus = (await getKeyFromStorage(STORAGE_KEYS.LAST_CODE_TESTING_STATUS)) || false;
	if (testCodesCount <= 3 || !lastCodeTestingStatus) {
		return false;
	}
	return !(await getKeyFromStorage(STORAGE_KEYS.HAS_SEEN_GENIE_SURVEY));
};
export const markSurveySeen = () => setStorageValue({ [STORAGE_KEYS.HAS_SEEN_GENIE_SURVEY]: true });

export const setDomainTimestamp = async (key, domain, timestamp) => {
	const domainLevelTimestamps = (await getKeyFromStorage(key)) || {};
	domainLevelTimestamps[domain] = timestamp;
	return setStorageValue({ [key]: domainLevelTimestamps });
};
export const hasRecentDomainTimestamp = async (key, domain, expirationTime) => {
	const domainLevelTimestamps = (await getKeyFromStorage(key)) || {};
	const domainLevelTimestamp = domainLevelTimestamps[domain] || 0;
	return domainLevelTimestamp + expirationTime >= Date.now();
};

export const markCboPromptDisplayed = (domain, timestamp) =>
	setDomainTimestamp(STORAGE_KEYS.DISPLAYED_CBO_PROMPTS, domain, timestamp);
export const shouldDisplayCboPrompt = async domain => {
	return !(await hasRecentDomainTimestamp(STORAGE_KEYS.DISPLAYED_CBO_PROMPTS, domain, MILLISECONDS.DAY));
};
export const markCartPromptDisplayed = (domain, timestamp) =>
	setDomainTimestamp(STORAGE_KEYS.DISPLAYED_CART_PROMPTS, domain, timestamp);
export const shouldDisplayCartPrompt = async domain => {
	return !(await hasRecentDomainTimestamp(STORAGE_KEYS.DISPLAYED_CART_PROMPTS, domain, MILLISECONDS.MINUTE * 20));
};

export const markRxSaverPromptDisplayed = (domain, timestamp) =>
	setDomainTimestamp(STORAGE_KEYS.DISPLAYED_RXSAVER_PROMPTS, domain, timestamp);
export const shouldDisplayRxSaverPrompt = async domain => {
	return !(await hasRecentDomainTimestamp(STORAGE_KEYS.DISPLAYED_RXSAVER_PROMPTS, domain, MILLISECONDS.MINUTE * 20));
};

export const getRecentlyDisplayedAmazonCboPrompts = async () => {
	const displayedAmazonCboPrompts = (await getKeyFromStorage(STORAGE_KEYS.DISPLAYED_AMAZON_CBO_PROMPTS)) || {};
	return Object.values(displayedAmazonCboPrompts).reduce((displayedPrompts, [cboUuid, timestamp]) => {
		if (timestamp + MILLISECONDS.DAY >= Date.now()) {
			displayedPrompts.add(cboUuid);
		}
		return displayedPrompts;
	}, new Set());
};
export const markAmazonCboPromptDisplayed = async (cboUuid, displayedAt) => {
	const displayedAmazonCboPrompts = await getKeyFromStorage(STORAGE_KEYS.DISPLAYED_AMAZON_CBO_PROMPTS);
	displayedAmazonCboPrompts[cboUuid] = displayedAt;
	return setStorageValue(STORAGE_KEYS.DISPLAYED_AMAZON_CBO_PROMPTS, displayedAmazonCboPrompts);
};

export const markCodeCheckingComplete = async (domain, timestamp) => {
	await setDomainTimestamp(STORAGE_KEYS.CODES_LAST_CHECKED, domain, timestamp);
	const codeCheckCount = (await getKeyFromStorage(STORAGE_KEYS.NUMBER_OF_TIMES_CHECKED_CODES)) || 0;
	await setStorageValue({ [STORAGE_KEYS.NUMBER_OF_TIMES_CHECKED_CODES]: codeCheckCount + 1 });
};

export const setLastCodeTestingStatus = async status => {
	await setStorageValue({ [STORAGE_KEYS.LAST_CODE_TESTING_STATUS]: status });
};

export const hasRecentlyCheckedCodes = domain =>
	hasRecentDomainTimestamp(STORAGE_KEYS.CODES_LAST_CHECKED, domain, MILLISECONDS.MINUTE * 5);

export const getLiveCbos = async () => {
	return (await getKeyFromStorage(STORAGE_KEYS.LIVE_CBOS)) || {};
};
export const setLiveCbos = async (domain, merchantCbos) => {
	const liveCbos = await getLiveCbos();
	liveCbos[domain] = merchantCbos;
	return setStorageValue({ [STORAGE_KEYS.LIVE_CBOS]: liveCbos });
};

export const getActivatedCbos = async () => {
	return (await getKeyFromStorage(STORAGE_KEYS.ACTIVATED_CBOS)) || {};
};
export const recordCboActivationState = async (cboUuid, activated) => {
	const activatedCbos = await getActivatedCbos();
	activatedCbos[cboUuid] = activated ? Date.now() : -1;
	return setStorageValue({ [STORAGE_KEYS.ACTIVATED_CBOS]: activatedCbos });
};
export const removeAllActivatedCbos = () => setStorageValue({ [STORAGE_KEYS.ACTIVATED_CBOS]: {} });

export const shouldDisplayStandDown = domain =>
	hasRecentDomainTimestamp(STORAGE_KEYS.STAND_DOWN_LIST, domain, MILLISECONDS.HOUR);
export const markStandDown = domain => setDomainTimestamp(STORAGE_KEYS.STAND_DOWN_LIST, domain, Date.now());

export const markCboInfoPromptSeen = () => setStorageValue({ [STORAGE_KEYS.HAS_SEEN_CBO_INFO_PROMPT]: true });
export const shouldDisplayCboInfoPrompt = async () => {
	return !(await getKeyFromStorage([STORAGE_KEYS.HAS_SEEN_CBO_INFO_PROMPT]));
};

export const markCompetitorCheck = () => setStorageValue({ [STORAGE_KEYS.COMPETITOR_CHECK]: Date.now() });
export const getCompetitorCheck = () => getKeyFromStorage(STORAGE_KEYS.COMPETITOR_CHECK);

export const setShowReviewSideCard = flag => setStorageValue({ [STORAGE_KEYS.SHOULD_SHOW_REVIEW_US]: flag });
export const getReviewUsShown = () => getKeyFromStorage(STORAGE_KEYS.SHOULD_SHOW_REVIEW_US);

// Please remove the below upon next modification of this function
export const removeUnusedStorageAreas = () =>
	Promise.all([
		browser.storage.local.remove('hasCompletedGenieSurvey'),
		browser.storage.local.remove('browserBlackList'),
		browser.storage.local.remove('hasShownFirstMessage'),
		browser.storage.local.remove('hasSeenNewUiPrompt'),
		browser.storage.local.remove('hasSeenFirstTimeCallout'),
	]);
