<style lang="scss" scoped>
@import '../../styles/vars/colors';
@import '../../styles/vars/sizes';

.pp-container {
	padding: 1em 1.75em;
	display: flex;
	justify-content: space-around;
	align-items: center;
	padding-top: 0;

	.policy-btn {
		background: $bbb-white;
		border: none;
		border-radius: 999px;
		color: $bbb-gray;
		padding: 0.85em 0;
		font-size: 11px;
		letter-spacing: 0.3px;
		box-shadow: 0px 0px 4px 0px $bbb-gray-light;
		width: 33%;
		transition: all 0.3s;

		&:hover {
			box-shadow: 0px 3px 8px 0px $bbb-gray-light;
		}
	}

	@media screen and (max-width: $medium-screen-width) {
		.policy-btn {
			padding: 0.8em 0;
			width: 30%;
		}
		@media screen and (max-width: $small-screen-width) {
			.policy-btn {
				padding: 0.75em 0;
				width: 27%;
			}
		}
	}

	.california-pp {
		color: $bbb-gray;
		font-size: 11px;
		margin: 0;
	}

	span {
		cursor: pointer;
		&:hover {
			text-decoration: underline;
		}
	}
}
</style>

<template>
	<div class="pp-container">
		<button class="policy-btn" @click="openUrl('Privacy Policy')">Privacy Policy</button>
		<p class="california-pp">
			California Privacy Policy:
			<span class="california-pp" @click="openUrl('Do Not Sell My Info')">
				Do Not Sell My Info
			</span>
		</p>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import { privacyPolicy, doNotSellMyInfo } from '../../consts/links';

export default {
	computed: {
		...mapState(['merchant']),
		promptName() {
			return this.merchant.domain;
		},
	},
	methods: {
		openUrl(link) {
			if (link === 'Privacy Policy') {
				this.$fireClickPrompt(this.promptName, 'Privacy Policy');
				window.open(privacyPolicy, '_blank');
			} else if (link === 'Do Not Sell My Info') {
				this.$fireClickPrompt(this.promptName, 'Do Not Sell My Info');
				window.open(doNotSellMyInfo, '_blank');
			}
		},
	},
};
</script>
