export default {
	/**
	 * @function enrichCodes
	 * @description adds detail to a list of codes. If merchant object is missing a code, that code's info is not returned
	 * @param {Object} merchant - the merchant that the codes belong to
	 * @param {Array} codes - the list of codes to enrich
	 * @return {Dictionary} whose key is a code and whose value is the code offer detail
	 */
	enrichCodes(merchant, codes) {
		return merchant.codes
			.filter(
				codeOffer =>
					// limit to just the codes passed in
					codes.indexOf(codeOffer.code) > -1,
			)
			.reduce((enrichedCodeOffers, currentCodeOffer) => {
				Object.assign(enrichedCodeOffers, { [currentCodeOffer.code]: currentCodeOffer });
				return enrichedCodeOffers;
			}, {});
	},
};
