import memoize from 'lodash.memoize';
import Promise from 'bluebird';
import { genieReporter } from '../bg/monitoring';
import adBlockers from '../consts/adBlockers.json';

const userAgent = (navigator.userAgent || '').toLowerCase();
const vendor = (navigator.vendor || '').toLowerCase();

const getCookie = name => {
	const arr = document.cookie.match(`(?:^|;)\\s*${name}=([^;]*)`);
	return arr && arr[1];
};
const setCookie = (cName, value, minutes, domain, path) => {
	let expires = '';
	if (minutes) {
		const date = new Date();
		date.setTime(date.getTime() + minutes * 60 * 1000);
		expires = `; expires=${date.toUTCString()}`;
	}
	document.cookie = `${cName}=${value}${expires}; domain=${domain ||
		window.location.hostname.replace(/[^.]+/, '')}; path=${path || '/'}`;
};
/**
 * @function wait
 * @param {Function} conditionMethod Methods that returns true when condition is done
 * @param {Boolean} withoutException false - to send exception in case of timeOut, true - returns true in case of timeOut
 * @param {Number} stepTime with this time step method is waiting for action
 * @param {Number} maxWaitTime max time for waiting an action
 * @return {Promise} false or throw Error - timeOut occured, true - condition was accomplished.
 */
const wait = (conditionMethod, maxWaitTime = 4000, stepTime = 300, withoutException = false) =>
	new Promise((resolve, reject) => {
		let time = 0;
		const interval = setInterval(() => {
			time += stepTime;
			if (time > maxWaitTime) {
				clearInterval(interval);
				if (!withoutException) {
					reject(new Error('Timeout is occured'));
				}
				resolve(false);
			}
			if (conditionMethod()) {
				clearInterval(interval);
				resolve(true);
			}
		}, stepTime);
	});

/**
 * Looks at a the provided URL for a query string.  If one exists, it is parsed into a flat
 * object of keys and values.  Otherwise, an empty object is returned.
 *
 * @param {string} [url=''] The URL you want to extract the query string keys and values from
 * @returns {object} A flat map of the keys and values of the query string, or an empty object
 */
const parseQueryString = (url = '') => {
	const queryString = url.split('?')[1];
	const queryObject = {};

	if (queryString) {
		const querySegments = queryString.split('&');
		querySegments.reduce((accumulator, segment) => {
			const [key, value] = segment.split('=');
			// folks send around a lot of garbage urls, so we try/catch
			try {
				queryObject[key] = decodeURIComponent(value);
			} catch (e) {
				queryObject[key] = value;
			}
			return accumulator;
		}, queryObject);
	}

	return queryObject;
};

const domainFromHostname = hostname => {
	let domain = hostname;
	const splitHostname = hostname.split('.');
	const arrayLength = splitHostname.length;

	// extracting the root domain here
	if (arrayLength > 2) {
		domain = `${splitHostname[arrayLength - 2]}.${splitHostname[arrayLength - 1]}`;
	}
	return domain;
};

// user-agent checks for which browser we are in.
// seemed safer than feature/API mapping given recent FF rewrite.
const isFirefox = memoize(() => {
	const match = userAgent.match(/(?:firefox)\/(\d+)/);
	return match !== null;
});
const isChrome = memoize(() => {
	const match = /google inc/.test(vendor) ? userAgent.match(/(?:chrome)\/(\d+)/) : null;
	return match !== null;
});

const checkExtensionExistence = (extensionId, webAccessibleResourcePath) => {
	const urlPart = isChrome() ? 'chrome-extension' : 'moz-extension';
	return fetch(`${urlPart}://${extensionId}/${webAccessibleResourcePath}`)
		.then(response => Promise.resolve(response.ok))
		.catch(e => {
			if (e.name === 'TypeError' && e.message === 'Failed to fetch') {
				// This error occurs when the fetch url does not exist
				// which means the competitor does not exist or we don't have access to extension web-accessible resource
				return Promise.resolve(false);
			}
			return Promise.reject(e);
		});
};

const lookForInstalledAdBlockers = () => {
	const adBlockersOn = [];
	if (isChrome()) {
		return Promise.each(adBlockers, adBlocker => {
			return checkExtensionExistence(adBlocker.extensionId, adBlocker.webAccessibleResourcePath)
				.then(response => {
					if (response) {
						adBlockersOn.push(adBlocker.name);
					}
					return Promise.resolve();
				})
				.catch(() => {
					genieReporter.captureException(new Error('Check for adBlocker existence failed'), {
						extra: {
							source: '_checkInstalledAdBlockers',
							adBlocker: adBlocker.name,
						},
					});
					return Promise.resolve([]);
				});
		}).then(() => adBlockersOn);
	}
	return adBlockersOn;
};

export {
	getCookie,
	setCookie,
	wait,
	parseQueryString,
	domainFromHostname,
	isFirefox,
	isChrome,
	checkExtensionExistence,
	lookForInstalledAdBlockers,
};
