import uuid from '@rmn/uuid';
import merge from 'lodash.merge';
import bus from '../bus';
import busSetup from './bus-setup';
import newGenie from './app-initialization';
import backgroundMessaging from './background-messaging';
import config from '../../config';
import uiHelpers from './ui-helpers';
import { browserUtils, driverUtils, completionActions, log } from '../../utils';
import handleDriverError from './handle-driver-error';
import { DRIVER_DEFAULTS, STORAGE_KEYS_SUFFIXES } from '../consts';
import { setShowReviewSideCard, getReviewUsShown } from '../../bg/storage-access';

/**
 * @typedef {Object} baseGenieSettings
 * @property {String} openView - name of the component to render when extension is opened
 * @property {boolean} codesRecentlyChecked - localStorage check of whether genie was just run
 * @property {Object} merchant - object with merchant details and driver initialization
 * @property {String} merchantName - what it says on the tin
 * @property {String} completionAction - action name for continue to checkout functionality
 * @property {String} domain
 * @property {Array} coupons - list of coupon code objects
 * @property {Object} cashbackOffer - applicable CBO
 * @property {Object} bus - the content scripts message bus
 */

const { parseQueryString } = browserUtils;
const { isEnabled } = driverUtils;
const { DEFAULT_MAX_CODES_TO_CHECK } = DRIVER_DEFAULTS;

function _shouldStandDown(shouldStandDown) {
	const currentUrl = window.location.href;
	const currentQueryParams = parseQueryString(currentUrl);
	// handle specific affiliate who 200's before client-side requesting sites in FF
	// done as single entry now for speed, will move to list if 2nd instance found
	const clientSideStandown = document.referrer.startsWith('http://go.redirectingat.com');
	if (
		shouldStandDown ||
		// afsrc is an industry convention query parameter
		// that can sometimes be used to mark affiliate links
		// linkshare requires that we check for it, although
		// it does not seem to be present in many links
		// for us or our competitors
		(currentQueryParams.afsrc === '1' &&
			!// Linkshare
			(
				currentUrl.includes('OOTtr9mlaCk') ||
				// CJ
				currentUrl.includes('2210202') ||
				// CJ SEM
				currentUrl.includes('7764160') ||
				currentUrl.includes('8336150') ||
				currentUrl.includes('8336151') ||
				currentUrl.includes('7997401') ||
				currentUrl.includes('8336152') ||
				currentUrl.includes('8336153')
			)) ||
		clientSideStandown
	) {
		if (clientSideStandown) {
			backgroundMessaging.setStandDown(window.location.hostname);
		}
		log('standing down to competitor affiliate link');
		return true;
	}
	return false;
}

const checkSetReviewUsShown = async settings => {
	await getReviewUsShown().then(flag => {
		if (flag === undefined) {
			setShowReviewSideCard(true);
		} else {
			settings.shouldShowReviewUs = flag;
		}
	});
};

async function createAndHydrateSettings(displayInfo, merchantDriverSettings) {
	const { merchant, shouldShowCboPrompt, shouldShowCartPrompt } = displayInfo;
	let showValidCoupons = false;
	if (merchantDriverSettings) {
		({ showValidCoupons } = merchantDriverSettings);
	}

	let completionAction = completionActions.default;
	if (merchantDriverSettings && merchantDriverSettings.completionAction) {
		if (typeof merchantDriverSettings.completionAction === 'function') {
			completionAction = merchantDriverSettings.completionAction();
		} else {
			({ completionAction } = merchantDriverSettings);
		}
	}
	const baseGenieSettings = merge(displayInfo, {
		hasDriver: !!merchantDriverSettings,
		coupons: merchant.codes || [],
		bus,
		merchant: {
			...merchantDriverSettings,
			uuid: merchant.uuid,
			name: merchant.title,
			domain: merchant.browserExtensionDomainOverride || merchant.domain,
		},
		openView: 'offersList',
		cartContinueUrl: merchantDriverSettings && merchantDriverSettings.cartContinueUrl,
		validationSupport: merchantDriverSettings && merchantDriverSettings.validationSupport,
		completionAction,
		userStatus: displayInfo.userStatus,
		cashbackOffer: (merchant.rebates || [])[0],
		isOnCartUrl: false,
		isSinglePageApp: false,
		showValidCoupons,
		restoreUserData: false,
		isCartEmpty: false,
		merchantsViewOnly: false,
		codesRecentlyChecked: displayInfo.codesRecentlyChecked,
		codesForValidation: displayInfo.codesForValidation,
		shouldStandDown: _shouldStandDown(displayInfo && displayInfo.shouldStandDown),
		viewInstanceUuid: uuid.randomBase32(),
		watchForCartUrlFn: null,
		watchForRestoreUserDataFn: null,
		isCartEmptyFn: null,
		enableFreeGiftsWarning: !!(merchantDriverSettings && merchantDriverSettings.enableFreeGiftsWarning),
		showFreeGiftsWarningMessage: false,
		enableDifferentDiscountMessage: false,
		shouldShowReviewUs: true,
	});

	let driver;

	const currentDomain = uiHelpers.getFullDomain();
	const isOnBlacklistedSubdomain = (merchant.browserExtensionBlacklistedSubdomains || []).some(subdomain =>
		uiHelpers.isInSubdomain(subdomain, currentDomain),
	);
	const isSiteEnabled = merchant.isEnabledOnBrowserExtension;

	// Special case for google given how often users engage with Genie there
	if (currentDomain === 'google.com') {
		log('merchants view only');
		baseGenieSettings.openView = 'merchantSearch';
		baseGenieSettings.merchantsViewOnly = true;
		return Promise.resolve(baseGenieSettings);
	}

	if (baseGenieSettings.blockedByNetwork) {
		log('blocked by network');
		baseGenieSettings.openView = 'blockedByNetwork';
		return Promise.resolve(baseGenieSettings);
	}

	if (isOnBlacklistedSubdomain || !isSiteEnabled) {
		log('current site not supported');
		baseGenieSettings.merchant.domain = uiHelpers.getFullDomain(window.location.hostname);
		baseGenieSettings.openView = 'notSupported';
		if (currentDomain === config.site.baseDomain) {
			// if we are on RMN, ensure the genie banner is removed.
			// we do NOT do this for the homepage banner given how disruptive /redraw heavy
			// the removal of that prominent element would be.
			const genieBannerEl = document.querySelector('.js-genie-banner');
			if (genieBannerEl) {
				genieBannerEl.remove();
			}
			baseGenieSettings.openView = 'closed';
		}
		return Promise.resolve(baseGenieSettings);
	}

	const canDisplayCodes = baseGenieSettings.coupons.length > 0;
	const understandsWebsite = baseGenieSettings.hasDriver && isEnabled(merchant, baseGenieSettings.extensionVersion);
	const canApplyCbo = baseGenieSettings.cashbackOffer;

	baseGenieSettings.understandsWebsite = understandsWebsite;

	if (!canDisplayCodes && canApplyCbo && baseGenieSettings.shouldStandDown) {
		log('CBO is deactivated due to stand down');
		baseGenieSettings.openView = 'standDown';
		return Promise.resolve(baseGenieSettings);
	}

	if (!canApplyCbo && !canDisplayCodes) {
		// No coupons nor cashback available
		log('no content available for current site');
		baseGenieSettings.openView = 'noContent';
		if (currentDomain === config.site.baseDomain) {
			baseGenieSettings.openView = 'closed';
		}
		return Promise.resolve(baseGenieSettings);
	}
	// handle CBO only case correctly
	if (!canDisplayCodes) {
		baseGenieSettings.openView = 'cboPrompt';
	}

	const maxCodes = (merchantDriverSettings && merchantDriverSettings.maxCoupons) || DEFAULT_MAX_CODES_TO_CHECK;
	baseGenieSettings.coupons = baseGenieSettings.coupons.slice(0, maxCodes);

	if (understandsWebsite && (canDisplayCodes || canApplyCbo)) {
		baseGenieSettings.isSinglePageApp = !!merchantDriverSettings.isSinglePageApp;
		baseGenieSettings.restoreUserData = !!merchantDriverSettings.restoreUserData;
		// In case driver works in single page mode `isOnCartUrl` must be set to `false`
		// `watchForCartUrl` will update `isOnCartUrl` when needed
		if (!baseGenieSettings.isSinglePageApp) {
			// isOnCartUrl has default value of false (if crashes code testing will not be allowed)
			// Handle possible exceptions while running isOnCartUrl
			try {
				baseGenieSettings.isOnCartUrl = Boolean(merchantDriverSettings.isOnCartUrl(window.location.href));
			} catch (e) {
				handleDriverError(e, { source: 'isOnCartUrl' });
			}
		}
		// CASE: Cart Page with valid merchant driver and at least 1 piece of content
		if (baseGenieSettings.isOnCartUrl || baseGenieSettings.isSinglePageApp) {
			// If driver is not single page app and isCartEmpty function is set we will check the cart
			if (!baseGenieSettings.isSinglePageApp && typeof merchantDriverSettings.isCartEmpty === 'function') {
				// isCartEmpty default value is false
				// (if crashes code testing will be allowed - cart considered as not empty, but we will be notified)
				// Handle possible exceptions while running isCartEmpty
				try {
					baseGenieSettings.isCartEmpty = !!merchantDriverSettings.isCartEmpty();
				} catch (e) {
					handleDriverError(e, { source: 'isCartEmpty' });
				}
				// Disallow code testing if cart is empty
				if (baseGenieSettings.isCartEmpty) {
					log('Cart is empty! Disallowing codes testing.');
					return _determineNonCartPageInitialView(baseGenieSettings, shouldShowCboPrompt);
				}
			}
			log('preparing to support merchant cart page');
			try {
				// Exception might be thrown in the driver constructor
				// Need to handle it, otherwise extension may become unresponsive
				driver = new merchantDriverSettings.Driver();
				driver.name = merchantDriverSettings.name;
			} catch (e) {
				handleDriverError(e, { source: 'constructor' });
				// Fallback to non-cart page to avoid codes testing but still show codes list
				log('failed to create driver, disallowing codes testing');
				// Set `isOnCartUrl` to `false` to disable `Apply Savings` button
				baseGenieSettings.isOnCartUrl = false;
				return _determineNonCartPageInitialView(baseGenieSettings, shouldShowCboPrompt);
			}
			// For non-single page drivers display warning as we are on the cart page
			if (merchantDriverSettings.enableFreeGiftsWarning && !merchantDriverSettings.isSinglePageApp) {
				baseGenieSettings.showFreeGiftsWarningMessage = !(await backgroundMessaging.getDriverStorage(
					`${merchantDriverSettings.domain}${STORAGE_KEYS_SUFFIXES.FREE_GIFTS}`,
				));
			}
			if (merchantDriverSettings.showDifferentDiscountMessage) {
				baseGenieSettings.enableDifferentDiscountMessage = !(await backgroundMessaging.getDriverStorage(
					`${merchantDriverSettings.domain}${STORAGE_KEYS_SUFFIXES.DIFFERENT_DISCOUNT}`,
				));
			}
			await checkSetReviewUsShown(baseGenieSettings);
			Object.assign(baseGenieSettings, {
				driver,
				isStackable: merchantDriverSettings.stackable,
				isCartEmptyFn: merchantDriverSettings.isCartEmpty,
				watchForCartUrlFn: driver.watchForCartUrl.bind(driver),
				watchForRestoreUserDataFn: driver.watchForRestoreUserData.bind(driver),
				enforceOriginalCaseCodes: merchantDriverSettings.isOriginalCaseCodes,
				enforceMaxCoupons: merchantDriverSettings.maxCoupons,
				allowNoSavingsCode: merchantDriverSettings.allowNoSavingsCode,
				name: merchantDriverSettings.name,
			});
			return _determineCartPageInitialView(baseGenieSettings, shouldShowCboPrompt, shouldShowCartPrompt);
		}
	}
	// If there is a driver, reaching here means we are on a non-cart page
	// If there is no driver, this is fallback to non-cart view to avoid codes testing but still show codes list
	if (baseGenieSettings.hasDriver) {
		log('supported merchant on a non-cart page');
	} else {
		log('no merchant driver, preparing to display listview without CTA');
	}
	return _determineNonCartPageInitialView(baseGenieSettings, shouldShowCboPrompt);
}

function _determineCartPageInitialView(genieSettings, shouldShowCboPrompt, shouldShowCartPrompt) {
	const { shouldStandDown, cashbackOffer, coupons, codesRecentlyChecked, shouldShowSurvey } = genieSettings;
	const { isSinglePageApp } = genieSettings.merchant;
	if (shouldStandDown) {
		genieSettings.currentView = 'closed';
	} else if (codesRecentlyChecked && shouldShowSurvey) {
		genieSettings.currentView = 'survey';
	} else if (coupons.length > 0 && shouldShowCartPrompt && !isSinglePageApp) {
		genieSettings.currentView = 'cartPrompt';
	} else if (cashbackOffer && shouldShowCboPrompt && !isSinglePageApp) {
		genieSettings.currentView = 'cboPrompt';
	} else {
		genieSettings.currentView = 'closed';
	}
	return Promise.resolve(genieSettings);
}

function _determineNonCartPageInitialView(genieSettings, shouldShowCboPrompt) {
	if (genieSettings.shouldStandDown) {
		genieSettings.currentView = 'closed';
		return Promise.resolve(genieSettings);
	}
	if (genieSettings.cashbackOffer && !genieSettings.understandsWebsite) {
		if (shouldShowCboPrompt) {
			genieSettings.currentView = 'cboPrompt';
		}
	}
	return Promise.resolve(genieSettings);
}

export { _shouldStandDown, createAndHydrateSettings, _determineNonCartPageInitialView, _determineCartPageInitialView };

/**
 * @function setupUi
 * @param {Object} displayInfo - contains coupons, cbos, and user info
 * @param {Object} merchant - merchant module export with driver and settings
 */

export default async function setupUi(displayInfo, merchant) {
	const settings = await createAndHydrateSettings(displayInfo, merchant);
	log('settings %O', settings);
	const { cashbackOffer } = settings;
	if (BUILD_ENVIRONMENT !== 'production') {
		if (cashbackOffer && cashbackOffer.discounts && cashbackOffer.discounts.length) {
			const { offerStatus, endDate, discounts } = cashbackOffer;
			const availableDiscounts = discounts.map(discount => {
				if (discount.percentOff) {
					return {
						percentOff: discount.percentOff,
					};
				}
				return {
					discount: discount.amountOff,
					minimumPurchase: discount.minimumPurchase,
				};
			});

			let endDateFormatted = endDate;
			if (Date.parse(endDate)) {
				endDateFormatted = new Date(endDate).toISOString().substr(0, 10);
			}
			const cboInfo = {
				availableDiscounts,
				offerStatus,
				endDate: endDateFormatted,
			};
			log('Active CBO: %O', cboInfo);
		} else {
			log('No active CBO');
		}
	}
	window.RMNGenieApp = await newGenie(settings);
	const { RMNGenieApp } = window;
	busSetup.init(bus, RMNGenieApp);
}
