<style lang="scss" scoped>
@import '../styles/vars/colors';
@import '../styles/vars/sizes';
$transition-duration: 333ms;

.progress-bar {
	--bbbGoldRgba: #{rgba($bbb-gold, 0)}; // expose $bbb-gold for JS as an rgba() string

	background-color: rgba($bbb-white, 0.11);
	border-radius: 999px;
	margin-top: 1em;
	position: relative;
	height: 35px;
	width: $progress-bar-width;

	.progress {
		background-color: $bbb-gold;
		border-radius: 999px;
		box-shadow: 0 0 30px var(--bbbGoldRgba);
		position: absolute;
		height: 100%;
		top: 0;
		transition: box-shadow $transition-duration, width $transition-duration;
	}
}
@media screen and (max-width: $medium-screen-width) {
	.progress-bar {
		width: $progress-bar-width * 0.9;
	}
	@media screen and (max-width: $small-screen-width) {
		.progress-bar {
			width: $progress-bar-width * 0.85;
		}
	}
}
</style>

<template>
	<div class="bbb-container-dark justify-center">
		<CloseIcon :prompt-name="promptName" :on-dark-bg="true" />

		<template v-if="isFinalizing || isOneCodeStacking">
			<span class="primary-text">Almost Done!</span>
			<p class="secondary-text">Genie's applying the best codes to your cart.</p>
		</template>

		<template v-else-if="isStacking && workingCodesFound">
			<span class="primary-text">We're maximizing savings by stacking codes.</span>
			<p class="secondary-text">{{ currentCode }}</p>
		</template>

		<template v-else>
			<template v-if="isAllCodesGathered">
				<span class="primary-text">Testing {{ codes.length }} {{ codes.length | pluralize('Code') }}</span>
				<p class="secondary-text">Code: {{ currentCode }}</p>
			</template>
			<template v-else>
				<span class="primary-text">Checking For Codes</span>
				<p class="secondary-text">Let the Magic Happen</p>
			</template>
		</template>

		<div ref="progressBar" class="progress-bar" :style="overflowHidden">
			<!-- eslint-disable-next-line prettier/prettier; I refuse to write mutant HTML -->
			<div class="progress" :style="progressBarWidth"></div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import { pluralize } from '../../utils/format';
import CloseIcon from './partials/close-icon';

const GLOW_THRESHOLD_PCT = 60;

export default {
	components: {
		CloseIcon,
	},
	filters: {
		pluralize,
	},
	data() {
		return {
			glowRgbaTransparent: null, // set by mounted()
			promptName: 'checking codes',
		};
	},
	computed: {
		...mapState('coupon', ['checkingIndex', 'isOneCodeStacking', 'isAllCodesGathered']),
		...mapGetters('coupon', [
			'codes',
			'workingCodesFound',
			'totalToCheck',
			'currentCode',
			'isStacking',
			'isFinalizing',
		]),
		percentComplete() {
			return Math.min(100, 100 * ((this.checkingIndex + 1) / this.totalToCheck));
		},
		progressBarWidth() {
			return `width: ${this.percentComplete}% !important`;
		},
		overflowHidden() {
			// `overflow: hidden` needed to enforce progress bar shape at 0%, but must be removed for glow at threshold
			return this.percentComplete < GLOW_THRESHOLD_PCT ? 'overflow: hidden !important' : '';
		},
	},
	watch: {
		percentComplete(newVal) {
			if (newVal <= GLOW_THRESHOLD_PCT) {
				this.setProgressBarGlow(this.glowRgbaTransparent); // reset to no glow
			} else {
				const glowPercent = (this.percentComplete - GLOW_THRESHOLD_PCT) / (100 - GLOW_THRESHOLD_PCT);
				const glowColor = this.glowRgbaTransparent.replace(', 0)', `, ${glowPercent})`);
				this.setProgressBarGlow(glowColor);
			}
		},
	},
	mounted() {
		// $refs will be undefined during testing, so check and default if necessary; see: https://github.com/vuejs/vue-test-utils/issues/351
		this.glowRgbaTransparent = this.$refs.progressBar
			? getComputedStyle(this.$refs.progressBar).getPropertyValue('--bbbGoldRgba')
			: 'rgba(255, 175, 36, 0)';
	},
	methods: {
		setProgressBarGlow(rgba) {
			this.$refs.progressBar.style.setProperty('--bbbGoldRgba', rgba, 'important');
		},
	},
};
</script>
