/**
 * @typedef {Object} merchant
 * @property {String} uuid - merchant uuid in CQMS
 * @property {String} title - merchant name
 */

/**
 * @typedef {Object} extensionDisplayInfo
 * @property {merchant} merchant - merchant data
 * @property {userStatus} userStatus - boolean flags for whether the user is currently logged in and verified
 * @property {Boolean} shouldStandDown - boolean flag of whether to suppress any initial view
 */

/**
 * @typedef {Object} userStatus
 * @property {Boolean} isLoggedIn - boolean flag for whether the user is currently logged in
 * @property {Boolean} isVerfied - boolean flag for whether the user is currently verified
 */

/**
 * @typedef {Object} cboActivationResult
 * @property {Boolean} success - boolean flag for whether the activation succeeded
 */

/**
 * @module messageDispatcher
 */
const messageDispatcher = {
	/**
	 * @method _sendMessage
	 * @member messageDispatcher
	 * @param {Object} messageObject the payload to send to our background page
	 * @param {String} messageObject.instruction - required key to determine which handler to run on the background page
	 * @param {Boolean} expectsCallback -- if lack of data should present an error
	 * @returns {Promise<Object>} Promise which resolves to callback value or rejects with error
	 */
	_sendMessage(messageObject, expectsCallback) {
		// it is incumbent on the caller to catch
		// as we cannot predict where in a promise chain
		// we may be invoked
		return new Promise((resolve, reject) => {
			chrome.runtime.sendMessage(messageObject, data => {
				// `data` may store `false` value which is a valid response
				if (data || data === false) {
					resolve(data);
					return;
				}
				if (chrome.runtime.lastError) {
					reject(chrome.runtime.lastError);
					return;
				}
				if (expectsCallback === false) {
					resolve();
					return;
				}
				reject(new Error(`no data returned for ${messageObject.instruction}!`));
			});
		});
	},
	/**
	 * @method retrieveDisplayInfo
	 * @member messageDispatcher
	 * @param {String} hostname - the window.location.hostname
	 * @returns {Promise<extensionDisplayInfo>} Promise which resolves to callback value or rejects with error.
	 */
	retrieveDisplayInfo({ hostname, pathname = null, search = null }) {
		return this._sendMessage({
			instruction: 'getDisplayInformation',
			hostname,
			pathname,
			search,
		});
	},
	/**
	 * @method searchForMerchants
	 * @member messageDispatcher
	 * @param {String} searchString - the partial text to search for merchants based on
	 * @returns {Promise<Array>} Promise which resolves to array of merchant objects or rejects with error
	 */
	searchForMerchants(searchString) {
		return this._sendMessage({
			instruction: 'getMerchantSearchResults',
			searchString,
		});
	},
	/**
	 * @method getTopMerchants
	 * @member messageDispatcher
	 * @param {Number} limit - quantity of merchants to return
	 * @returns {Promise<Array>} Promise which resolves to array of merchant objects or rejects with error
	 */
	getTopMerchants(limit) {
		return this._sendMessage(
			{
				instruction: 'getTopMerchantsList',
				limit,
			},
			true,
		);
	},
	/**
	 * @method getAdditionalSearchMerchants
	 * @member messageDispatcher
	 * @param {Number} page - number of page to return
	 * @returns {Promise<Array>} Promise which resolves to array of merchant objects or rejects with error
	 */
	getAdditionalSearchMerchants(page) {
		return this._sendMessage(
			{
				instruction: 'getAdditionalSearchMerchantsList',
				page,
			},
			true,
		);
	},
	/**
	 * @method requestUserStatus
	 * @member messageDispatcher
	 * @returns {Promise<userStatus>} Promise which resolves to Object with user info
	 */
	requestUserStatus() {
		return this._sendMessage({
			instruction: 'requestUserStatus',
		});
	},
	/**
	 * @method activateCbo
	 * @member messageDispatcher
	 * @param {Object} activationInfo
	 * @returns {Promise<cboActivationResult>}
	 */
	activateCbo({ cashbackOffer, viewInstanceUuid, pageName, pageType, testVariations }) {
		return this._sendMessage({
			instruction: 'activateCbo',
			cashbackOffer,
			viewInstanceUuid,
			pageName,
			pageType,
			testVariations,
		});
	},
	checkCboStatus(domain) {
		return this._sendMessage({
			instruction: 'checkCboStatus',
			domain,
		});
	},
	setStandDown(domain) {
		return this._sendMessage(
			{
				instruction: 'setStandDown',
				domain,
			},
			false,
		);
	},
	markCartPromptDisplayed(domain, timestamp) {
		return this._sendMessage(
			{
				instruction: 'markCartPromptDisplayed',
				domain,
				timestamp,
			},
			false,
		);
	},
	markRxSaverPromptDisplayed(domain, timestamp) {
		return this._sendMessage(
			{
				instruction: 'markRxSaverPromptDisplayed',
				domain,
				timestamp,
			},
			false,
		);
	},
	shouldDisplayPrompt(domain, promptType = 'cart') {
		return this._sendMessage(
			{
				instruction: 'shouldDisplayPrompt',
				domain,
				promptType,
			},
			true,
		);
	},
	fireOutclick({ uuid, pageName, pageType, viewInstanceUuid, testVariations }) {
		return this._sendMessage({
			instruction: 'outclick',
			uuid,
			pageName,
			pageType,
			viewInstanceUuid,
			testVariations,
		});
	},
	getOutclickURL({ uuid, pageName, pageType, viewInstanceUuid, testVariations }) {
		return this._sendMessage({
			instruction: 'getOutclickURL',
			uuid,
			pageName,
			pageType,
			viewInstanceUuid,
			testVariations,
		});
	},
	openNewTab({ url }) {
		return this._sendMessage({ instruction: 'openNewTab', url }, true);
	},
	getDriverStorage(domain) {
		return this._sendMessage(
			{
				instruction: 'getDriverStorage',
				domain,
			},
			false,
		);
	},
	setDriverStorage(domain, value) {
		return this._sendMessage({
			instruction: 'setDriverStorage',
			domain,
			value,
		});
	},
	deleteDriverStorage(domain) {
		return this._sendMessage({
			instruction: 'deleteDriverStorage',
			domain,
		});
	},
	/**
	 * @method markCboPromptDisplayed
	 * @param {String} domain
	 * @param {Number} timestamp - a timestamp of Date.now(), or -1
	 */
	markCboPromptDisplayed(domain, timestamp) {
		return this._sendMessage(
			{
				instruction: 'markCboPromptDisplayed',
				domain,
				timestamp,
			},
			false,
		);
	},
	/**
	 * @method markCodeCheckingComplete
	 */
	markCodeCheckingComplete(domain, timestamp) {
		return this._sendMessage(
			{
				instruction: 'markCodeCheckingComplete',
				domain,
				timestamp,
			},
			false,
		);
	},
	/**
	 * @method setLastCodeTestingStatus
	 */
	setLastCodeTestingStatus(status) {
		return this._sendMessage(
			{
				instruction: 'setLastCodeTestingStatus',
				status,
			},
			false,
		);
	},
	/**
	 *
	 * @param flag Boolean => need to show or not review us side card on result screen.
	 * @returns {*|Promise<Object>} // unnecessary return;
	 */
	setShowReviewUsSideCard(flag) {
		return this._sendMessage(
			{
				instruction: 'setShowReviewUsSideCard',
				flag,
			},
			false,
		);
	},
	/**
	 * @method markSurveySeen
	 */
	markSurveySeen() {
		return this._sendMessage(
			{
				instruction: 'markSurveySeen',
			},
			false,
		);
	},
	markCboInfoPromptSeen() {
		return this._sendMessage(
			{
				instruction: 'markCboInfoPromptSeen',
			},
			false,
		);
	},
	updateIcon(iconImage) {
		return this._sendMessage(
			{
				instruction: 'updateIcon',
				value: iconImage,
			},
			false,
		);
	},
	/**
	 * Adds or removes a badge over the extension icon
	 * @param {Number} savingsCount the number to display in the badge over the icon, or falsy to remove
	 */
	updateBadge(savingsCount) {
		return this._sendMessage(
			{
				instruction: 'updateBadge',
				value: savingsCount,
			},
			false,
		);
	},
	displayPopUp(type) {
		return this._sendMessage({
			instruction: 'displayPopUp',
			type,
		});
	},
	focusPopUp() {
		return this._sendMessage({
			instruction: 'focusPopUp',
		});
	},
	closePopUp() {
		return this._sendMessage({
			instruction: 'closePopUp',
		});
	},
};

export default messageDispatcher;
