<template>
	<div ref="scrollBlock" class="merchant-search-body" @scroll="scrollHandler">
		<div class="merchant-search-results">
			<div v-if="showNoResultsMessage" class="search-no-results">
				<p class="secondary-text">Sorry, we couldn’t find any results for ‘{{ searchQuery }}’</p>
			</div>
			<div v-else class="merchant-search-results-list">
				<MerchantBlock
					v-for="merchant in merchantListToShow"
					:key="merchant.uuid"
					:domain="merchant.domain"
					:title="merchant.title"
					:rebates-count="merchant.rebatesCount"
					:codes-count="merchant.codesCount"
					:outclick-url="merchant.outclickURL"
					:prompt-name="promptName"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
import { SEARCH_ACTIONS, SEARCH_MUTATIONS } from '../../../store/types';
import { MerchantBlock } from '..';

export default {
	components: {
		MerchantBlock,
	},
	props: {
		promptName: {
			type: String,
			required: true,
		},
	},
	data() {
		return {
			noResults: false,
		};
	},
	computed: {
		...mapState('search', [
			'searchQuery',
			'merchantList',
			'topMerchantsList',
			'additionalMerchantsList',
			'isLoading',
		]),
		showDefaultView() {
			return !this.searchQuery.trim();
		},
		showNoResultsMessage() {
			return this.searchQuery.trim() && this.noResults;
		},
		merchantListToShow() {
			if (this.showDefaultView) {
				return [...this.topMerchantsList, ...this.additionalMerchantsList];
			}
			return [...this.merchantList];
		},
	},
	watch: {
		searchQuery(value) {
			// old versions of chrome don't support scrollTo method
			if (this.$refs.scrollBlock.scrollTo) {
				this.$refs.scrollBlock.scrollTo(0, 0);
			} else {
				this.$refs.scrollBlock.scrollTop = 0;
			}

			this.startLoading();
			if (value.trim()) {
				this.loadResultsList();
			} else {
				this.clearResultsList();
				this.loadTopMerchantsList();
			}
		},
	},
	mounted() {
		this.loadTopMerchantsList();
	},
	methods: {
		...mapActions('search', {
			setTopMerchants: SEARCH_ACTIONS.SET_TOP_MERCHANTS,
			setAdditionalMerchants: SEARCH_ACTIONS.SET_ADDITIONAL_MERCHANTS,
			searchForMerchants: SEARCH_ACTIONS.SEARCH_FOR_MERCHANTS,
		}),
		...mapMutations('search', {
			startLoading: SEARCH_MUTATIONS.START_LOADING,
			endLoading: SEARCH_MUTATIONS.END_LOADING,
			updateSearchResults: SEARCH_MUTATIONS.UPDATE_MERCHANT_LIST,
		}),
		loadTopMerchantsList() {
			this.startLoading();
			this.setTopMerchants().finally(() => {
				this.endLoading();
			});
		},
		loadResultsList() {
			this.noResults = false;
			this.startLoading();
			this.searchForMerchants(this.searchQuery).finally(() => {
				this.endLoading();
				this.noResults = !this.merchantList.length;
			});
		},
		clearResultsList() {
			this.updateSearchResults();
		},
		scrollHandler() {
			if (this.showDefaultView) {
				const listWrap = this.$refs.scrollBlock;
				const onBottom = listWrap.offsetHeight + listWrap.scrollTop === listWrap.scrollHeight;
				if (onBottom && !this.isLoading) {
					this.startLoading();

					this.setAdditionalMerchants().finally(() => {
						this.endLoading();
					});
				}
			}
		},
	},
};
</script>
