export default {
	async asyncForEachExecutedInParallel(arr, cb, ...args) {
		return Promise.all(arr.map(el => cb(el, ...args)));
	},
	/** This performs the same function as Bluebird's Promise.each() function
	 * It might be preferable to use asyncForEachExecutedInSuccession when you don't
	 * want the overhead of importing the entire Bluebird library
	 */
	async asyncForEachExecutedInSuccession(arr, cb, ...args) {
		const retVals = [];
		for (let idx = 0; idx < arr.length; idx++) {
			retVals.push(await cb(arr[idx], ...args)); // eslint-disable-line no-await-in-loop
		}
		return retVals;
	},
};
