export default {
	/**
	 * @function compare
	 * @description compares two semantic versions (http://semver.org/)
	 * @param {String} lhs a semver of format major.minor.patch, ex. 1.0.225
	 * @param {String} rhs another semver
	 * @return {int} -1 if lhs is greater, 0 if same, 1 if rhs is greater
	 */
	compare(lhs, rhs) {
		const lhsComponents = lhs.split('.');
		const rhsComponents = rhs.split('.');
		for (let i = 0; i < 3; i++) {
			const nlhs = Number(lhsComponents[i]);
			const nrhs = Number(rhsComponents[i]);
			if (!Number.isNaN(Number(nlhs)) && Number.isNaN(Number(nrhs))) {
				return 1;
			}
			if (Number.isNaN(Number(nlhs)) && !Number.isNaN(Number(nrhs))) {
				return -1;
			}
			// In the case both are NaN, comparison is false, so we skip that check
			if (nlhs > nrhs) {
				return 1;
			}
			if (nrhs > nlhs) {
				return -1;
			}
		}
		return 0;
	},
};
