import { browserUtils } from '../../utils';
import { genieReporter } from '../../bg/monitoring';

const _checkIfAdWasBlocked = async () => {
	try {
		const testElementForAdBlocker = window.document.createElement('div');
		testElementForAdBlocker.setAttribute(
			// set class names that are in block list of ad blocker extensions
			'class',
			'pub300x250 pub300x250m pub728x90 text-ad textAd textad textads text-ads text-ad-links adBanner',
		);
		testElementForAdBlocker.setAttribute(
			'style',
			'width: 1000px !important; height: 1000px !important; position: absolute !important; left: -10000px !important; top: -1000px !important; display: block !important',
		);
		const renderedElement = document.body.appendChild(testElementForAdBlocker);
		// ad blocker is on if the testElementForAdBlocker isn't rendered
		return !renderedElement.offsetParent;
	} catch (e) {
		genieReporter.captureException(e, {
			extra: {
				source: '_checkIfAdWasBlocked',
			},
		});
		return false;
	}
};

/**
 * @function handleError
 * @param {Error} e the exception being thrown and handled by handleError
 * @param {Object} [extraInfo] optional supplemental data
 */
async function handleError(e, extraInfo = {}) {
	// set lifetime for sentry errors
	// time returns in seconds
	const { RMNGenieApp } = window;
	const wasAdBlocked = await _checkIfAdWasBlocked();
	extraInfo.adBlockerTurnedOn = Boolean(wasAdBlocked);
	extraInfo.adBlockers = await browserUtils.lookForInstalledAdBlockers();
	extraInfo.lifetime = RMNGenieApp ? (Date.now() - RMNGenieApp.setupTime) / 1000 : 0;
	extraInfo.cookiesStatus = window.navigator.cookieEnabled ? 'on' : 'off';
	extraInfo.closedWhileTesting = RMNGenieApp && RMNGenieApp.$store.state.display.closedWhileTesting ? 'yes' : 'no';
	// Force error to be string to avoid invalid object output for custom errors
	console.error(e.toString()); // eslint-disable-line
	const messageForAladdin = {
		errorMessage: e.message,
		errorUrl: window.location.href,
		errorInfo: extraInfo,
	};
	chrome.runtime.sendMessage(
		{
			instruction: 'error',
			errorStack: e.stack,
			domain: extraInfo.domain,
			logOnBackground: false,
			...messageForAladdin,
		},
		() => {},
	);
	let errorContainer = document.querySelector('#__genieQAErrorElement');
	if (!errorContainer) {
		errorContainer = document.createElement('ol');
		errorContainer.id = '__genieQAErrorElement';
		errorContainer.style = 'display:none;';
		document.documentElement.appendChild(errorContainer);
	}
	const errorMessageContainer = document.createElement('li');
	errorMessageContainer.innerHTML = JSON.stringify(messageForAladdin, undefined, 4);
	errorContainer.appendChild(errorMessageContainer);
}

export default handleError;
