import devtools from '@vue/devtools';
import Vue from 'vue';
import Vuex, { mapState, mapGetters } from 'vuex';
import get from 'lodash.get';
import * as Components from '../components';
import { LoadingSpinner } from '../components/partials';
import { initializeStore } from '../store';
import { OwenAnalyticsPlugin } from '../plugins';
import backgroundMessaging from './background-messaging';
import handleError from './handle-error';

const appendStyleSheet = (containingEl, filename) => {
	const styleNode = document.createElement('style');
	const cssPath = chrome.extension.getURL(`built/${filename}.css`);
	styleNode.innerHTML = `@import "${cssPath}";`;
	containingEl.appendChild(styleNode);
};

export default function newGenie(settings) {
	if (process.env.VUE_DEVTOOLS === 'devtools') {
		Vue.config.devtools = true;
		devtools.connect('http://localhost', 8098);
	}

	Vue.config.errorHandler = (err, vm, info) => {
		// lodash.get is used here to avoid errors if required property does not exist
		const currentView = get(vm, '$store.state.display.currentView', null);
		const component = get(vm, '$options._componentTag', null);
		handleError(err, {
			currentView,
			component,
			info,
		});
	};
	Vue.use(Vuex);
	const store = new Vuex.Store(initializeStore(settings));
	// we create an HTML element to anchor our shadow-dom implementation
	// this allows us to inject into the page in a sandboxed manner
	// where we can avoid collisions with merchant styling
	// it also makes it harder for competitors to detect the existence of
	// our extension
	// See: https://developers.google.com/web/fundamentals/getting-started/primers/shadowdom
	const wrappingDiv = document.createElement('div');
	wrappingDiv.style.all = 'initial';

	// This id is used by `handleError()` function in utils
	// Be sure to update it as well in case you update it here
	wrappingDiv.id = '__genieContainer';

	// we'll try to append to the html outside of body for ordering purposes vs. eBates
	document.documentElement.appendChild(wrappingDiv);
	let containingEl = wrappingDiv;
	if (wrappingDiv.attachShadow) {
		containingEl = wrappingDiv.attachShadow({ mode: 'open' });
	}

	// inject a reference to our stylesheet so that we can
	// use those encapsulated styles in the shadowDOM fragment
	appendStyleSheet(containingEl, 'inject');

	// we need to create this because Vue will *replace*
	// the element given to the constructor; we do not want to wipe out the shadow root
	// by giving Vue ot or its parent
	const wrappingDiv2 = document.createElement('div');
	wrappingDiv2.id = 'rmnGenieWrappingDiv';
	wrappingDiv2.style.all = 'initial';
	containingEl.appendChild(wrappingDiv2);

	Vue.use(OwenAnalyticsPlugin, { store });

	const RMNGenieApp = new Vue({
		el: wrappingDiv2,
		store,
		components: { ...Components, LoadingSpinner },
		data() {
			return {
				setupTime: Date.now(),
				closedWhileTesting: false,
				merchantsViewOnly: settings.merchantsViewOnly,
				additionalCallout: (settings.merchant || {}).additionalCallout,
				checkingTimeout: null,
				nextCheckingImage: checkingIconGenerator(),
			};

			function* checkingIconGenerator() {
				let ctr = 1;
				while (true) {
					yield `checking-${ctr}`;
					ctr = ctr === 8 ? 1 : ctr + 1;
				}
			}
		},
		computed: {
			...mapState('display', ['currentView']),
			...mapGetters('display', ['badgeCount', 'iconImage']),
		},
		watch: {
			badgeCount: {
				immediate: true,
				handler: function updateBadgeCount(newBadgeCount, oldBadgeCount) {
					if (newBadgeCount !== oldBadgeCount) backgroundMessaging.updateBadge(newBadgeCount);
				},
			},
			iconImage: {
				immediate: true,
				handler: function updateIcon(newIconImage, oldIconImage) {
					if (newIconImage !== oldIconImage) {
						if (newIconImage === 'checking') {
							this.checkingSpinner();
						} else {
							clearTimeout(this.checkingTimeout);
							backgroundMessaging.updateIcon(newIconImage);
						}
					}
				},
			},
		},
		methods: {
			checkingSpinner() {
				backgroundMessaging.updateIcon(this.nextCheckingImage.next().value);
				this.checkingTimeout = setTimeout(this.checkingSpinner, 50);
			},
		},
		/**
		 * The template below utilizes the reserved `<component>` element
		 * to allow us to choose the view that should be presented dynamically
		 * You can see more details about this here:
		 * https://vuejs.org/v2/guide/components.html#Dynamic-Components
		 * The overlay component is always rendered and hidden/shown
		 * based on state logic from our event handlers.
		 */
		template: `<div id="rmnGenieWrappingDiv" v-show="currentView !== 'closed'">
			<Overlay />
			<LoadingSpinner style="display: none !important;" />
			<component
				v-bind:is="currentView"
				:merchants-view-only="merchantsViewOnly"
				:additional-callout="additionalCallout"
			/>
		</div>`,
	});
	return RMNGenieApp;
}
