<style lang="scss" scoped>
.sidecar-continue-button {
	margin-top: 0;
}
</style>

<template>
	<div>
		<div
			:class="QA_CLASSES.results"
			:data-code-result="couponResult.discount"
			:data-free-shipping-result="couponResult.freeShipping"
			:data-free-gifts-result="couponResult.freeGifts"
			:data-cbo-result="cashbackAvailable"
			:data-cbo-activated="isCboActivated ? 'true' : 'false'"
		>
			<CloseIcon :prompt-name="promptName" :on-dark-bg="onDarkBg" />
			<div :class="[savingsClasses, { 'show-sidecar': showSidecar }]">
				<ResultsMessage :additional-callout="additionalCallout" :no-savings-available="noSavingsAvailable" />
				<button
					v-if="!triggerSidecar"
					:class="QA_CLASSES.continue"
					class="button-text"
					@click="onContinueClick"
				>
					Continue to Checkout
				</button>
			</div>
			<div v-if="triggerSidecar" class="bbb-sidecar">
				<CboUi :prompt-prefix="'results'" />
				<button
					:class="QA_CLASSES.continue"
					class="button-text sidecar-continue-button"
					@click="onContinueClick"
				>
					{{ continueButtonText }}
				</button>
			</div>
			<div v-else-if="showReviewUs" class="bbb-sidecar">
				<ReviewUs />
			</div>
		</div>
		<WarningMessage
			v-if="needShowWarning && needShowWarningTrigger"
			:is-rounded="true"
			:is-indent-from-bottom="true"
			:if-trigger-sidecar="triggerSidecar || showReviewUs"
			:close="closeWarningHandler"
		>
			{{ warningMessageText }}
		</WarningMessage>
	</div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import { CboUi, CloseIcon, ResultsMessage, ReviewUs, WarningMessage } from './partials';
import { CBO_PROMPT_NAMES, STORAGE_KEYS_SUFFIXES } from '../consts';
import { COUPON_ACTIONS, DISPLAY_ACTIONS } from '../store/types';
import backgroundMessaging from '../lib/background-messaging';

export default {
	components: {
		CboUi,
		CloseIcon,
		ResultsMessage,
		WarningMessage,
		ReviewUs,
	},
	props: {
		additionalCallout: {
			type: Object,
			default: () => ({
				message: '',
				showAlways: false,
				showWhenCodeWasFound: false,
			}),
		},
	},
	data() {
		return {
			needShowWarningTrigger: false,
			showSidecar: false,
			CBO_PROMPT_NAMES,
			QA_CLASSES: Object.freeze({
				continue: 'qa-go-to-checkout',
				results: 'qa-results-container',
			}),
		};
	},
	computed: {
		...mapState('cbo', { cboUuid: 'uuid' }),
		...mapState('coupon', { couponResult: 'bestResult', orderSubtotal: 'preTaxShippingPrice' }),
		...mapState('display', [
			'currentView',
			'enableDifferentDiscountMessage',
			'shouldShowReviewUs',
			'showCboLimitationWarning',
		]),
		...mapState(['merchant']),
		...mapGetters('cbo', {
			bestDiscount: 'bestDiscount',
			cboStatus: 'uiStatus',
			bestDiscountForTotal: 'bestDiscountForTotal',
			isCboActivated: 'isActivated',
		}),
		...mapGetters('coupon', ['validCodeFound']),
		cashbackAvailable() {
			// The CBO shouldn't be displayed in case subtotal = 0 or driver failed to parse total due to errors
			const { price } = this.couponResult;
			if (this.orderSubtotal === 0 || price === Number.MAX_SAFE_INTEGER) return false;
			const { amountOff, percentOff } = this.bestDiscountForTotal(this.orderSubtotal || price);
			return amountOff || percentOff;
		},
		triggerSidecar() {
			return this.cboUuid && this.cashbackAvailable && this.cboStatus !== CBO_PROMPT_NAMES.DEACTIVATED;
		},
		noSavingsAvailable() {
			return !this.cashbackAvailable && !this.validCodeFound;
		},
		onDarkBg() {
			return !this.showSidecar && !this.noSavingsAvailable;
		},
		savingsClasses() {
			return ['bbb-gray-blob', this.noSavingsAvailable ? 'bbb-container-light' : 'bbb-container-dark'];
		},
		promptName() {
			return this.validCodeFound || (this.cashbackAvailable && this.isCboActivated)
				? 'deal success'
				: 'no savings found';
		},
		continueButtonText() {
			let text = 'Continue to Checkout';
			if (
				this.cboStatus === CBO_PROMPT_NAMES.CAN_ACTIVATE ||
				this.cboStatus === CBO_PROMPT_NAMES.UNRECOGNIZED_ERROR ||
				this.cboStatus === CBO_PROMPT_NAMES.NOT_LOGGED_IN
			) {
				text = `No Thanks, ${text}`;
			}
			return text;
		},
		showReviewUs() {
			return (
				(this.couponResult.discount || this.couponResult.freeGifts || this.couponResult.freeShipping) &&
				this.shouldShowReviewUs
			);
		},
		showDifferentDiscountWarning() {
			return this.couponResult.discount && this.enableDifferentDiscountMessage;
		},
		needShowWarning() {
			return this.showCboLimitationWarning || this.showDifferentDiscountWarning;
		},
		warningMessageText() {
			let message;
			if (this.showCboLimitationWarning) {
				message = `${this.merchant.domain} offers a maximum CBO reward of $50.`;
			} else if (this.showDifferentDiscountWarning) {
				message = 'Final savings in cart may not include taxes and fees';
			}
			return message;
		},
	},
	watch: {
		showDifferentDiscountWarning: {
			immediate: true,
			handler(needShowDifferentDiscountMessage) {
				if (needShowDifferentDiscountMessage) this.markDifferentDiscountMessageDisplayed();
			},
		},
		showCboLimitationWarning: {
			immediate: true,
			handler(needShowCboLimitationMessage) {
				if (needShowCboLimitationMessage) this.markCboLimitationMessageDisplayed();
			},
		},
	},
	mounted() {
		if (this.triggerSidecar || this.showReviewUs) {
			setTimeout(() => {
				this.showSidecar = true;
			}, 100);
		}
		setTimeout(
			() => {
				this.needShowWarningTrigger = true;
			},
			this.triggerSidecar ? 1000 : 0,
		);

		this.$fireViewPrompt(this.promptName);
		// mute CBO prompt if user has complete code testing
		if (this.cboUuid) {
			backgroundMessaging.markCboPromptDisplayed(this.merchant.domain, Date.now());
		}
	},
	destroyed() {
		if (this.currentView !== 'backgroundRelaunch') {
			this.advanceToCheckout();
		}
	},
	methods: {
		...mapActions('coupon', { advanceToCheckout: COUPON_ACTIONS.ADVANCE_TO_CHECKOUT }),
		...mapActions('display', {
			closeExtension: DISPLAY_ACTIONS.CLOSE_EXTENSION,
			closeCboLimitationWarning: DISPLAY_ACTIONS.CLOSE_CBO_LIMITATION_WARNING,
			closeDifferentDiscountWarning: DISPLAY_ACTIONS.CLOSE_DIFFERENT_DISCOUNT_WARNING,
		}),
		onContinueClick() {
			this.$fireClickPrompt(this.promptName, 'continue to checkout');
			this.closeExtension();
		},
		closeWarningHandler() {
			if (this.showCboLimitationWarning) {
				this.closeCboLimitationWarning();
			}
			if (this.showDifferentDiscountWarning) {
				this.closeDifferentDiscountWarning();
			}
		},
		markDifferentDiscountMessageDisplayed() {
			backgroundMessaging.setDriverStorage(
				`${this.merchant.domain}${STORAGE_KEYS_SUFFIXES.DIFFERENT_DISCOUNT}`,
				true,
			);
		},
		markCboLimitationMessageDisplayed() {
			backgroundMessaging.setDriverStorage(
				`${this.merchant.domain}${STORAGE_KEYS_SUFFIXES.SBO_LIMITATION}`,
				true,
			);
		},
	},
};
</script>
