import bus from '../bus';
import handleDriverError from './handle-driver-error';

/**
 * @typedef CustomOptions
 * @type {object}
 * @property {Number} [actionInterval = null] setIinterval that will be cleared after expiring session
 * @property {Number} [timeout = 15] session timeout (minutes).
 * @property {Number} [checkDOMTime = 60] a period of the time which shows how often checkFunction will be checked (seconds).
 * @property {Boolean} [toCheckDom = false] this flag shows if DOM will be checked default without check
 * @property {String} [checkElement = 'body'] selector of the DOM-element that must be under control
 * @property {function} [checkFunction = () => ''] additional function for checking page state. default - without additional contro)
 * @property {function} [finishFunction = () => bus.$emit('cart-inactive')] which function will be executed after expiring session.
 * 	default - turn off running
/
/**
 * @description This function waits for expiring session. Attention!!! Before calling this function check if the right page is opened (for
 * non-default element).
 * @function sessionTimer
 * @param {CustomOptions} [customOptions] All keys are optional
 */
// we can't check document.documentElement, because on Firefox the extension is a part of this element. A timer would be resetted every time
// when a user makes manipulations with extension.
function initialize(customOptions) {
	const defaultOptions = {
		actionInterval: null,
		timeout: 15,
		checkDOMTime: 60, // this parameter is actual for checkFunction. Cheking is done every checkDOMTime
		toCheckDOM: false,
		checkElement: 'body',
		checkFunction: () => null,
		finishFunction: () => bus.$emit('cart-inactive'),
	};
	const options = {
		...defaultOptions,
		...customOptions,
	};
	let observer = null;
	let startTime = Date.now();
	let checkFuncTimer = Date.now();
	let previousAdditionalState = null;
	// A function for creating observer
	const createObserver = elementToObserve => {
		const observerOptions = {
			childList: true,
			attributes: true,
			subtree: true,
		};
		const callback = () => {
			startTime = Date.now();
		};
		const creatingObserver = new MutationObserver(callback);
		creatingObserver.observe(elementToObserve, observerOptions);
		return creatingObserver;
	};
	try {
		previousAdditionalState = options.checkFunction();
	} catch (e) {
		// sending an error to sentry
		handleDriverError(e, { source: '[sessionTimer]checkFunction' });
		return;
	}
	const sessionInterval = setInterval(() => {
		const currentTime = Date.now();
		const observeElement = document.querySelector(options.checkElement);
		// Observer is created once when observing element is on the page
		if (options.toCheckDOM && !!observeElement && !observer) {
			observer = createObserver(observeElement);
		}
		if (currentTime - checkFuncTimer > options.checkDOMTime * 1000) {
			checkFuncTimer = currentTime;
			let currentAdditionalState = null;
			try {
				currentAdditionalState = options.checkFunction();
				// if something has changed according to the additional conditional function
				if (previousAdditionalState !== currentAdditionalState) {
					startTime = currentTime;
				}
				previousAdditionalState = currentAdditionalState;
			} catch (e) {
				// sending an error to sentry
				handleDriverError(e, { source: '[sessionTimer]checkFunction' });
				if (observer) {
					observer.disconnect();
				}
				clearInterval(sessionInterval);
			}
		}

		// in case of timeout
		if (currentTime - startTime >= options.timeout * 1000 * 60) {
			clearInterval(sessionInterval);
			if (options.toCheckDOM && !!observer) {
				observer.disconnect();
			}
			if (options.actionInterval) {
				clearInterval(options.actionInterval);
			}
			try {
				options.finishFunction();
			} catch (e) {
				// sending an error to sentry
				handleDriverError(e, { source: '[sessionTimer]finishFunction' });
			}
		}
	}, options.checkDOMTime * 1000);
}

export default {
	initialize,
};
