import handleDriverError from './handle-driver-error';

const _driverDomainToGroupMap = new Map(
	// eslint-disable-next-line global-require
	JSON.parse(JSON.stringify(require('../drivers/drivers-information.json'))).map(driver => [
		driver.domain,
		driver.group,
	]),
);

const _driverCache = new Map();

export default {
	async cacheDriversByGroup(groupName) {
		const drivers = (await import(/* webpackChunkName: "drivers__[request]" */
		/* webpackInclude: /(1-off-drivers|atgCommerce|basketdo|bonton|champion|chicos|demandware(AJAX)?|famousfootwear|footlocker|gap|halston|hp|lanebryant|m-flow|magento|progifts|saks|sears|shopify|toms|websphere)\/index\.js$/ */
		`../drivers/${groupName}`)).default;
		_driverCache.set(groupName, new Map(drivers.map(driver => [driver.domain, driver])));
	},
	hasDriverForDomain(domain) {
		return _driverDomainToGroupMap.has(domain);
	},
	/**
	 * @function getDriverForDomain
	 * @member driverCache
	 * @description the merchant driver returned here may not end up getting used
	 * depending on other criteria, for example, whether the user is on a blacklisted subdomain
	 * @param {String} domain the normalized domain for a given page
	 * @return {Object} the driver for the corresponding domain
	 */
	async getDriverForDomain(domain) {
		try {
			if (!_driverDomainToGroupMap.has(domain)) {
				return null;
			}
			const driverGroup = _driverDomainToGroupMap.get(domain);
			if (!_driverCache.has(driverGroup)) {
				await this.cacheDriversByGroup(driverGroup);
			}
			return _driverCache.get(driverGroup).get(domain);
		} catch (e) {
			handleDriverError(e);
			return null;
		}
	},
};
