/**
 * This is an object of constants that maps all possible CBO activation states to the
 * human-readable prompt name of that state (generally used in analytics).
 *
 * The general form is:
 *
 *      NAME_OF_STATE: 'prompt name'
 *
 * and it might be used like so:
 *
 *      if (this.promptName == CBO_PROMPT_NAMES.CAN_ACTIVATE) { ... }
 *
 * More details about each can be found below.
 */

export default Object.freeze({
	// The user should be able to press an 'activate' button to attempt to activate a CBO
	CAN_ACTIVATE: 'activate cashback',

	// The user has successfully activated a CBO
	ACTIVATED: 'cashback activated',

	// When we have to turn off (deactivate) a CBO that a user already activated,
	// typically because of stand-down requirements in our Affiliate network.
	DEACTIVATED: 'cashback deactivated',

	// An error response to an activation request that isn't one of the following reasons
	UNRECOGNIZED_ERROR: 'unrecognized error',

	// The response to an activation request when the CBO has expired
	EXPIRED: 'cashback expired',

	// When the user attempts to activate a CBO and hasn't signed in yet
	NOT_LOGGED_IN: 'cashback login',

	// When a user attempts to activate with an account that hasn't been verifed
	// (by clicking a link in their email)
	NOT_VERIFIED: 'cashback verify',

	LOADING: 'loading',
});
