<style lang="scss" scoped>
@import '../styles/vars/colors';

.genie-survey-wrapper {
	align-items: center;
	display: flex;
	justify-content: space-between;
	margin: 5px 0 40px;
	position: relative;
	width: 300px;

	hr {
		border-color: $bbb-gray-light;
		margin: 0 auto;
		padding: 0;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 99%;
	}

	&:before,
	&:after {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
	}

	&:before {
		content: '0';
		right: calc(100% + 1em);
	}

	&:after {
		content: '10';
		left: calc(100% + 1em);
	}

	input[type='radio'] {
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;

		display: block;
		margin: 0;
		padding: 0;

		$size: 0.4em;
		$border-size: 3px;

		width: $size;
		height: $size;
		border: $border-size solid transparent;
		border-radius: 999px;
		background-color: $bbb-gray-light;
		font-size: 15px;
		box-sizing: content-box;
		background-clip: content-box;

		position: relative;
		text-align: center;

		&:before {
			content: '';
			border: 7px solid transparent;
			border-bottom-color: $bbb-blue;
			bottom: -1em;
		}

		&:after {
			content: attr(value);
			background-color: $bbb-blue;
			border-radius: 3px;
			bottom: -2.5em;
			color: white;
			padding: 5px;
			min-width: 25px;
			box-sizing: border-box;
		}

		&:before,
		&:after {
			left: 50%;
			transform: translateX(-50%);
			opacity: 0;
			position: absolute;
		}

		&:checked:after,
		&:checked:before {
			opacity: 1;
		}

		&:focus {
			outline: none;
		}

		&:checked {
			border-color: $bbb-blue;
			background-color: $bbb-blue;
			background-clip: border-box;
		}

		&:first-of-type {
			margin-left: -$border-size;
		}

		&:last-of-type {
			margin-right: -$border-size;
		}
	}
}
</style>

<template>
	<form class="genie-survey-wrapper">
		<hr />

		<input
			v-for="n in 10"
			:key="n"
			v-model="valueContainer[valueKey]"
			type="radio"
			:name="valueKey"
			:value="n"
			@mouseenter="dragHandler"
		/>
	</form>
</template>

<script>
import { requireProps } from '../../utils';

export default {
	props: requireProps({
		valueContainer: Object,
		valueKey: String,
	}),
	methods: {
		dragHandler(evt) {
			if (evt.buttons === 1) {
				this.valueContainer[this.valueKey] = evt.target.value;
				evt.target.focus();
			}
		},
	},
};
</script>
