import semverUtils from './semver';

export default {
	/**
	 * @function isEnabled
	 * @description compares two semantic versions
	 * @param {Object} merchantSettings the object describing merchant configs and content
	 * @param {String} minimumRequiredVersion the minimum semver (http://semver.org/) of
	 * the extension that the driver can run on
	 * @return {Boolean} is the driver enabled on this version of the extension
	 */
	isEnabled(merchantSettings, minimumRequiredVersion) {
		if (!merchantSettings || merchantSettings.disableBrowserExtensionDriver) {
			return false;
		}

		const MINIMUM_DRIVER_VERSION = merchantSettings.browserExtensionMinimumDriverVersion;

		// If there's no minimum version defined in settings, then we default
		// driver to enabled
		if (!MINIMUM_DRIVER_VERSION) {
			return true;
		}
		// If browserExtensionMinimumDriverVersion
		return semverUtils.compare(minimumRequiredVersion, MINIMUM_DRIVER_VERSION) >= 0;
	},
};
