<style lang="scss" scoped>
$animation-duration: 1000ms;
$transition-iterations: 100; // arbitrary large number
$anim-box-shadow-alpha: 0.24;

@keyframes spin {
	to {
		transform: rotate(1turn);
	}
}

@keyframes glow {
	to {
		box-shadow: 0 0 23px 0 rgba(245, 190, 68, $anim-box-shadow-alpha * 2.5);
	}
}

.bbb-loading-spinner {
	--box-shadow-glow-duration: #{$animation-duration / 2};

	-moz-transition: -moz-transform ($animation-duration * $transition-iterations) linear,
		box-shadow ($animation-duration / 2) linear;
	animation: spin $animation-duration linear infinite, glow ($animation-duration / 2) linear infinite alternate;
	background: transparent url('../images/loading-ring.png') center no-repeat;
	border-radius: 999px;
	box-shadow: 0 0 23px 0 rgba(245, 190, 68, $anim-box-shadow-alpha);
	width: 54px;
	height: 54px;
	overflow: hidden;
	position: relative;
	margin-top: 10px;

	&.transition-spin {
		// for browsers without Shadow DOM (*cough* Firefox *cough*) we cannot animate properties marked with !important, so use Transitions instead
		-moz-transform: rotate(1turn * $transition-iterations);
		animation: none;
	}

	&.transition-box-shadow {
		// for browsers without Shadow DOM (*cough* Firefox *cough*) we cannot animate properties marked with !important, so use Transitions instead
		box-shadow: 0 0 23px 0 rgba(245, 190, 68, $anim-box-shadow-alpha * 2.5);
	}
}
</style>
<template>
	<div class="bbb-loading-spinner" :class="classes"></div>
</template>
<script>
import { isFirefox } from '../../../utils/browser';

export default {
	data: () => ({
		classes: { 'transition-box-shadow': false, 'transition-spin': false },
		glowInterval: -1,
	}),
	mounted() {
		if (isFirefox()) {
			this.classes['transition-spin'] = true;

			const boxShadowGlowDuration = getComputedStyle(this.$el).getPropertyValue('--box-shadow-glow-duration');

			this.glowInterval = setInterval(() => {
				this.classes['transition-box-shadow'] = !this.classes['transition-box-shadow'];
			}, parseInt(boxShadowGlowDuration, 10));
		}
	},
	beforeDestroy() {
		clearInterval(this.glowInterval);
	},
};
</script>
