/**
 * This function is a substitute for the browser's 'fetch()' method.  In Firefox versions lower than 57,
 * the browser's 'fetch()' method sets the ORIGIN header to 'null', which interferes with the proper
 * functioning of certain sites.  This utility method is intended as a replacement for 'fetch()' in
 * those cases.  (See target.com for example usage.)
 *
 * @function sendXHR
 * @param {string} requestURL The URL you want to make an XMLHttpRequest to
 * @param {Object} args A config object for the XMLHttpRequest.  Config keys are a subset of the 'fetch()' arguments.
 * @param {string} [args.method] The HTTP request method (or verb - e.g. 'GET', 'POST')
 * @param {string} [args.credentials] When to send user credentials with the XHR. Must be one of 'omit', 'same-origin', or 'include'
 * @param {string} args.body The Request body
 * @param {Object[]} args.headers An array of headers to send with the XHR
 * @returns {Promise<Response>} A Promise that resolves to the Response from the attempted XHR
 */
export default (requestURL, args) => {
	const method = args.method || 'GET';
	const hasCredentials = args.credentials === 'include';
	const { headers = [], body = null } = args;

	return new Promise(resolve => {
		const request = new XMLHttpRequest();
		request.withCredentials = hasCredentials;
		request.open(method, requestURL);
		Object.entries(headers).forEach(([headerName, headerValue]) => {
			request.setRequestHeader(headerName, headerValue);
		});
		// we purposefully do NOT set x-original-user-agent headers here
		// as this is only used to communicate with merchant websites
		// so we do not need or want that tracing information
		request.onreadystatechange = () => {
			if (request.readyState === XMLHttpRequest.DONE) {
				resolve({
					get status() {
						return request.status;
					},
					headers() {
						const emptyHeadersObject = {
							get(param) {
								return this[param] ? this[param] : null;
							},
						};
						try {
							const headersString = request.getAllResponseHeaders();
							const headersArray = headersString.split('\n');
							return headersArray.reduce((headersObject, current) => {
								const arr = current.split(':');
								headersObject[arr[0].trim()] = arr[1].trim();
								return headersObject;
							}, emptyHeadersObject);
						} catch (e) {
							return emptyHeadersObject;
						}
					},
					json() {
						return JSON.parse(request.responseText);
					},
					text() {
						return request.responseText;
					},
				});
			}
		};
		request.send(body);
	});
};
