import adapters from './requestAdapters';

const defaults = {
	type: 'fetch',
};

class GenieRequest {
	constructor({ type }) {
		this.adapter = adapters[type];

		this.defaultHeaders = {};
		this.mergeHeaders = false;
	}

	get(url, options = {}) {
		if (Object.keys(this.defaultHeaders).length) {
			Object.assign(options, { headers: this._prepareHeaders(options.headers) });
		}

		const adapter = this._getAdapter(options);
		return adapter({
			url,
			method: 'GET',
			...options,
		});
	}

	head(url, options = {}) {
		if (Object.keys(this.defaultHeaders).length) {
			Object.assign(options, { headers: this._prepareHeaders(options.headers) });
		}

		const adapter = this._getAdapter(options);
		return adapter({
			url,
			method: 'HEAD',
			...options,
		});
	}

	options(url, options = {}) {
		if (Object.keys(this.defaultHeaders).length) {
			Object.assign(options, { headers: this._prepareHeaders(options.headers) });
		}

		const adapter = this._getAdapter(options);
		return adapter({
			url,
			method: 'OPTIONS',
			...options,
		});
	}

	post(url, body = null, options = {}) {
		if (Object.keys(this.defaultHeaders).length) {
			Object.assign(options, { headers: this._prepareHeaders(options.headers) });
		}

		const adapter = this._getAdapter(options);
		return adapter({
			url,
			body,
			method: 'POST',
			...options,
		});
	}

	put(url, body = null, options = {}) {
		if (Object.keys(this.defaultHeaders).length) {
			Object.assign(options, { headers: this._prepareHeaders(options.headers) });
		}

		const adapter = this._getAdapter(options);
		return adapter({
			url,
			body,
			method: 'PUT',
			...options,
		});
	}

	patch(url, body = null, options = {}) {
		if (Object.keys(this.defaultHeaders).length) {
			Object.assign(options, { headers: this._prepareHeaders(options.headers) });
		}

		const adapter = this._getAdapter(options);
		return adapter({
			url,
			body,
			method: 'PATCH',
			...options,
		});
	}

	delete(url, options = {}) {
		if (Object.keys(this.defaultHeaders).length) {
			Object.assign(options, { headers: this._prepareHeaders(options.headers) });
		}

		const adapter = this._getAdapter(options);
		return adapter({
			url,
			method: 'DELETE',
			...options,
		});
	}

	setType(type) {
		this.adapter = adapters[type] || this.adapter;
		return Boolean(adapters[type]);
	}

	setDefaultHeaders(headers, allowMerge = false) {
		if (typeof headers !== 'object' || typeof allowMerge !== 'boolean') {
			throw new Error('You need to pass valid values into setDefaultHeaders method!');
		}

		this.defaultHeaders = headers;
		this.mergeHeaders = allowMerge;

		return true;
	}

	_prepareHeaders(headers) {
		if (!headers) {
			return this.defaultHeaders;
		}
		if (!this.mergeHeaders) {
			return headers;
		}
		return Object.assign(this.defaultHeaders, headers);
	}

	_getAdapter(options) {
		if (options.type) {
			return adapters[options.type] || this.adapter;
		}
		return this.adapter;
	}
}

const request = new GenieRequest(defaults);

export default request;
