<style lang="scss" scoped>
.cbo-ui {
	max-height: 500px;
	transition: max-height 200ms, margin 200ms;
	.primary-text {
		white-space: nowrap;
	}
}

.bbb-sidecar .cbo-ui {
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.flatten {
	max-height: 0;
	margin: 0;
}

// adds "Cash Back" no wrap text to header
.add-cb-text .primary-text:after {
	content: ' Cash Back';
	white-space: nowrap;
}
</style>

<template>
	<div class="cbo-ui" :class="{ flatten: hideCboPrompt }">
		<!-- SIDECAR VIEW for ACTIVATE/LOADING/ACTIVATED -->
		<div
			v-if="
				isOnSidecar &&
					(uiStatus === CBO_PROMPT_NAMES.CAN_ACTIVATE ||
						uiStatus === CBO_PROMPT_NAMES.LOADING ||
						uiStatus === CBO_PROMPT_NAMES.ACTIVATED)
			"
			class="add-cb-text"
		>
			<span class="primary-text" :class="QA_CLASSES.cboDiscountUnactivated">{{ ctaTitle }}</span>
			<p class="secondary-text">
				<EllipsedText :lines="2">
					{{ cboInfo }}
				</EllipsedText>
				<a :href="cashbackUrl" target="_blank">See Details</a>
			</p>
			<CboCta />
		</div>

		<!-- ***** LIST VIEWS & RESULTS VIEWS ***** -->

		<!-- ACTIVATE -->
		<div
			v-else-if="!isOnSidecar && uiStatus === CBO_PROMPT_NAMES.CAN_ACTIVATE"
			class="bbb-container-light bbb-color-blob add-cb-text"
			:class="{ 'bbb-no-logo': isOnListView }"
		>
			<span class="primary-text">{{ ctaTitle }} </span>
			<p class="secondary-text">
				<EllipsedText :lines="2">
					{{ cboInfo }}
				</EllipsedText>
				<a :href="cashbackUrl" target="_blank">See Details</a>
			</p>
			<CboCta :hide-cbo="hideCbo" />
		</div>

		<!-- LOADING -->
		<div
			v-else-if="!isOnSidecar && uiStatus === CBO_PROMPT_NAMES.LOADING"
			class="bbb-container-dark justify-center"
			:class="{ 'bbb-no-logo': isOnListView }"
		>
			<span class="primary-text">Activating Cash Back</span>
			<p class="secondary-text">
				<EllipsedText :lines="2">
					{{ cboLoadingText }}
				</EllipsedText>
			</p>
			<CboCta :hide-cbo="hideCbo" />
		</div>

		<!-- ACTIVATED -->
		<div
			v-else-if="!isOnSidecar && uiStatus === CBO_PROMPT_NAMES.ACTIVATED"
			class="bbb-container-dark bbb-gray-blob"
			:class="[{ 'bbb-no-logo': isOnListView }, QA_CLASSES.cboActivated]"
		>
			<span class="primary-text">Activated!</span>
			<p class="secondary-text">
				<EllipsedText :lines="2">
					{{ cboActivatedText }}
				</EllipsedText>
			</p>
			<a :href="cashbackUrl" class="button-text" target="_blank">See Details</a>
		</div>

		<!-- ***** IMPASSE VIEWS FOR SIDECAR, LIST VIEW & RESULTS VIEW ***** -->

		<!-- NOT LOGGED IN -->
		<div v-else-if="uiStatus === CBO_PROMPT_NAMES.NOT_LOGGED_IN" class="justify-center" :class="impasseClasses">
			<span class="primary-text">Get Your Reward</span>
			<p class="secondary-text">
				Log in or sign up so we can deliver the reward to your RetailMeNot account. We promise it’s quick!
			</p>
			<CboCta :hide-cbo="hideCbo" />
		</div>

		<!-- NOT VERIFIED -->
		<div
			v-else-if="uiStatus === CBO_PROMPT_NAMES.NOT_VERIFIED"
			class="justify-center sidecar-padding"
			:class="impasseClasses"
		>
			<span class="primary-text">Verify Your Email</span>
			<p class="secondary-text">We'll deliver your reward to the account associated with your email.</p>
			<CboCta :hide-cbo="hideCbo" />
		</div>

		<!-- EXPIRED -->
		<div v-else-if="uiStatus === CBO_PROMPT_NAMES.EXPIRED" :class="impasseClasses">
			<span class="primary-text no-wrap">This offer has expired</span>
			<p class="secondary-text">However, Genie can still help you find savings at checkout!</p>
			<button v-if="isOnListView" class="button-text" @click="hideCbo">Dismiss</button>
		</div>

		<!-- DEACTIVATED -->
		<div v-else-if="uiStatus === CBO_PROMPT_NAMES.DEACTIVATED" :class="impasseClasses">
			<span class="primary-text">Get the magic back</span>
			<p class="secondary-text">
				You can only use one savings tool at a time, so this deal is no longer activated.
			</p>
		</div>

		<!-- ERROR -->
		<div v-else-if="uiStatus === CBO_PROMPT_NAMES.UNRECOGNIZED_ERROR" :class="impasseClasses">
			<span class="primary-text">Genie's Sincerest Apologies!</span>
			<p class="secondary-text">There was an error activating this Cash Back offer.</p>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import CboCta from './cbo-cta';
import { CBO_PROMPT_NAMES } from '../../consts';
import config from '../../../config';
import backgroundMessaging from '../../lib/background-messaging';
import EllipsedText from './ellipsed-text';

export default {
	components: {
		CboCta,
		EllipsedText,
	},
	props: {
		promptPrefix: {
			type: String,
			default: null,
		},
	},
	data() {
		return {
			hideCboPrompt: false,
			CBO_PROMPT_NAMES,
			QA_CLASSES: Object.freeze({
				cboActivated: 'qa-cbo-activated',
				cboDiscountUnactivated: 'qa-cbo-amount-unactivated',
			}),
		};
	},
	computed: {
		...mapState(['merchant']),
		...mapState('cbo', ['uuid', 'title']),
		...mapState('display', ['currentView']),
		...mapGetters('cbo', ['uiStatus', 'ctaTitle', 'bestDiscount', 'isPercentBased']),
		cashbackUrl() {
			// Navigate user to activeted CBO details when it is already activated
			if (this.uiStatus === CBO_PROMPT_NAMES.ACTIVATED) {
				return `https://www.${config.site.baseDomain}/cashback/${this.uuid}`;
			}
			return `https://www.${config.site.baseDomain}/cashback/2/${this.uuid}`;
		},
		cboInfo() {
			const cboInfoTitle =
				this.uiStatus === CBO_PROMPT_NAMES.ACTIVATED
					? "You're now earning rewards!"
					: 'Just activate to earn rewards.';
			return this.isPercentBased ? cboInfoTitle : this.title;
		},
		isOnListView() {
			return this.currentView === 'offersList';
		},
		isOnSidecar() {
			return this.currentView === 'results';
		},
		impasseClasses() {
			if (this.isOnListView) {
				return 'bbb-container-gray bbb-no-logo';
			}
			if (this.isOnSidecar) {
				return '';
			}
			return 'bbb-container-light bbb-gray-blob';
		},
		cboActivatedText() {
			if (this.isPercentBased) {
				return `Enjoy ${this.title.toLowerCase()}`;
			}
			if (this.bestDiscount.minimumPurchase === 0) {
				return "You're all set to earn Cash Back!";
			}
			return `Shop and spend at least $${this.bestDiscount.minimumPurchase} to qualify`;
		},
		cboLoadingText() {
			if (this.isPercentBased) {
				return this.title.toLowerCase();
			}
			if (this.bestDiscount.minimumPurchase === 0) {
				return '';
			}
			return `$${this.bestDiscount.amountOff} back on $${this.bestDiscount.minimumPurchase}+ purchase`;
		},
	},
	watch: {
		uiStatus: {
			immediate: true,
			handler: function updateCboInfo(newUiStatus) {
				if (newUiStatus !== CBO_PROMPT_NAMES.LOADING) {
					// switch to a blacklist array if we need to check for more than LOADING
					let promptName = newUiStatus;
					if (this.promptPrefix) {
						promptName = `${this.promptPrefix} - ${promptName}`;
					}
					this.$fireViewPrompt(promptName);
				}
			},
		},
	},
	mounted() {
		if (this.isOnSidecar) {
			backgroundMessaging.markCboPromptDisplayed(this.merchant.domain, Date.now());
		}
	},
	methods: {
		hideCbo() {
			this.hideCboPrompt = true;
		},
	},
};
</script>
