export default /**
 * A helper function to force Vue to require the keys passed to 'props'.
 * @param {Object} props A plain object of {propNames:TYPES} that you want to make Vue require
 */
function requireProps(props) {
	const requiredProps = {};

	Object.entries(props).forEach(([name, type]) => {
		let requiredType = type;

		// If 'type' is already an object, assume original author set 'default' or 'required' as desired
		if (typeof type !== 'object') {
			requiredType = {
				type,
				required: true,
			};
		}

		requiredProps[name] = requiredType;
	});

	return requiredProps;
}
