<style lang="scss" scoped>
$duration: 400ms;

.first-message-modal {
	position: absolute;
	height: auto;
	transform: translateY(10px);

	:first-child {
		font-weight: bold;
	}
}

.slide-fade-enter-active,
.slide-fade-leave-active {
	transition: transform $duration, opacity $duration / 2 !important;
}

.slide-fade-enter-to,
.slide-fade-leave {
	opacity: 1;
}

.slide-fade-enter,
.slide-fade-leave-to {
	opacity: 0;
	transform: none;
}
</style>

<template>
	<transition name="slide-fade">
		<div v-if="showPrompt" class="bbb-container-dark bbb-clouds first-message-modal">
			<span class="secondary-text">New to cash back?</span>
			<p class="secondary-text">
				It's easy! Just activate an offer and shop. 15–45 days after your purchase, the cash back reward will be
				delivered to your RetailMeNot Wallet.
			</p>
			<button class="button-text" @click="closeModal">Got It</button>
		</div>
	</transition>
</template>

<script>
import { mapState } from 'vuex';
import { backgroundMessaging } from '../../lib';

export default {
	data() {
		return {
			showPrompt: false,
			promptName: 'welcome to genie in cbo prompt',
		};
	},
	computed: {
		...mapState('display', ['shouldShowCboInfoPrompt']),
	},
	mounted() {
		this.showPrompt = this.shouldShowCboInfoPrompt;
		if (this.shouldShowCboInfoPrompt) {
			backgroundMessaging.markCboInfoPromptSeen();
			this.$fireViewPrompt(this.promptName);
		}
	},
	methods: {
		closeModal() {
			this.showPrompt = false;
			this.fireClickAnalytics('Got It');
		},
		fireClickAnalytics(eventTargetString) {
			this.$fireClickPrompt(this.promptName, eventTargetString.toLowerCase());
		},
	},
};
</script>
