import owenAnalytics from '../lib/owen-analytics';

export default {
	install(Vue, { store }) {
		if (!store) {
			throw new Error('OwenAnalyticsPlugin requires a Vuex store');
		}
		Vue.prototype.$fireClickPrompt = (...args) => {
			owenAnalytics.fireClickPrompt(store, ...args);
		};
		Vue.prototype.$fireViewPrompt = (...args) => {
			owenAnalytics.fireViewPrompt(store, ...args);
		};
		Vue.prototype.$fireTrackSurvey = (...args) => {
			owenAnalytics.fireTrackSurvey(store, ...args);
		};
		Vue.prototype.$fireCodeListImpressions = (...args) => {
			owenAnalytics.fireCodeListImpressions(store, ...args);
		};
		Vue.prototype.$fireSearch = (...args) => {
			owenAnalytics.fireSearch(store, ...args);
		};
	},
};
