import Vue from 'vue';
import { backgroundMessaging } from '../../lib';
import log from '../../../utils/log';
import { ACTIONS, MUTATIONS } from '../types/cbo';
import { ROOT_ACTIONS } from '../types';
import { CBO_STATES, CBO_PROMPT_NAMES, CBO_TYPES } from '../../consts';

// sorts from high to low
const sortAmount = (a, b) => b.amountOff - a.amountOff;
const sortPercent = (a, b) => b.percentOff - a.percentOff;

export const defaultState = {
	uuid: null,
	title: null,
	offerStatus: null,
	discounts: [],
};

export const getters = {
	bestDiscount: (state, getters) => {
		const sortFn = getters.isPercentBased ? sortPercent : sortAmount;
		return state.discounts.sort(sortFn)[0] || null;
	},
	uiStatus: (state, getters, rootState) => {
		if (state.offerStatus === CBO_STATES.OFFER_LOADING) {
			if (!rootState.userStatus.isLoggedIn) return CBO_PROMPT_NAMES.NOT_LOGGED_IN;
			if (!rootState.userStatus.isVerified) return CBO_PROMPT_NAMES.NOT_VERIFIED;
		}
		return state.offerStatus === CBO_STATES.OFFER_NOT_ACTIVATED ? CBO_PROMPT_NAMES.CAN_ACTIVATE : state.offerStatus;
	},
	isActivated: (state, getters) => getters.uiStatus === CBO_PROMPT_NAMES.ACTIVATED,
	isExpired: (state, getters) => getters.uiStatus === CBO_PROMPT_NAMES.EXPIRED,
	isPercentBased: state =>
		Array.isArray(state.discounts) &&
		state.discounts.length > 0 &&
		state.discounts[0].type === CBO_TYPES.PERCENT_OFF,
	isTiered: state => state.discounts && state.discounts.length > 1,
	ctaTitle: (state, getters) => {
		const { isTiered, bestDiscount, isPercentBased } = getters;
		if (!state.uuid || !bestDiscount) return '';
		const upTo = isTiered ? 'Up To ' : '';
		const discount = isPercentBased ? `${bestDiscount.percentOff}%` : `$${bestDiscount.amountOff}`;
		return `${upTo}${discount}`;
	},
	bestDiscountForTotal: (state, getters) => total => {
		const noDiscount = { amountOff: 0, percentOff: 0 };
		if (!state.uuid || state.offerStatus === CBO_STATES.OFFER_STOOD_DOWN) return noDiscount;

		if (getters.isPercentBased) {
			return getters.bestDiscount;
		}

		// Loops through all the discounts and does not early exit
		return state.discounts.reduce((bestDiscount, currentDiscount) => {
			if (total >= currentDiscount.minimumPurchase && currentDiscount.amountOff > bestDiscount.amountOff) {
				return currentDiscount;
			}
			return bestDiscount;
		}, noDiscount);
	},
};

export const mutations = {
	[MUTATIONS.UPDATE_OFFER](state, offer = { discounts: [] }) {
		state.uuid = offer.uuid;
		state.title = offer.title;
		state.offerStatus = offer.offerStatus;
		state.discounts = offer.discounts;
	},
	[MUTATIONS.SET_OFFER_STATUS](state, newStatus) {
		if (!Object.values(CBO_STATES).includes(newStatus)) {
			log('Cashback Offer status was not selected from "cbo-states.js".');
			if (BUILD_ENVIRONMENT !== 'production') {
				throw new TypeError(`Expected a valid CBO activation state, got "${newStatus}" instead.`);
			}
		}
		state.offerStatus = newStatus;
		log('CBO status set to: %o', newStatus);
	},
};

export const actions = {
	async [ACTIONS.ATTEMPT_TO_ACTIVATE]({ state, commit, dispatch, rootState, rootGetters }) {
		if (state.offerStatus !== CBO_STATES.OFFER_LOADING) {
			commit(MUTATIONS.SET_OFFER_STATUS, CBO_STATES.OFFER_LOADING);
			await Vue.nextTick();
		}
		if (!rootState.permissions.rmn) {
			await dispatch(ROOT_ACTIONS.REQUEST_USER_STATUS, null, { root: true });
		}
		if (!rootState.userStatus.isVerified) {
			// upon exiting early, the user is presented with the "please login" or "please verify" screen
			return;
		}
		const activatedCbo = await backgroundMessaging.activateCbo({
			cashbackOffer: state,
			viewInstanceUuid: rootState.display.viewInstanceUuid,
			pageName: window.location.hostname + window.location.pathname,
			pageType: rootGetters['display/pageType'],
			testVariations: rootState.testVariations,
		});
		commit(MUTATIONS.SET_OFFER_STATUS, activatedCbo.success ? CBO_STATES.OFFER_ACTIVATED : activatedCbo.cboState);
	},
	async [ACTIONS.RESET_ACTIVATION_ATTEMPT]({ state, commit }) {
		if (state.offerStatus === CBO_STATES.OFFER_LOADING) {
			commit(MUTATIONS.SET_OFFER_STATUS, CBO_STATES.OFFER_NOT_ACTIVATED);
		}
	},
};

export const initializeModule = ({
	uuid = defaultState.uuid,
	title = defaultState.title,
	offerStatus = defaultState.offerStatus,
	discounts = defaultState.discounts,
	endDate,
	startDate,
} = {}) => ({
	namespaced: true,
	state: {
		uuid,
		title,
		offerStatus,
		discounts,
		endDate,
		startDate,
	},
	getters,
	mutations,
	actions,
});
