<style lang="scss">
@import '../styles/vars/colors';
@import '../styles/vars/sizes';

.not-supported-container {
	display: flex;
	align-items: flex-start;
	flex-flow: column nowrap;
	justify-content: center;
	min-height: 290px;
	overflow: hidden;
	position: relative;
	width: $container-width;
	z-index: 2;
	background-color: $background-color;
	padding: 0;

	.not-supported-content {
		display: flex;
		align-items: flex-start;
		flex-flow: column nowrap;
		justify-content: center;
		flex: 1;
		width: 100%;
		padding: 18px 30px 16px 30px;
		overflow-y: auto;
		overflow-x: hidden;
		max-height: 485px;

		.not-supported-content-inn {
			width: 100%;
		}
	}

	.not-supported-message {
		width: 100%;
		padding: 0 16px;
	}

	.not-supported-title {
		width: 100%;
		padding: 0 16px;
	}

	.not-supported-merchantsList {
		margin: 24px 0 0 0;
		padding: 0 0 16px 0;
	}

	.not-supported-loading {
		height: 180px;
		width: 100%;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
	}

	.merchant-block-btn {
		.button-primary {
			line-height: 25px;
			&:hover:before {
				box-shadow: 0 22px 28px -22px $bbb-pink !important;
			}
		}
	}
}
@media screen and (max-width: $medium-screen-width) {
	.not-supported-container {
		width: $container-width * 0.9;
		.not-supported-content {
			padding: 18px 24px 16px 24px;
			max-height: 435px;
		}
		.not-supported-title {
			padding: 0 8px;
		}
	}
	@media screen and (max-width: $small-screen-width) {
		.not-supported-container {
			width: $container-width * 0.85;
			.not-supported-content {
				padding: 18px 23px 16px 23px;
				max-height: 410px;
			}
		}
	}
}
</style>

<template>
	<div class="not-supported-container">
		<Header :prompt-name="promptName" />
		<div class="not-supported-content">
			<div class="not-supported-content-inn">
				<div class="not-supported-title">
					<span class="primary-text">We don’t support this site yet</span>
				</div>

				<div v-if="topMerchantsList.length" class="not-supported-merchantsList-wrap">
					<div class="not-supported-message">
						<p class="secondary-text">
							But that doesn’t mean you need to miss out on savings. Check out these offers from similar
							stores!
						</p>
					</div>
					<div class="not-supported-merchantsList">
						<MerchantBlock
							v-for="merchant in topMerchantsList"
							:key="merchant.uuid"
							:domain="merchant.domain"
							:title="merchant.title"
							:rebates-count="merchant.rebatesCount"
							:codes-count="merchant.codesCount"
							:outclick-url="merchant.outclickURL"
							:prompt-name="promptName"
						/>
					</div>
				</div>

				<div v-else-if="isLoading" class="not-supported-loading">
					<LoadingSpinner />
				</div>
			</div>
		</div>
		<Footer />
	</div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import { SEARCH_ACTIONS } from '../store/types';
import { Header, Footer, LoadingSpinner, MerchantBlock } from './partials';

const MERCHANTS_TO_SHOW_NUM = 10;

export default {
	components: {
		Header,
		Footer,
		LoadingSpinner,
		MerchantBlock,
	},
	data() {
		return {
			isLoading: true,
			promptName: 'unsupported merchant',
		};
	},
	computed: {
		...mapState('search', ['topMerchantsList']),
	},
	mounted() {
		this.setTopMerchants(MERCHANTS_TO_SHOW_NUM).finally(() => {
			this.isLoading = false;
		});

		this.$fireViewPrompt(this.promptName);
	},
	methods: {
		...mapActions('search', { setTopMerchants: SEARCH_ACTIONS.SET_TOP_MERCHANTS }),
	},
};
</script>
