/**
 * These are the states that a cashback offer can be in coming out of our database
 */

import CBO_PROMPT_NAMES from './cbo-prompt-names';

export default Object.freeze({
	// The CBO has not been activated
	OFFER_NOT_ACTIVATED: 'not activated',

	// The CBO has been successfully activated
	OFFER_ACTIVATED: CBO_PROMPT_NAMES.ACTIVATED,

	// An error has occurred while attempting to activate the CBO
	OFFER_ERROR: CBO_PROMPT_NAMES.EXPIRED,

	// TODO: check me
	// OFFER_EXPIRED: 'expired',

	// The CBO is fetching or processing some information
	OFFER_LOADING: CBO_PROMPT_NAMES.LOADING,

	// An activated CBO was then activated on a competitor site
	OFFER_STOOD_DOWN: CBO_PROMPT_NAMES.DEACTIVATED,
});
