<style lang="scss" scoped>
@import '../styles/vars/z-index';

.bbb-overlay {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.5);
	cursor: pointer;
	z-index: z(mask);
}
</style>

<template>
	<div v-if="showOverlay" class="bbb-overlay" @click="onOverlayClick" />
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import { DISPLAY_ACTIONS } from '../store/types';

export default {
	computed: {
		...mapState('display', ['currentView']),
		...mapGetters('display', ['showOverlay']),
	},
	methods: {
		...mapActions('display', { closeExtension: DISPLAY_ACTIONS.CLOSE_EXTENSION }),
		onOverlayClick() {
			this.$fireClickPrompt('overlay', 'overlay');
			if (this.currentView === 'checking') {
				return;
			}
			this.closeExtension();
		},
	},
};
</script>
