import { browserUtils, format } from '../../utils';

const { domainFromHostname } = browserUtils;
const { roundToDecimal } = format;

/**
 * The Vuex store
 * @typedef {Object} VuexStore
 * @property {Object} state The Vuex store's state
 * @property {String} state.viewInstanceUuid The store's view instance UUID
 * @property {Object} state.merchant The store's merchant
 * @property {String} state.merchant.uuid The store's merchant's UUID
 * @property {Object} getters The Vuex store's getters
 * @property {String} getters.pageType The Vuex store's page type
 */

/**
 * Helper function that sends events to background page for reporting
 * @param {VuexStore} store The Vuex store
 * @param {String} eventType
 * @param {Object} eventPayload
 *
 */
function _sendEvent(store, eventType, eventPayload) {
	// overwrite pageName, viewInstanceUuid, and pageType
	// overwrite pageUuid when a merchant UUID is present
	const context = {
		...(eventPayload.context || {}),
		pageName: window.location.hostname + window.location.pathname,
		viewInstanceUuid: store.state.display.viewInstanceUuid,
		pageType: store.getters['display/pageType'],
		pageUuid: store.state.merchant.uuid || window.location.hostname,
		experiment: store.state.testVariations,
	};
	// additional hydration happens in the background page's processing
	chrome.runtime.sendMessage(
		{
			instruction: 'beacon',
			eventType,
			eventPayload: { ...eventPayload, context },
		},
		() => {},
	);
}

const owenAnalytics = {
	/**
	 * Fire an analytics event to indicate an element was clicked
	 *
	 * @param {VuexStore} store The Vuex store
	 * @param {String} promptName An Owen-valid prompt name
	 * @param {String} eventTarget The target of the Owen event
	 * @param {Array} [inventory] The inventory of the Owen event
	 */

	fireClickPrompt(store, promptName, eventTarget, inventory) {
		const eventPayload = {
			event: {
				eventTarget,
			},
			context: {
				promptName,
				...(inventory && { inventory }),
			},
		};
		_sendEvent(store, 'ClickPrompt', eventPayload);
	},
	fireViewPrompt(store, promptName) {
		_sendEvent(store, 'ViewPrompt', {
			context: {
				promptName,
			},
		});
	},
	fireTrackSurvey(store, { recommend, easeOfUse }) {
		const promptName = 'genie survey';
		this.fireClickPrompt(store, promptName, 'submit');
		this.fireClickPrompt(store, promptName, `refer to friend:${recommend}`);
		this.fireClickPrompt(store, promptName, `ease of use:${easeOfUse}`);
	},
	/**
	 * An analytics event that handles the start and end of the code checking process,
	 * as well as the checking of an individual code
	 * @param {VuexStore} store The Vuex store
	 * @param {String} promptName An Owen-valid prompt name
	 * @param {String} eventTarget The target of the Owen event
	 * @param {Object} codeData Information about the code(s) checked/applied
	 * @param {Number} codeData.totalSavings The discount provided by any checked/applied codes
	 * @param {Array} codeData.codes Collection of codes checked/applied
	 * @param {String} codeData.codes.uuid The UUID of the code
	 * @param {String} codeData.codes.ranking The store page ranking of the code
	 * @param {Object} [cboData] Information about the cashback offer applied
	 * @param {Number} cboData.totalSavings The discount provided by the cash back offer
	 * @param {String} cboData.uuid The UUID of the cashback offer
	 */

	fireTestCodes(store, promptName, eventTarget, codeData, cboData) {
		const payload = {
			context: {
				promptName,
				pageType: 'cart',
			},
			event: {
				eventTarget,
				eventName: 'genie',
				eventAction: 'test codes',
			},
		};
		// overwrite siteUuid when a uuid is present
		const siteUuid = store.state.merchant.uuid || window.location.hostname;
		const inventory = [];
		// Add cbo savings info, if applicable
		if (cboData && cboData.discountAmount) {
			inventory.push({
				inventoryUuid: 'cashback',
				inventoryType: 'bucket label',
				discountType: cboData.discountType,
				discountAmount: roundToDecimal(cboData.discountAmount),
			});
			inventory.push({
				inventoryUuid: cboData.uuid,
				inventoryType: 'rebate',
				parentInventoryUuid: 'cashback',
				siteUuid,
			});
		}
		// Add code savings info, if applicable
		if (eventTarget !== 'start') {
			inventory.push({
				inventoryUuid: 'codes',
				inventoryType: 'bucket label',
				savingsAmount: roundToDecimal(codeData.totalSavings) || 0,
				siteUuid,
			});
		}
		// Add data for each code
		codeData.codes.forEach(code => {
			inventory.push({
				inventoryUuid: code.uuid,
				inventoryType: 'offer',
				parentInventoryUuid: code.ranking,
				siteUuid,
			});
		});
		payload.context.inventory = inventory;
		_sendEvent(store, 'TestCodes', payload);
	},
	/**
	 * Fires an analytics event to indicate that the list of available codes and CBOs
	 * for this merchant was seen by the user.
	 *
	 * @param {VuexStore} store The Vuex store
	 * @param {String} domain The merchant domain
	 * @param {Array} codes Known code discounts for this merchant
	 * @param {Object} [cboData] A plain object with pieces of a cashback offer
	 * @param {String} cboData.uuid The UUID of a cashback offer
	 * @param {boolean} cboData.activated Whether this CBO was activated or not
	 */
	fireCodeListImpressions(store, domain, codes, cboData) {
		// overwrite siteUuid when a uuid is present
		const siteUuid = store.state.merchant.uuid || window.location.hostname;
		const inventory = [];
		// only append cbo info if present
		if (cboData.uuid) {
			inventory.push({
				inventoryUuid: 'cashback',
				inventoryType: 'bucket label',
				siteUuid,
			});
			inventory.push({
				inventoryUuid: cboData.uuid,
				inventoryType: 'rebate',
				parentInventoryUuid: 'cashback',
				statusCheck: cboData.activated ? 'activated' : 'inactive',
				siteUuid,
			});
		}
		if (codes.length) {
			inventory.push({
				inventoryUuid: 'offer list',
				inventoryType: 'bucket label',
				siteUuid,
			});
			for (let index = 0; index < codes.length; index++) {
				inventory.push({
					inventoryUuid: codes[index].uuid || 'unavailable',
					inventoryType: 'offer',
					position: index,
					parentInventoryUuid: 'offer list',
					siteUuid,
				});
			}
		}
		_sendEvent(store, 'RenderedImpression', {
			context: {
				promptName: `/list/${domain}`,
				inventory,
			},
		});
	},
	/**
	 * Fires an analytics event to indicate search is occurring
	 *
	 * @param {VuexStore} store The Vuex store
	 * @param {String} searchTerm The search term
	 */
	fireSearch(store, searchTerm) {
		const domain = domainFromHostname(window.location.hostname);
		_sendEvent(store, 'Search', {
			context: {
				promptName: `/search/${domain}`,
			},
			event: {
				eventTarget: searchTerm,
			},
		});
	},
};

export default owenAnalytics;
