<template>
	<footer>
		<a class="icon footer-profile" :href="profileUrl" target="_blank"> <ProfileIcon /> </a>
		<a class="icon footer-search" @click="searchClickHandler()"><SearchIcon /></a>
		<a class="icon footer-faq" :href="helpUrl" target="_blank"> <FaqIcon /> </a>
	</footer>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
import config from '../../../config';
import FaqIcon from './faq-icon';
import ProfileIcon from './profile-icon';
import SearchIcon from './search-icon';
import { SEARCH_ACTIONS, SEARCH_MUTATIONS } from '../../store/types';

export default {
	components: {
		SearchIcon,
		FaqIcon,
		ProfileIcon,
	},
	data() {
		return {
			profileUrl: `https://secure.${config.site.baseDomain}/my-rewards`,
			helpUrl: 'https://help.retailmenot.com',
		};
	},
	computed: {
		...mapState('display', ['currentView']),
	},
	methods: {
		...mapActions('search', {
			navigateToSearch: SEARCH_ACTIONS.NAVIGATE_TO_SEARCH,
			setTopMerchants: SEARCH_ACTIONS.SET_TOP_MERCHANTS,
			returnSearchToDefault: SEARCH_ACTIONS.RETURN_STATE_TO_DEFAULT,
		}),
		...mapMutations('search', {
			startLoading: SEARCH_MUTATIONS.START_LOADING,
			endLoading: SEARCH_MUTATIONS.END_LOADING,
		}),
		searchClickHandler() {
			if (this.currentView === 'merchantSearch') {
				this.returnSearchToDefault();
				this.startLoading();
				this.setTopMerchants().finally(() => {
					this.endLoading();
				});
			} else {
				this.navigateToSearch();
			}
		},
	},
};
</script>
