<style lang="scss" scoped>
.primary-text {
	max-width: 340px;
}
</style>

<template>
	<div>
		<span class="primary-text" v-html="savingsData.header"></span>
		<div class="secondary-text" v-html="savingsData.body"></div>
	</div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import { moneyFormat } from '../../../utils/format';
import { DISPLAY_ACTIONS } from '../../store/types';
import { backgroundMessaging } from '../../lib';
import { STORAGE_KEYS_SUFFIXES } from '../../consts';

export default {
	props: {
		additionalCallout: {
			type: Object,
			default: () => ({
				message: '',
				showAlways: false,
				showWhenCodeWasFound: false,
			}),
		},
		noSavingsAvailable: {
			required: true,
			type: Boolean,
		},
	},
	data() {
		return {
			QA_CLASSES: Object.freeze({
				codeDiscount: 'qa-codes-amount',
				freeShipping: 'qa-free-shipping',
				freeGifts: 'qa-free-gifts-count',
				cboDiscountActivated: 'qa-cbo-amount',
				bestCodes: 'qa-best-codes',
			}),
		};
	},
	computed: {
		...mapState('coupon', {
			couponResult: 'bestResult',
			orderSubtotal: 'preTaxShippingPrice',
			updatedAdditionalCallout: 'additionalCallout',
		}),
		...mapState('display', ['currentView', 'showValidCoupons']),
		...mapState(['merchant']),
		...mapGetters('cbo', {
			bestDiscountForTotal: 'bestDiscountForTotal',
			isCboActivated: 'isActivated',
		}),
		...mapGetters('coupon', ['noSavingsCodeFound']),
		codeSavings() {
			return this.couponResult.discount || 0;
		},
		cashbackMessage() {
			const { amountOff, percentOff } = this.discountForTotal;

			let cashbackText;
			if (percentOff) {
				if (this.orderSubtotal) {
					const calculatedAmount = (this.orderSubtotal * percentOff) / 100;
					// TODO: when Genie will have information about percentbased CBO max pay sum
					// it should be refactoreed to respect it, currently we display at max $50 (EXT-5287)
					cashbackText = `${moneyFormat(Math.min(calculatedAmount, 50))} Cash Back`;
				} else {
					cashbackText = `${percentOff}% Cash Back`;
				}
			} else if (amountOff) {
				cashbackText = `${moneyFormat(amountOff)} Cash Back`;
			}

			cashbackText = this.wrapMessageWithQA(cashbackText, this.QA_CLASSES.cboDiscountActivated);
			return cashbackText;
		},
		discountForTotal() {
			return this.bestDiscountForTotal(this.orderSubtotal || this.couponResult.price);
		},
		isCashbackAvailable() {
			return this.discountForTotal.amountOff || this.discountForTotal.percentOff;
		},
		cashbackOnly() {
			const { discount, freeGifts, freeShipping } = this.couponResult;
			return !discount && this.isCashbackAvailable && !freeGifts && !freeShipping;
		},
		cashbackIsActivated() {
			return this.isCboActivated && this.isCashbackAvailable;
		},
		cashbackNotActivated() {
			return !this.isCboActivated && this.isCashbackAvailable;
		},
		savingsData() {
			const {
				cashbackOnly,
				codeSavings,
				noSavingsAvailable,
				cashbackIsActivated,
				cashbackNotActivated,
				isCashbackAvailable,
				noSavingsCodeFound,
				showValidCoupons,
			} = this;
			const { freeGifts, freeShipping } = this.couponResult;
			const codesWithQA = this.wrapMessageWithQA(this.couponResult.codes.join(', '), this.QA_CLASSES.bestCodes);
			const isSavingsFound = Boolean(codeSavings || freeGifts || freeShipping);
			let header = [];
			let body = 'No Codes Applied';
			if (noSavingsAvailable) {
				header = ['Testing Complete'];
				body = `Genie tested all of our codes, and you already have the best price.`;
			} else if (cashbackOnly && cashbackNotActivated && !noSavingsCodeFound) {
				header = ['You have the best price'];
			} else {
				if (isSavingsFound) {
					// update body to reflect applied codes
					body = `Code${this.couponResult.codes.length > 1 ? 's' : ''} ${codesWithQA} Applied`;
				}

				if (codeSavings) {
					header.push(this.codeSavingsMessage());
				} else if (noSavingsCodeFound) {
					header.push('Code Savings Found');
					if (showValidCoupons && this.couponResult.codes.length > 0) {
						body = codesWithQA;
					} else {
						body = 'See the savings at checkout';
					}
				}

				if (cashbackIsActivated) {
					header.push(this.cashbackMessage);
					body = `${body} + Cash Back Reward Activated`;
				}

				if (freeGifts) {
					header.push(this.freeGiftsMessage());
				}

				if (freeShipping) {
					header.push(this.freeShippingMessage());
				}
			}

			// result with possible additional callout message
			// updatedAdditionalCallout used if additionalCallout was changed dynamically from driver
			const additionalCallout = this.updatedAdditionalCallout.isChanged
				? this.updatedAdditionalCallout
				: this.additionalCallout;
			const { message, showAlways, showWhenCodeWasFound } = additionalCallout;
			const showCallout = showAlways || showWhenCodeWasFound === isSavingsFound;
			if (message && showCallout) {
				body = `${body}${this.wrapAdditionCalloutMessage(message)}`;
			}

			header = header.join(' +<br>');

			const addFreeItemsHeader =
				!codeSavings && (!isCashbackAvailable || cashbackNotActivated) && (freeGifts || freeShipping);

			if (addFreeItemsHeader) {
				header = `You received ${header}`;
			}

			return { header: `${header}!`, body };
		},
	},
	watch: {
		cashbackIsActivated: {
			handler() {
				this.toggleWarningMessage();
			},
			immediate: true,
		},
	},
	methods: {
		...mapActions('display', {
			showWarningMessage: DISPLAY_ACTIONS.ACTIVE_CBO_LIMITATION_WARNING,
			hideWarningMessage: DISPLAY_ACTIONS.CLOSE_CBO_LIMITATION_WARNING,
		}),
		wrapMessageWithQA(message, qaClass) {
			// Wrap message with QA selector for Aladdin
			return `<span class="${qaClass}">${message}</span>`;
		},
		wrapAdditionCalloutMessage(calloutMessage) {
			return `<p class="secondary-text callout-message">${calloutMessage}</p>`;
		},
		freeGiftsMessage() {
			const freeGiftsText =
				this.couponResult.freeGifts > 1 ? `${this.couponResult.freeGifts} Free Gifts` : 'a Free Gift';
			return this.wrapMessageWithQA(freeGiftsText, this.QA_CLASSES.freeGifts);
		},
		freeShippingMessage() {
			const freeShippingText = 'Free Shipping';
			return this.wrapMessageWithQA(freeShippingText, this.QA_CLASSES.freeShipping);
		},
		codeSavingsMessage() {
			return `${this.wrapMessageWithQA(
				moneyFormat(this.codeSavings),
				this.QA_CLASSES.codeDiscount,
			)} Code Savings`;
		},
		async toggleWarningMessage() {
			const driverStorage = await backgroundMessaging.getDriverStorage(
				`${this.merchant.domain}${STORAGE_KEYS_SUFFIXES.SBO_LIMITATION}`,
			);
			const { discountForTotal, orderSubtotal, isCboActivated } = this;
			const { percentOff } = discountForTotal;
			if (
				isCboActivated &&
				percentOff &&
				orderSubtotal &&
				(orderSubtotal * percentOff) / 100 >= 50 &&
				!driverStorage
			) {
				// if percentbase cashback >= 50$
				this.showWarningMessage();
			} else {
				this.hideWarningMessage();
			}
		},
	},
};
</script>
