import config from '../config';
import { validationIntegrationReporter } from './monitoring';
import { sendXhr } from '../utils';

const validationFlow = Object.freeze({
	200: response => response.json(),
	400: async (response, responseStatus, domain) => {
		const error = new Error(`${domain} - Missing some required data while sending results to the validation`);
		validationIntegrationReporter.captureException(error, {
			extra: { domain, responseStatus, response: await response.json() },
		});
		return [];
	},
});

const manageCodes = async (domain, installationUuid, urlPath, method, body) => {
	const requestURL = config.validationIntegrationAPI;
	// need to use sendXhr to support FF
	const response = await sendXhr(`${requestURL}${urlPath}`, {
		method,
		headers: {
			'X-Client-ID': installationUuid,
		},
		body,
	});

	const responseStatus = await response.status;

	if (responseStatus in validationFlow) {
		return validationFlow[responseStatus](response, responseStatus, domain);
	}
	validationIntegrationReporter.captureException(
		new Error(`${domain} - Response comes with the different status code`),
		{
			extra: {
				domain,
				validationSupport: true,
				responseStatus,
				response,
			},
		},
	);
	return [];
};

const validationApi = {
	getCodes(installationUuid, domain, qty = 1) {
		return manageCodes(domain, installationUuid, `?domain=${domain}&qty=${qty}`, 'GET');
	},

	sendCodes(resultBody, installationUuid) {
		return manageCodes(resultBody.merchant, installationUuid, '', 'POST', JSON.stringify(resultBody));
	},
};
export default validationApi;
