/**
 * @module uiHelpers
 *
 */

const uiHelpers = {
	/**
	 * @function getFullDomain
	 * @member uiHelpers
	 * @return {String} domain the full domain for the current page
	 */
	getFullDomain() {
		// this regex will catch www./www4./ etc
		return window.location.hostname.replace(/^w{2,3}(\d|)./, '');
	},
	/**
	 * @function isInSubdomain
	 * @member uiHelpers
	 * @param {String} checkingDomain domain that may or may not contain the provided subdomain
	 * @param {String} subdomain subdomain to check for
	 * @return {Boolean} indicates whether the supplied domain is at the supplied subdomain
	 */
	isInSubdomain(subdomain, checkingDomain) {
		const checkingSubdomains = checkingDomain.split('.').slice(0, -2);
		return checkingSubdomains.includes(subdomain);
	},
};

export default uiHelpers;
