<style lang="scss">
@import '../styles/vars/colors';
@import '../styles/vars/sizes';

.merchant-search-container {
	display: flex;
	align-items: flex-start;
	flex-flow: column nowrap;
	justify-content: center;
	min-height: 300px;
	overflow: hidden;
	width: 470px;
	padding: 0;
	background-color: $background-color;

	header {
		background: #fff;
	}
}

.merchant-search-body-wrap {
	position: relative;
	display: flex;
	align-items: flex-start;
	flex-flow: column nowrap;
	justify-content: center;
	flex: 1;
	width: 100%;
	.search-loading {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		z-index: 5;
		background: rgba(255, 255, 255, 0.65);
	}
}

.merchant-search-body {
	display: flex;
	align-items: flex-start;
	flex-flow: column nowrap;
	justify-content: center;
	flex: 1;
	width: 100%;
	padding: 1em 1.75em;
	overflow-y: auto;
	overflow-x: hidden;
	max-height: 550px;
}

.merchant-search-results {
	width: 100%;
}

.search-no-results {
	.secondary-text {
		font-size: 16px;
	}
}

.merchant-search-results-list {
	padding: 0 0 16px 0;
}

.merchant-block-btn {
	.button-primary {
		line-height: 25px;
		&:hover:before {
			box-shadow: 0 22px 28px -22px $bbb-pink !important;
		}
	}
}
@media screen and (max-width: $medium-screen-width) {
	.merchant-search-container {
		width: $container-width * 0.9;
		.merchant-search-body {
			padding: 1em 1.55em;
			max-height: 490px;
		}
	}
	@media screen and (max-width: $small-screen-width) {
		.merchant-search-container {
			width: $container-width * 0.85;
			.merchant-search-body {
				padding: 1em 1.45em;
				max-height: 440px;
			}
		}
	}
}
@media screen and (max-height: 700px) {
	.merchant-search-body {
		max-height: 440px;
	}
}
</style>

<template>
	<div class="merchant-search-container">
		<SearchHeader :prompt-name="promptName" />
		<div class="merchant-search-body-wrap">
			<div v-if="isLoading" class="search-loading">
				<LoadingSpinner />
			</div>
			<SearchList :prompt-name="promptName" />
		</div>
		<Footer />
	</div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import { SEARCH_ACTIONS } from '../store/types';
import { Footer, LoadingSpinner } from './partials';
import { SearchHeader, SearchList } from './partials/search';
import { domainFromHostname } from '../../utils/browser';

export default {
	components: {
		SearchHeader,
		Footer,
		SearchList,
		LoadingSpinner,
	},
	data() {
		const domain = domainFromHostname(window.location.hostname);
		return {
			promptName: `/search/${domain}`,
		};
	},
	computed: {
		...mapState('search', ['searchQuery', 'isLoading']),
	},
	mounted() {
		this.$fireViewPrompt(this.promptName);
	},
	destroyed() {
		this.returnStateToDefault();
	},
	methods: {
		...mapActions('search', {
			returnStateToDefault: SEARCH_ACTIONS.RETURN_STATE_TO_DEFAULT,
		}),
	},
};
</script>
