export default options => {
	const { url, body = null, headers = [], method = 'GET' } = options;
	return new Promise((resolve, reject) => {
		const request = new XMLHttpRequest();
		request.withCredentials = true;
		request.open(method, url, true);

		// Listen for ready state
		request.onreadystatechange = () => {
			if (!request || request.readyState !== 4) {
				return;
			}

			// Prepare the response
			const responseHeaders = reformatHeaders(request.getAllResponseHeaders());
			let responseData = request.responseText;

			try {
				responseData = JSON.parse(responseData);
			} catch (e) {
				/* ignore */
			}

			resolve({
				body: responseData,
				status: request.status,
				statusText: request.statusText,
				headers: responseHeaders,
				text: () => request.responseText,
				json: () => JSON.parse(request.responseText),
			});
		};

		// Handle browser request cancellation (as opposed to a manual cancellation)
		request.onabort = () => reject(new Error('Request aborted'));
		// Handle low level network errors
		request.onerror = () => reject(new Error('Network Error'));
		// Handle timeout
		request.ontimeout = () => reject(new Error('timeout exceeded'));

		// Add headers to the request
		Object.entries(headers).forEach(([headerName, headerValue]) => {
			request.setRequestHeader(headerName, headerValue);
		});

		// Send the request
		request.send(body);
	});
};

function reformatHeaders(headersString) {
	const ignoreDuplicateOf = [
		'age',
		'authorization',
		'content-length',
		'content-type',
		'expires',
		'from',
		'host',
		'if-modified-since',
		'if-unmodified-since',
		'last-modified',
		'location',
		'max-forwards',
		'proxy-authorization',
		'referer',
		'retry-after',
		'user-agent',
	];
	try {
		const headersArray = headersString.split('\n');
		return headersArray.reduce((headersObject, headerKeyValuePair) => {
			const [key, value] = headerKeyValuePair.split(':').map(keyOrValue => keyOrValue.trim());
			if (!ignoreDuplicateOf.includes(key)) {
				Object.assign(headersObject, { [key]: value });
			}

			return headersObject;
		}, {});
	} catch (e) {
		return {};
	}
}
