<style lang="scss" scoped>
@import '../styles/vars/colors';

$edge-gutter: 40px; // from _containers.scss

.survey,
.survey-thanks {
	background-color: $bbb-white;
	font-weight: bold;
	padding: 0 $edge-gutter $edge-gutter;
	&.bbb-container-light.bbb-no-logo {
		padding-top: 0;
	}

	header {
		background-image: url('images/genie-logo-blue.svg');
		background-position: center 18px;
		background-repeat: no-repeat;
		background-size: 185px auto;
		margin: 0 0 ($edge-gutter - 8px);
		height: 58px;
		position: relative;
		width: calc(100% + #{$edge-gutter * 2});

		.bbb-close-button {
			right: 26px;
			top: 50%;
			transform: translateY(-50%);
		}
	}

	h1 {
		font-size: 24px;
	}

	.secondary-text {
		letter-spacing: 0.25px;
		margin: 0.7em 0 1em;
		max-width: 400px;
		line-height: 22px;
	}

	p {
		margin: 1.2em 0 0.5em;
		line-height: 20px;
	}

	&.bbb-container-light {
		height: auto;
	}

	.button-primary {
		margin-top: 26px;
		width: 200px;
	}
}

.survey.bbb-container-light {
	width: 470px;
}
.survey-thanks.bbb-container-light {
	width: 420px;
}
</style>

<template>
	<div :class="sayThanks ? 'survey-thanks' : 'survey'" class="bbb-container-light bbb-no-logo justify-center">
		<header><CloseIcon :prompt-name="promptName" /></header>

		<!-- Survey (w/ sliders) -->
		<template v-if="!sayThanks">
			<h1>What do you think of Genie?</h1>
			<p class="secondary-text">We’re committed to making Genie the best online tool for saving you money.</p>

			<p>On a scale of 0 &ndash; 10, how likely are you to recommend Genie to a friend?</p>
			<SurveySlider :value-container="scores" :value-key="'recommend'" />

			<p>On a scale of 0 &ndash; 10, how easy was Genie to use?</p>
			<SurveySlider :value-container="scores" :value-key="'easeOfUse'" />

			<button class="button-primary" @click="submit">Submit</button>
		</template>

		<!-- Survey thanks -->
		<template v-else>
			<h1>Thank you!</h1>
			<p class="secondary-text">We appreciate your feedback.</p>
		</template>
	</div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import { DISPLAY_ACTIONS } from '../store/types';
import SurveySlider from './survey-slider';
import { CloseIcon } from './partials';
import { backgroundMessaging } from '../lib';
import config from '../../config';

export default {
	components: {
		CloseIcon,
		SurveySlider,
	},
	data() {
		return {
			sayThanks: false,
			scores: {
				// Done as an encapsulating object to simplify setting them in the wrapped slider component
				recommend: 9,
				easeOfUse: 9,
			},
			promptName: 'genie survey',

			// survey-thanks only
			countdownTimer: -1,
		};
	},
	computed: mapState(['installationUuid', 'extensionVersion']),
	watch: {
		sayThanks(newVal, oldVal) {
			if (oldVal !== false) return; // only allow this watch to run once

			this.countdownTimer = setTimeout(() => {
				this.closeExtension();
			}, 8000);

			this.promptName = `${this.promptName} complete`;
			this.$fireViewPrompt(this.promptName);
		},
	},
	mounted() {
		this.$fireViewPrompt(this.promptName);
		backgroundMessaging.markSurveySeen();
	},
	destroyed() {
		clearTimeout(this.countdownTimer);
	},
	methods: {
		...mapActions('display', {
			closeExtension: DISPLAY_ACTIONS.CLOSE_EXTENSION,
		}),
		submit() {
			const min = Math.min(this.scores.recommend, this.scores.easeOfUse);
			this.$fireTrackSurvey(this.scores);
			// Show survey if the user did not find the extension useful
			if (min <= 7) {
				this.closeExtension();
				window.open(
					`https://www.${config.site.baseDomain}/genie/survey?uu=${this.installationUuid}&v=${
						this.extensionVersion
					}`,
				);
			} else {
				this.sayThanks = true;
			}
		},
	},
};
</script>
