import { COUPON_MUTATIONS, DISPLAY_ACTIONS, COUPON_ACTIONS, VALIDATION_MUTATIONS } from '../store/types';

const busSetup = {
	/**
	 * @function init
	 * @param {VueInstance} bus the event bus for the UI
	 * @param {VueInstance} app the main Vue app
	 * @param {Object} settings contains some config about which events to add listeners for
	 * This function will wire up the key listeners for the extension to function.
	 */
	init(bus, app) {
		this._ensureCodeCheckListeners(bus, app);
		this._ensureStackedCodesListeners(bus, app);
		// handler for SPA url-tracking events
		bus.$on('cart-active', () => {
			app.$store.dispatch(`display/${DISPLAY_ACTIONS.MARK_CART_ACTIVE}`);
		});
		bus.$on('cart-inactive', () => {
			app.$store.dispatch(`display/${DISPLAY_ACTIONS.MARK_CART_INACTIVE}`);
		});
		app.$store.dispatch(`display/${DISPLAY_ACTIONS.MONITOR_CART_STATUS}`);
		app.$store.dispatch(`display/${DISPLAY_ACTIONS.MONITOR_RX_SAVER_STATE}`);
		app.$store.dispatch(`display/${DISPLAY_ACTIONS.MONITOR_USER_DATA_RESTOREMENT}`);
		// handler for dynamic changing completion action
		bus.$on('change-completion-action', completionAction => {
			app.$store.commit(`coupon/${COUPON_MUTATIONS.SET_COMPLETION_ACTION}`, completionAction);
		});
		// handler for dynamic changing additional callout
		bus.$on('change-additional-callout', additionalCallout => {
			app.$store.commit(`coupon/${COUPON_MUTATIONS.SET_ADDITIONAL_CALLOUT}`, additionalCallout);
		});
		// handler for user data restorement action
		bus.$on('restore-user-data', () => {
			app.$store.dispatch(`coupon/${COUPON_ACTIONS.RESTORE_USER_DATA}`);
		});
		// handler for sending results about testing codes from validation side
		bus.$on('send-validation-codes', resultData => {
			app.$store.commit(`validation/${VALIDATION_MUTATIONS.SET_VALIDATION_RESULTS}`, resultData);
		});
	},
	_ensureCodeCheckListeners(bus, app) {
		bus.$on('check-codes', ({ promptName }) => {
			app.$store.dispatch(`coupon/${COUPON_ACTIONS.START_CHECKING}`, promptName);
		});
		bus.$on('code-checked', resultData => {
			app.$store.dispatch(`coupon/${COUPON_ACTIONS.RECORD_CHECKING_RESULT}`, resultData);
		});
	},
	_ensureStackedCodesListeners(bus, app) {
		bus.$on('stack-codes', validationInfo => {
			app.$store.dispatch(`coupon/${COUPON_ACTIONS.START_STACKING}`, validationInfo);
		});
		bus.$on('code-stacked', resultData => {
			app.$store.dispatch(`coupon/${COUPON_ACTIONS.RECORD_STACKING_RESULT}`, resultData);
		});
		bus.$on('stackable-complete', data => {
			app.$store.dispatch(`coupon/${COUPON_ACTIONS.FINISH_STACKING}`, data);
		});
	},
};

export default busSetup;
