<template>
	<div>
		<CloseIcon :prompt-name="uiStatus" :on-dark-bg="onDarkBg" />
		<CboUi />
		<FirstMessageModal v-if="uiStatus === CBO_PROMPT_NAMES.CAN_ACTIVATE" />
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import { CboUi, FirstMessageModal, CloseIcon } from './partials';
import { CBO_PROMPT_NAMES } from '../consts';
import { backgroundMessaging } from '../lib';

export default {
	components: {
		CboUi,
		FirstMessageModal,
		CloseIcon,
	},
	data() {
		return {
			CBO_PROMPT_NAMES,
		};
	},
	computed: {
		...mapState(['merchant']),
		...mapGetters('cbo', ['uiStatus']),
		onDarkBg() {
			return (
				this.uiStatus === CBO_PROMPT_NAMES.CAN_ACTIVATE ||
				this.uiStatus === CBO_PROMPT_NAMES.ACTIVATED ||
				this.uiStatus === CBO_PROMPT_NAMES.LOADING
			);
		},
	},
	mounted() {
		backgroundMessaging.markCboPromptDisplayed(this.merchant.domain, Date.now());
	},
};
</script>
