<template>
	<section class="main-wrapper">
		<div class="write-review-main">
			<h1 class="like-us">Like Genie?</h1>
			<p class="write-review">
				Write a review and let us know how much you saved!
			</p>
			<div class="stars">
				<Star v-for="index in 5" :key="index" />
			</div>
			<button class="button-primary qa-apply-savings-button" @click="navigateToReview">
				Review Genie
			</button>
		</div>
	</section>
</template>

<script>
import { mapMutations } from 'vuex';
import { DISPLAY_MUTATIONS } from '../../store/types';
import { backgroundMessaging } from '../../lib';
import Star from './review-us-star-icon';
import { isChrome } from '../../../utils/browser';
import { reviewUsFireFoxLink, reviewUsChromeLink } from '../../consts/links';

export default {
	components: {
		Star,
	},
	data() {
		return {
			promptName: 'review us',
			isChrome: isChrome(),
			userClickedReviewUsBtn: false,
		};
	},
	mounted() {
		this.$fireViewPrompt(this.promptName);
	},
	destroyed() {
		/**
		 *  This case needed, when we don't reload the page after the code testing...
		 *  There are one of the possible ways, where we should change review us displaying a status but not immediately
		 */
		if (this.userClickedReviewUsBtn) this.setHideReviewUsSideCard(false);
	},
	methods: {
		...mapMutations('display', {
			setHideReviewUsSideCard: DISPLAY_MUTATIONS.SET_SHOW_REVIEW_US,
		}),
		navigateToReview() {
			this.userClickedReviewUsBtn = true;
			this.$fireClickPrompt(this.promptName, 'review genie');
			backgroundMessaging.setShowReviewUsSideCard(false);
			window.open(this.isChrome ? reviewUsChromeLink : reviewUsFireFoxLink, '_blank');
		},
	},
};
</script>

