import uuid from '@rmn/uuid';
import { ACTIONS, MUTATIONS } from '../types/display';
import { CBO_ACTIONS, COUPON_ACTIONS } from '../types';
import { CBO_PROMPT_NAMES, STORAGE_KEYS_SUFFIXES } from '../../consts';
import { backgroundMessaging, handleDriverError } from '../../lib';
import { completionActions, log } from '../../../utils';
import pharmacyWebsites from '../../consts/pharmacy-domains-list';
import { shouldDisplayRxSaverPrompt } from '../../../bg/storage-access';
import { domainFromHostname, isFirefox } from '../../../utils/browser';

export const defaultState = {
	viewInstanceUuid: null,
	currentView: 'closed',
	previousView: 'closed',
	openView: null,
	shouldStandDown: false,
	shouldShowCboInfoPrompt: false,
	isOnCartUrl: false,
	isSinglePageApp: false,
	showFreeGiftsWarningMessage: false,
	showCboLimitationWarning: false,
	enableFreeGiftsWarning: false,
	restoreUserData: false,
	isCartEmpty: false,
	watchForCartUrlFn: null,
	watchForRestoreUserDataFn: null,
	isCartEmptyFn: null,
	closedWhileTesting: false,
	enableDifferentDiscountMessage: false,
	shouldShowReviewUs: true,
	showValidCoupons: false,
};

export const getters = {
	badgeCount: (state, getters, rootState, rootGetters) => {
		if (state.shouldStandDown || getters.iconImage !== 'active' || state.openView === 'RxSaverPrompt') return 0;

		const numCodes = rootState.coupon.codesRecentlyChecked ? 0 : rootGetters['coupon/codes'].length;
		const numCbos = rootGetters['cbo/uiStatus'] === CBO_PROMPT_NAMES.CAN_ACTIVATE ? 1 : 0;
		return numCodes + numCbos;
	},
	isExtensionClosed: state => state.currentView === 'closed',
	iconImage: state => {
		if (state.shouldStandDown) return 'inactive';

		switch (state.openView) {
			case 'checking':
				return 'checking';
			case 'blockedByNetwork':
			case 'merchantSearch':
			case 'noContent':
			case 'notSupported':
			case 'standDown':
				return 'inactive';
			default:
				return 'active';
		}
	},
	pageType: state => (state.isOnCartUrl ? 'cart' : 'browse'),
	showOffersListCta: state => state.isOnCartUrl && (state.isSinglePageApp || !state.isCartEmpty),
	showOverlay: state => ['checking', 'results', 'survey', 'backgroundRelaunch'].includes(state.currentView),
};

export const mutations = {
	[MUTATIONS.SET_CURRENT_VIEW](state, view) {
		state.viewInstanceUuid = uuid.randomBase32();
		state.previousView = state.currentView;
		state.currentView = view;
	},
	[MUTATIONS.SET_OPEN_VIEW](state, view) {
		state.openView = view;
	},
	[MUTATIONS.SET_SHOULD_STAND_DOWN](state, standDown) {
		state.shouldStandDown = standDown;
	},
	[MUTATIONS.SET_IS_ON_CART_URL](state, isOnCartUrl) {
		state.isOnCartUrl = isOnCartUrl;
	},
	[MUTATIONS.SET_CLOSED_WHILE_TESTING](state, closedWhileTesting) {
		state.closedWhileTesting = closedWhileTesting;
	},
	[MUTATIONS.SET_SHOW_FREE_GIFTS_WARNING](state, showFreeGiftsWarningMessage) {
		state.showFreeGiftsWarningMessage = showFreeGiftsWarningMessage;
	},
	[MUTATIONS.SET_HIDE_DIFFERENT_DISCOUNT](state, enableDifferentDiscountMessage) {
		state.enableDifferentDiscountMessage = enableDifferentDiscountMessage;
	},
	[MUTATIONS.SET_SHOW_CBO_LIMITATION_WARNING](state, showCboLimitationWarning) {
		state.showCboLimitationWarning = showCboLimitationWarning;
	},
	[MUTATIONS.SET_SHOW_REVIEW_US](state, shouldShow) {
		state.shouldShowReviewUs = shouldShow;
	},
};

export const actions = {
	[ACTIONS.CLOSE_FREE_GIFTS_WARNING]({ commit, rootState }) {
		backgroundMessaging.setDriverStorage(`${rootState.merchant.domain}${STORAGE_KEYS_SUFFIXES.FREE_GIFTS}`, true);
		commit(MUTATIONS.SET_SHOW_FREE_GIFTS_WARNING, false);
	},
	[ACTIONS.CLOSE_CBO_LIMITATION_WARNING]({ commit }) {
		commit(MUTATIONS.SET_SHOW_CBO_LIMITATION_WARNING, false);
	},
	[ACTIONS.ACTIVE_CBO_LIMITATION_WARNING]({ commit }) {
		commit(MUTATIONS.SET_SHOW_CBO_LIMITATION_WARNING, true);
	},
	[ACTIONS.OPEN_EXTENSION]({ state, commit }) {
		commit(MUTATIONS.SET_CURRENT_VIEW, state.openView);
	},
	[ACTIONS.CLOSE_EXTENSION]({ state, commit, rootState, dispatch }) {
		if (state.currentView === 'checking') {
			commit(MUTATIONS.SET_OPEN_VIEW, 'offersList');
			// for now we will not add this label to the sentry error when completionAction is none
			// because when user will click on the close button while testing nothing will happen
			// and codes testing will be continued

			if (rootState.coupon.completionAction !== completionActions.none) {
				commit(MUTATIONS.SET_CLOSED_WHILE_TESTING, true);
			}
			dispatch(`coupon/${COUPON_ACTIONS.FINISH_CHECKING}`, null, { root: true });
			dispatch(`coupon/${COUPON_ACTIONS.ADVANCE_TO_CHECKOUT}`, null, { root: true });
		}
		commit(MUTATIONS.SET_CURRENT_VIEW, 'closed');
		backgroundMessaging.closePopUp();
		dispatch(`cbo/${CBO_ACTIONS.RESET_ACTIVATION_ATTEMPT}`, null, { root: true });
	},
	[ACTIONS.TOGGLE_EXTENSION]({ getters, dispatch }) {
		if (getters.isExtensionClosed) {
			dispatch(ACTIONS.OPEN_EXTENSION);
		} else {
			dispatch(ACTIONS.CLOSE_EXTENSION);
		}
	},
	[ACTIONS.CLOSE_DIFFERENT_DISCOUNT_WARNING]({ commit }) {
		commit(MUTATIONS.SET_HIDE_DIFFERENT_DISCOUNT, false);
	},
	async [ACTIONS.MARK_CART_ACTIVE]({ state, getters, commit, rootState }) {
		if (!state.isOnCartUrl) {
			log('cart activated');
			commit(MUTATIONS.SET_IS_ON_CART_URL, true);
			if (state.enableFreeGiftsWarning && !state.showFreeGiftsWarningMessage) {
				const showFreeGiftsWarningMessage = !(await backgroundMessaging.getDriverStorage(
					`${rootState.merchant.domain}${STORAGE_KEYS_SUFFIXES.FREE_GIFTS}`,
				));
				commit(MUTATIONS.SET_SHOW_FREE_GIFTS_WARNING, showFreeGiftsWarningMessage);
			}
			const promptType = rootState.coupon.coupons.length > 0 ? 'cart' : 'cbo';
			const displayPrompt = await backgroundMessaging.shouldDisplayPrompt(rootState.merchant.domain, promptType);
			// If Genie was just used we won't show check codes prompt
			// and replace CBO with cart prompt in case it is displayed
			// the check for the shouldStandDown setting is most likely real-world redundant through
			// the API call, but can be set via a different code path, so we want to also respect that
			if (!state.shouldStandDown && displayPrompt && getters.isExtensionClosed) {
				commit(MUTATIONS.SET_CURRENT_VIEW, `${promptType}Prompt`);
			}
		}
	},
	[ACTIONS.MARK_CART_INACTIVE]({ state, commit }) {
		if (state.isOnCartUrl) {
			log('cart inactivated');
			commit(MUTATIONS.SET_IS_ON_CART_URL, false);
			if (state.currentView === 'cartPrompt') {
				commit(MUTATIONS.SET_CURRENT_VIEW, 'closed');
			}
			if (state.enableFreeGiftsWarning && state.showFreeGiftsWarningMessage) {
				commit(MUTATIONS.SET_SHOW_FREE_GIFTS_WARNING, false);
			}
		}
	},
	[ACTIONS.MONITOR_USER_DATA_RESTOREMENT]({ state, rootState }) {
		if (rootState.coupon.driver && state.restoreUserData) {
			try {
				state.watchForRestoreUserDataFn();
			} catch (e) {
				handleDriverError(e, { source: 'watchForRestoreUserData' });
			}
		}
	},
	[ACTIONS.MONITOR_RX_SAVER_STATE]({ state, commit, rootState }) {
		let oldTabUrl = null;
		let rxShowed = false;

		Object.entries(pharmacyWebsites).forEach(async domains => {
			const { host } = new URL(window.location.href);
			if (domainFromHostname(host) === 'meijerspecialtypharmacy.com' && isFirefox()) {
				// this merchant on the FF remove our extension from the DOM tree when user clicks on the "back" btn on the browser
				// so we won't support this merchant on the FF browser
				return;
			}
			const [domain, path] = domains;
			const domainRegex = new RegExp(domain, 'i');
			const isOnRightDomain = domainRegex.test(domainFromHostname(host));
			if (isOnRightDomain) {
				const pathRegex = new RegExp(path.join('|'), 'i');
				setInterval(async () => {
					if (pathRegex.test(window.location.href) && !rxShowed) {
						const shouldShow = (await shouldDisplayRxSaverPrompt(domain)) || null;
						commit(MUTATIONS.SET_CURRENT_VIEW, 'closed');
						commit(MUTATIONS.SET_OPEN_VIEW, 'RxSaverPrompt');
						rxShowed = true;
						if (shouldShow) {
							commit(MUTATIONS.SET_CURRENT_VIEW, 'RxSaverPrompt');
						}
					} else if (!state.isOnCartUrl && window.location.href !== oldTabUrl && rxShowed) {
						const isSiteEnabled = !!rootState.coupon.driver;
						commit(MUTATIONS.SET_CURRENT_VIEW, 'closed');
						if (!isSiteEnabled) {
							commit(MUTATIONS.SET_OPEN_VIEW, 'notSupported');
						} else {
							commit(MUTATIONS.SET_OPEN_VIEW, 'offersList');
						}
						rxShowed = false;
					}
					oldTabUrl = window.location.href;
				}, 500);
			}
		});
	},
	[ACTIONS.MONITOR_CART_STATUS]({ state, dispatch, rootState }) {
		if (rootState.coupon.driver) {
			if (state.isSinglePageApp) {
				try {
					// If implemented, will dispatch 'cart-active/inactive' events to the bus
					// Refactoring this is off the table for now, as it would affect a large number of legacy drivers
					state.watchForCartUrlFn();
				} catch (e) {
					handleDriverError(e, { source: 'watchForCartUrl' });
				}

				// Close extension when user press return button
				window.onpopstate = () => {
					if (state.currentView === 'checking' || state.currentView === 'results') {
						dispatch(ACTIONS.CLOSE_EXTENSION);
					}
				};
			} else if (typeof state.isCartEmptyFn === 'function') {
				// This check deactivates code testing if a user deletes all items from a cart
				// Allows to deactivate cart even if page wasn't reloaded
				let previousState = false;

				const interval = setInterval(() => {
					let currentState = null;
					try {
						// Handle exceptions of isCartEmpty to be notified
						currentState = state.isCartEmptyFn();
					} catch (e) {
						handleDriverError(e, { source: 'isCartEmpty' });
						clearInterval(interval);
					}

					if (currentState !== previousState) {
						if (currentState) {
							dispatch(ACTIONS.MARK_CART_INACTIVE);
						} else {
							dispatch(ACTIONS.MARK_CART_ACTIVE);
						}
					}
					previousState = currentState;
				}, 500);
			}
		}
	},
};

export const initializeModule = ({
	currentView = defaultState.currentView,
	openView = defaultState.openView,
	viewInstanceUuid = defaultState.viewInstanceUuid,
	shouldStandDown = defaultState.shouldStandDown,
	shouldShowCboInfoPrompt = defaultState.shouldShowCboInfoPrompt,
	isOnCartUrl = defaultState.isOnCartUrl,
	isSinglePageApp = defaultState.isSinglePageApp,
	showFreeGiftsWarningMessage = defaultState.showFreeGiftsWarningMessage,
	enableFreeGiftsWarning = defaultState.enableFreeGiftsWarning,
	showCboLimitationWarning = defaultState.showCboLimitationWarning,
	restoreUserData = defaultState.restoreUserData,
	isCartEmpty = defaultState.isCartEmpty,
	watchForCartUrlFn = defaultState.watchForCartUrlFn,
	watchForRestoreUserDataFn = defaultState.watchForRestoreUserDataFn,
	isCartEmptyFn = defaultState.isCartEmptyFn,
	closedWhileTesting = defaultState.closedWhileTesting,
	previousView = defaultState.previousView,
	enableDifferentDiscountMessage = defaultState.enableDifferentDiscountMessage,
	shouldShowReviewUs = defaultState.shouldShowReviewUs,
	showValidCoupons = defaultState.showValidCoupons,
} = {}) => ({
	namespaced: true,
	state: {
		currentView,
		openView,
		viewInstanceUuid,
		shouldStandDown,
		shouldShowCboInfoPrompt,
		isOnCartUrl,
		isSinglePageApp,
		showFreeGiftsWarningMessage,
		enableFreeGiftsWarning,
		showCboLimitationWarning,
		restoreUserData,
		isCartEmpty,
		watchForCartUrlFn,
		watchForRestoreUserDataFn,
		isCartEmptyFn,
		closedWhileTesting,
		previousView,
		enableDifferentDiscountMessage,
		shouldShowReviewUs,
		showValidCoupons,
	},
	getters,
	mutations,
	actions,
});
