<style lang="scss" scoped>
@import '../../../styles/vars/colors.scss';
.back-icon {
	margin-left: 1.5em;
}
.search-icon {
	width: 15px;
	margin-right: 0.5em;
}
.search-container {
	margin-left: 30px;
	margin-right: 45px;
	display: flex;
	flex-grow: 1;
}
.search-form {
	border: 0;
	flex-grow: 1;
	color: $bbb-gray;

	&::-webkit-input-placeholder {
		color: lighten($bbb-gray, 25%);
	}
	&:focus {
		outline: none;
	}
}
</style>

<template>
	<header>
		<BackIcon class="back-icon" @click.native="setCurrentView(previousView)" />
		<div class="search-container">
			<SearchIcon class="search-icon" />
			<input
				v-model="searchInputValue"
				type="text"
				class="secondary-text search-form"
				placeholder="Search Stores"
				@input="updateSearchQueryDebounce"
			/>
		</div>
		<CloseIcon :prompt-name="promptName" />
	</header>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import debounce from 'lodash.debounce';
import BackIcon from '../back-icon';
import CloseIcon from '../close-icon';
import SearchIcon from '../search-icon';
import { DISPLAY_MUTATIONS, SEARCH_MUTATIONS } from '../../../store/types';

export default {
	components: {
		CloseIcon,
		BackIcon,
		SearchIcon,
	},
	props: {
		promptName: {
			type: String,
			required: true,
		},
	},
	data() {
		return {
			searchInputValue: '',
		};
	},
	computed: {
		...mapState('display', ['previousView']),
		...mapState('search', ['searchQuery']),
	},
	watch: {
		searchQuery(value) {
			// need this watcher here to update input text when we change 'searchQuery' in state from outside
			this.searchInputValue = value;
		},
	},
	mounted() {
		this.updateSearchQuery();
	},
	created() {
		this.updateSearchQueryDebounce = debounce(this.updateSearchQuery, 300, { maxWait: 1000 });
	},
	methods: {
		...mapMutations('display', {
			setCurrentView: DISPLAY_MUTATIONS.SET_CURRENT_VIEW,
		}),
		...mapMutations('search', {
			setSearchQuery: SEARCH_MUTATIONS.SET_SEARCH_QUERY,
		}),
		updateSearchQuery() {
			this.$fireSearch(this.searchInputValue);
			this.setSearchQuery(this.searchInputValue);
		},
	},
};
</script>
