<template>
	<header class="bbb-genie-logo">
		<span v-if="merchantName" class="header-merchant">
			<a v-if="merchantDomain" :href="merchantLink" target="_blank" class="secondary-text">
				{{ merchantName }}
			</a>
			<span v-else class="secondary-text">{{ merchantName }}</span>
		</span>
		<CloseIcon :prompt-name="promptName" />
	</header>
</template>

<script>
import CloseIcon from './close-icon';

export default {
	components: {
		CloseIcon,
	},
	props: {
		merchantName: {
			type: String,
			default: null,
		},
		merchantDomain: {
			type: String,
			default: null,
		},
		promptName: {
			type: String,
			required: true,
		},
	},
	computed: {
		merchantLink() {
			return `http://${this.merchantDomain}`;
		},
	},
};
</script>
