import Raven from 'raven-js';
import config from '../config';

// See https://github.com/getsentry/sentry-javascript/blob/edd08f9fa4e4c387ed9c3ba2500119c78e67be15/packages/raven-js/src/singleton.js#L36
config.raven.config.release = config.extensionVersion;

const genieReporter = new Raven.Client();
const driversReporter = new Raven.Client();
const topDriversReporter = new Raven.Client();
const validationIntegrationReporter = new Raven.Client();

genieReporter.config(config.raven.baseUrl, config.raven.config);

driversReporter.config(config.raven.driverUrl, config.raven.config);

topDriversReporter.config(config.raven.topDriverUrl, config.raven.config);

validationIntegrationReporter.config(config.raven.validationIntegrationUrl, config.raven.config);

function reconfigureCapture(ravenInstance) {
	const oldCaptureException = ravenInstance.captureException;
	const oldCaptureMessage = ravenInstance.captureMessage;

	/**
	 * Manually capture an exception and send it over to Sentry and console on the background page
	 * with adding fingerprints to the capture exception function to ensure that
	 * errors are bucketed by event and message, and not by URL
	 *
	 * @param {error} ex An exception to be logged
	 * @param {object} options A specific set of options for this error [optional]
	 * @param {object} options.extra specific settings to pass along for fingerprinting, etc
	 * @param {boolean} logOnBackground is responsible for whether or not need to show the error message in the background
	 * @return {Raven}
	 */
	ravenInstance.captureException = function captureException(ex, options = {}, logOnBackground = true) {
		options.extra = options.extra || {};
		const extraProperties = Object.getOwnPropertyNames(ex);
		extraProperties.forEach(key => {
			if (key !== 'name' && key !== 'message') {
				options.extra[key] = ex[key];
			}
		});
		if (!options.fingerprint) {
			options.fingerprint = [ex.message];
			if (options.extra.domain) {
				options.fingerprint.push(options.extra.domain);
			}
		}

		// error from client injected script no need to duplicate in the background
		if (logOnBackground) {
			// eslint-disable-next-line no-console
			console.error(ex);
		}

		return oldCaptureException.apply(this, [ex, options]);
	};

	/**
	 * Manually capture a message and send it over to Sentry and console on the background page
	 *
	 * @param {string} message A message to be logged
	 * @param {object} options A specific set of options for this message [optional]
	 * @param {boolean} logOnBackground is responsible for whether or not need to show the error message in the background
	 * @return {Raven}
	 */
	ravenInstance.captureMessage = function captureMessage(message, options = {}, logOnBackground = true) {
		options.extra = options.extra || {};
		if (logOnBackground) {
			// eslint-disable-next-line no-console
			console.warn(message);
		}
		return oldCaptureMessage.apply(this, [message, options]);
	};
}

reconfigureCapture(genieReporter);
reconfigureCapture(driversReporter);
reconfigureCapture(topDriversReporter);
reconfigureCapture(validationIntegrationReporter);

export { genieReporter, driversReporter, topDriversReporter, validationIntegrationReporter };
