import { backgroundMessaging, handleError } from './lib';
import driverCache from './lib/driver-cache';
import setupUi from './lib/ui-setup';
import { ROOT_MUTATIONS, CBO_MUTATIONS, CBO_ACTIONS, DISPLAY_ACTIONS, DISPLAY_MUTATIONS } from './store/types';
import CBO_STATES from './consts/cbo-states';
import { log } from '../utils';
import config from '../config';

(async function inject() {
	// short circuit if we are in an iframe or subresource
	if (window.top !== window) {
		return;
	}

	let setupKickedOff = false;

	document.addEventListener(
		'toggle-genie',
		() => {
			// TODO: undocumented, rewrite
			// https://developer.chrome.com/extensions/extension
			chrome.extension.sendMessage({
				toggle: true,
			});
		},
		false,
	);

	chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
		// guard against empty callbacks
		if (!request) {
			return;
		}
		const { RMNGenieApp } = window;
		if (request.reveal) {
			if (RMNGenieApp) {
				RMNGenieApp.$store.dispatch(`display/${DISPLAY_ACTIONS.TOGGLE_EXTENSION}`);
			} else {
				setupExtension();
			}
			return;
		}
		switch (request.instruction) {
			case 'cboStatusChanged':
				if (RMNGenieApp) {
					const currentCboUuid = RMNGenieApp.$store.state.cbo.uuid;
					if (request.cboStatus === 'cboDeactivated') {
						RMNGenieApp.$store.commit(`cbo/${CBO_MUTATIONS.SET_OFFER_STATUS}`, CBO_STATES.OFFER_STOOD_DOWN);
					} else if (request.cboStatus === 'cboActivated' && request.offerUuid === currentCboUuid) {
						RMNGenieApp.$store.commit(`cbo/${CBO_MUTATIONS.SET_OFFER_STATUS}`, CBO_STATES.OFFER_ACTIVATED);
					}
				}
				break;

			case 'standDown':
				if (RMNGenieApp) {
					RMNGenieApp.$store.commit(`display/${DISPLAY_MUTATIONS.SET_SHOULD_STAND_DOWN}`, true);
					if (RMNGenieApp.$store.state.coupon.coupons && RMNGenieApp.$store.state.cbo.uuid) {
						RMNGenieApp.$store.commit(`display/${DISPLAY_MUTATIONS.SET_CURRENT_VIEW}`, 'standDown');
					}
				}
				break;
			case 'popUpClosed':
				if (RMNGenieApp && RMNGenieApp.$store.state.display.currentView === 'backgroundRelaunch') {
					RMNGenieApp.$store.commit(
						`display/${DISPLAY_MUTATIONS.SET_CURRENT_VIEW}`,
						RMNGenieApp.$store.state.display.openView,
					);
					/* TODO:
					.* needed to be revisited and refactored possibly more carefully,
					.* as it's assumed here that we always display cboPrompt when there are no codes,
					.* but ideally it's not guaranteed that CBO exists (for now it's true,
					.* because there is no user flow to close sign in window without CBO,
					.* but it might change in future).
					.* Jira ticket: https://jira.rmn.com/browse/EXT-5141
					*/
					const openView = RMNGenieApp.$store.state.coupon.coupons.length ? 'offersList' : 'cboPrompt';
					RMNGenieApp.$store.commit(`display/${DISPLAY_MUTATIONS.SET_OPEN_VIEW}`, openView);
					sendResponse({ success: true });
				} else {
					sendResponse({ success: false });
				}
				break;
			case 'permissionsChanged':
				try {
					RMNGenieApp.$store.commit(ROOT_MUTATIONS.UPDATE_PERMISSIONS, request.permissions);
					sendResponse({ success: true });
				} catch (e) {
					sendResponse({ success: false });
				}
				break;
			case 'userStatusChanged':
				try {
					RMNGenieApp.$store.commit(ROOT_MUTATIONS.UPDATE_USER_STATUS, request.userStatus);
					updateCboStatus();
					sendResponse({ success: true });
				} catch (e) {
					sendResponse({ success: false });
				}
				break;
			default:
				break;
		}
	});

	// this href needs to be updated when we add the route to the landing page
	if (window.location.href === `https://www.${config.site.baseDomain}/genie/login`) {
		// this listens for when we are on the login success route
		// of the landing page, then closes the pop up
		setTimeout(() => {
			backgroundMessaging.closePopUp();
		}, 3000);
		return;
	}

	const readyStateCheckInterval = setInterval(() => {
		const { readyState } = document;
		if ((readyState === 'interactive' || readyState === 'complete') && document.documentElement) {
			clearInterval(readyStateCheckInterval);
			setupExtension();
		}
	}, 10);

	async function updateCboStatus() {
		const { RMNGenieApp } = window;
		if (RMNGenieApp) {
			const { $store } = RMNGenieApp;
			const { userStatus, merchant, cbo } = $store.state;

			// we only want to do state updates if there is a CBO
			// login is irrelevant in other cases (as, obviously, is CBO status)
			const currentCboUuid = cbo.uuid;

			if (currentCboUuid) {
				// if we have logged out, the offer must be set to not activated
				if (!userStatus.isLoggedIn || !userStatus.isVerified) {
					$store.commit(`cbo/${CBO_MUTATIONS.SET_OFFER_STATUS}`, CBO_STATES.OFFER_NOT_ACTIVATED);
				} else {
					log('requesting CBO info');
					const cboInfo = await backgroundMessaging.checkCboStatus(merchant.domain);
					const activationTimestamp = cboInfo[currentCboUuid];
					let cboStatusToSet = CBO_STATES.OFFER_ACTIVATED;
					if (activationTimestamp === -1) {
						cboStatusToSet = CBO_STATES.OFFER_STOOD_DOWN;
					} else if (!activationTimestamp) {
						cboStatusToSet = CBO_STATES.OFFER_NOT_ACTIVATED;
						if (cbo.offerStatus === CBO_STATES.OFFER_LOADING) {
							await $store.dispatch(`cbo/${CBO_ACTIONS.ATTEMPT_TO_ACTIVATE}`);
							return;
						}
					}
					$store.commit(`cbo/${CBO_MUTATIONS.SET_OFFER_STATUS}`, cboStatusToSet);
				}
			}
		}
	}

	async function setupExtension() {
		// short circuit in case of race condition b/w doc ready and browser action click
		if (setupKickedOff) {
			return;
		}
		setupKickedOff = true;

		// Fetch codes, cbos, and user information
		try {
			const displayInfo = await backgroundMessaging.retrieveDisplayInfo(window.location);
			const domain = displayInfo.merchant.browserExtensionDomainOverride || displayInfo.merchant.domain;
			const driver = await driverCache.getDriverForDomain(domain);

			log(`Domain: ${domain}`);
			log('Merchant driver: %O', driver);
			log('displayInfo: %O', displayInfo);
			setupUi(displayInfo, driver);
		} catch (e) {
			handleError(e);
		}
	}
})();
