import serializeForm from 'form-serialize';
import { wait } from './browser';

/**
 * @module DOM
 * @member utils
 *
 */
const DOM = {
	/**
	 * @function extractFromHTML
	 * @param {String} htmlString String text content from which we want to extract text content
	 * @param {String} selector String CSS selector for the specific element we want the text of
	 * @param {Boolean} [all=false] optional flag whether to return data of each found element. defaults to false
	 * @param {String} [attributeName=textContent] optional information about what attribute to extract. Defaults to textContent
	 * @return {String|String[]} String or array of String text content of the requested DOM nodes
	 */
	extractFromHTML: function extractFromHTML(htmlString, selector, all, attributeName) {
		const tempDocument = document.implementation.createHTMLDocument();
		tempDocument.body.innerHTML = htmlString;
		if (all) {
			const elements = [...tempDocument.querySelectorAll(selector)];
			if (attributeName) {
				return elements.map(element => element.getAttribute(attributeName));
			}
			return elements.map(element => element.textContent);
		}
		const element = tempDocument.querySelector(selector);
		if (!element) {
			return '';
		}
		if (attributeName) {
			return element.getAttribute(attributeName);
		}
		return element.textContent;
	},
	/**
	 * @function extractElementFromHTML
	 * @param {String} htmlString String text content from which we want to extract element
	 * @param {String} selector String CSS selector for the specific element we want to extract
	 * @param {Boolean} [all=false] Boolean optional parameter indicates whether to return all found elements or only first one
	 * @return {Element|Element[]} DOM element or array of DOM elements in case `all` parameter is true
	 */
	extractElementFromHTML: function extractElementFromHTML(htmlString, selector, all) {
		const tempDocument = document.implementation.createHTMLDocument();
		tempDocument.body.innerHTML = htmlString;
		return all ? tempDocument.querySelectorAll(selector) : tempDocument.querySelector(selector);
	},

	/**
	 * @typedef {Object} ExtractedFormValues
	 * @property {String} action the "action" attribute of a <form>
	 * @property {URLSearchParams} params key/value pairs of all the <input>s owned by this <form>
	 */
	/**
	 * @function extractFormValues
	 * @param {String} selector String CSS selector for the specific form we want values of
	 * @param {String} [htmlString] String optional text content from which we want to extract form, default select from current page
	 * @param {Boolean} [append] Boolean optional parameter indicates whether we should append form values instead of setting them
	 * @return {ExtractedFormValues} Object with two fields: string with form action value and extracted URLSearchParams
	 */
	extractFormValues: function extractFormValues(selector, htmlString, append) {
		let form = null;
		if (htmlString) {
			const tempDocument = document.implementation.createHTMLDocument();
			tempDocument.body.innerHTML = htmlString;
			form = tempDocument.querySelector(selector);
		} else {
			form = document.querySelector(selector);
		}
		if (form) {
			const formValues = serializeForm(form, {
				empty: true,
				hash: false,
			});
			const formSearchParams = new URLSearchParams(formValues);
			const params = Array.from(formSearchParams).reduce((formattedParams, [key, value]) => {
				if (key.endsWith('[]')) {
					const trimmedKey = key.slice(0, -2);
					if (!formattedParams.has(trimmedKey)) {
						formattedParams.set(trimmedKey, formSearchParams.getAll(key).join(','));
					}
				} else if (append) {
					formattedParams.append(key, value);
				} else {
					formattedParams.set(key, value);
				}
				return formattedParams;
			}, new URLSearchParams());

			// If parsed from HTML string, action will be empty, so we do an attribute lookup and ensure it is
			// an absolute URL (firefox does not allow fetches of relative URLs)
			let { action } = form;
			if (!action || typeof form.action !== 'string') {
				action = form.getAttribute('action') || null;
			}
			// unable to extract will return as null and must be handled in the driver
			if (action) {
				// handle protocol relative
				if (action.startsWith('//')) {
					action = window.location.protocol + action;
				} else if (!action.startsWith('http')) {
					if (!action.startsWith('/')) {
						action = `/${action}`;
					}
					action = window.location.origin + action;
				}
			}
			return {
				action,
				params,
			};
		}
		return null;
	},
	extractElement: function extractElement(selector, htmlString = false, all = false) {
		const doc = htmlString ? new DOMParser().parseFromString(htmlString, 'text/html') : document;
		return all ? doc.querySelectorAll(selector) : doc.querySelector(selector);
	},
	/**
	 * @function setValue
	 * @param {String} htmlElement DOM element for which value should be set
	 * @param {Element} value String value which should be set
	 */
	setValue: function setValue(htmlElement, value, event = 'input') {
		const prototype = Object.getPrototypeOf(htmlElement);
		const prototypeValueSetter = Object.getOwnPropertyDescriptor(prototype, 'value').set;

		prototypeValueSetter.call(htmlElement, value);

		htmlElement.dispatchEvent(new Event(event, { bubbles: true }));
	},

	/**
	 * @function extractData
	 * @param {String} selector String CSS selector for the specific element we want to extract
	 * @param {String} [htmlString=null] Optional string text content from which we want to extract element. If null - element(s) are extracted from page
	 * @param {Boolean} [all=false] Optional boolean parameter indicates whether to return attribute/innerText of all found elements or only the first one
	 * @param {String} [attributeName=textContent] Optional string that defines attributeName of element/elements
	 * @return {Element|Element[]} String or array of strings in case `all` parameter is true
	 */
	extractData: function extractData(selector, htmlString = null, all = false, attributeName = null) {
		const elements = DOM.extractElement(selector, htmlString, all);
		const getElementData = element => (attributeName ? element.getAttribute(attributeName) : element.textContent);
		if (all) {
			return [...elements].map(element => getElementData(element));
		}
		return getElementData(elements);
	},

	/**
	 * @function runEmbeddedScript
	 * @param {String} scriptText Script string
	 * @param {String} returnVariableName The variable's name in script that must be returned. If it is not defined method returns nothing.
	 * @param {Object} condition Must be defined for waiting for executing condition. Keys - conditionMethod: function returns Boolean,
	 * 					maxWaitTime: maximal waiting time, stepTime - step of time
	 */
	runEmbeddedScript: function runEmbeddedScript(
		scriptText,
		returnVariableName = null,
		condition = {
			conditionMethod: () => true,
			maxWaitTime: 4000,
			stepTime: 300,
		},
	) {
		if (returnVariableName) {
			window[returnVariableName] = null;
		}
		const exceptionParam = 'rmnExceptionOfScript';
		const returnValueParam = 'rmnReturnValue';
		localStorage.removeItem(returnValueParam);
		localStorage.removeItem(exceptionParam);
		const readyScript = returnVariableName
			? `${scriptText} localStorage.setItem('${returnValueParam}', ${returnVariableName});`
			: scriptText;
		return new Promise(resolve => {
			const script = document.createElement('script');
			script.innerHTML = `try {
				${readyScript}
			}
			catch (error) {
				localStorage.setItem('${exceptionParam}', true);
			}`;
			document.body.appendChild(script);
			script.remove();
			if (localStorage[exceptionParam]) {
				localStorage.removeItem(exceptionParam);
				throw new Error('Exception in embedded script');
			}
			if (returnVariableName) {
				window[returnVariableName] = localStorage.getItem(returnValueParam);
				localStorage.removeItem(returnValueParam);
			}
			resolve();
		})
			.then(() => wait(condition.conditionMethod, condition.maxWaitTime, condition.stepTime))
			.then(() => (returnVariableName ? window[returnVariableName] : null));
	},
};

export default DOM;
export const {
	extractFromHTML,
	extractElementFromHTML,
	extractFormValues,
	extractElement,
	setValue,
	extractData,
	runEmbeddedScript,
} = DOM;
