import { ACTIONS, MUTATIONS } from '../types/validation';
import validationApi from '../../../bg/validation-api';
import { backgroundMessaging } from '../../lib';

const DISABLE_TIME_MS = 60 * 60 * 1000; // 1 hour

export const defaultState = {
	merchant: null, // for GET request params
	validationSupport: false,
	installationUuid: null, // for GET request params
	qty: 1, // for GET request params
	codesForValidation: [],
	validationResults: [], // results after testing codes from validation
};
export const getters = {
	codesForValidation: state => state.codesForValidation,
};

export const mutations = {
	[MUTATIONS.SET_VALIDATION_RESULTS](state, results) {
		const codes = state.codesForValidation;
		if (codes[codes.findIndex(obj => obj.code.toLowerCase() === results.code.toLowerCase())]) {
			const result = {
				tracking: codes[codes.findIndex(obj => obj.code.toLowerCase() === results.code.toLowerCase())].tracking,
				...results,
			};

			state.validationResults.push(result);
		}
	},
	[MUTATIONS.SET_CODES_FOR_VALIDATION](state, codes) {
		state.codesForValidation = codes;
	},
};

export const actions = {
	async [ACTIONS.GET_CODES_FOR_VALIDATION]({ state, commit }) {
		const availableCodesCount = state.merchant.codesCount;
		if (availableCodesCount === 0) {
			return [];
		}
		// if no coupons number is specified in the driver, so there are 15 coupons for code testing
		const driverMaxCoupons = state.merchant.maxCoupons ? state.merchant.maxCoupons : 15;
		// codes that are missing to the maxCoupons
		const qty = driverMaxCoupons - availableCodesCount;
		if (qty > 0) {
			// send request to get codes from the validation
			const codesFromValidation = await validationApi.getCodes(
				state.installationUuid,
				state.merchant.domain,
				qty,
			);
			const codesFromValidationLength = codesFromValidation.length;
			const driverStorage = await backgroundMessaging.getDriverStorage(state.merchant.domain);
			if (driverStorage) {
				const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= DISABLE_TIME_MS;
				if (dataExpired) {
					backgroundMessaging.deleteDriverStorage(state.merchant.domain);
				}
			}

			// if validation side doesn't have any codes for testing we will take them from the driver cache or
			// if there are no driver cache => return empty array
			let newCodesArray = (driverStorage || {}).codesForValidation || [];
			const conditions = {
				validationCodesEqualQty: codesFromValidationLength === qty,
				validationCodesLessQtyAndCache:
					!!driverStorage && codesFromValidationLength && codesFromValidationLength < qty,
				validationCodesLessQtyNoCache:
					!driverStorage && codesFromValidationLength && codesFromValidationLength < qty,
			};
			const conditionsActions = {
				validationCodesEqualQty: () => {
					newCodesArray = codesFromValidation;
				},
				validationCodesLessQtyAndCache: () => {
					// if validation side doesn't have enought codes for testing we will take the rest from the driver cache
					newCodesArray = [
						...codesFromValidation,
						...driverStorage.codesForValidation.slice(codesFromValidationLength, qty),
					];
				},
				validationCodesLessQtyNoCache: () => {
					// if validation side doesn't have enought codes for testing and there are no driver cache (first code run)
					newCodesArray = codesFromValidation;
				},
			};
			const action = Object.entries(conditions).find(([, val]) => val === true);
			if (action) {
				conditionsActions[action[0]]();
				backgroundMessaging.setDriverStorage(state.merchant.domain, {
					codesForValidation: newCodesArray,
					lastTestTimestamp: Date.now(),
				});
			}
			commit(MUTATIONS.SET_CODES_FOR_VALIDATION, newCodesArray);
		}

		return [];
	},
	[ACTIONS.SEND_RESULTS_TO_THE_VALIDATION]({ state }) {
		validationApi.sendCodes(
			{ merchant: state.merchant.domain, results: state.validationResults },
			state.installationUuid,
		);
	},
};

export const initializeModule = ({
	merchant = defaultState.merchant,
	validationSupport = defaultState.validationSupport,
	installationUuid = defaultState.installationUuid,
	qty = defaultState.qty,
	codesForValidation = defaultState.codesForValidation,
	validationResults = defaultState.validationResults,
}) => ({
	namespaced: true,
	state: {
		merchant,
		validationSupport,
		installationUuid,
		qty,
		codesForValidation,
		validationResults,
	},
	getters,
	mutations,
	actions,
});
